import * as ReactBootStrap from 'react-bootstrap';
import React, { useEffect, useState } from 'react';
import MFPPieGraphComponent from './MFPPieGraphComponent'
const MFPSummaryTableComponent = (props) => {
    return (
        <div style={{ display: "flex" }}  >
            <ReactBootStrap.Table className="nas_graph_table"  >
                <tbody>

                    <tr>
                        <th className="Table-header" colSpan={2}><span style={{ fontsize: "150%" }}>[月間平均] 印刷数</span></th>
                    </tr>

                    <tr>
                        <td style={{ width: "50%", borderRightStyle: "none", borderBottom: "none", textAlign: "center", verticalAlign: 'middle' }}>
                            <span style={{ fontSize: "200%" }}>{typeof(props.data.headerinfo.avg_header)!== "undefined" ? props.data.headerinfo.avg_header.toLocaleString():0}</span>
                        </td>
                        <td id="id_mfp_info_graph_data_ave_div" rowSpan="3" style={{ borderLeftStyle: "none" }}>
                            <MFPPieGraphComponent data={{
                                mfp_data: props.data.mfp_data.graph_data_ave,
                                id: "id_mfp_avg_graph" + props.data.index,
                                tenantid:props.data.tenantid
                            }} />
                        </td>
                    </tr>
                    <tr>
                        <td style={{ borderTop: "none", borderRight: "none", borderBottom: "none" }}>
                            <div style={{ display: "flex", alignItems: "center" }}>
                                <span className="bar ml-2" style={{ width:"1.2rem",height:"1.2rem", background: "#B1E7F4" }}>
                                </span>
                                <span className="ml-1" >{typeof(props.data.mfp_data.graph_data_ave.value[0]) !== "undefined" ?  props.data.mfp_data.graph_data_ave.value[0].toLocaleString():0}</span>
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <td style={{ borderTop: "none", borderRight: "none" }}>
                            <div style={{ display: "flex", alignItems: " center" }}>
                                <span className="bar ml-2" style={{ width:"1.2rem",height:"1.2rem", background: "#dcdcdc" }}></span>
                                <span className="ml-1">{typeof( props.data.mfp_data.graph_data_ave.value[1]) !== "undefined" ?  props.data.mfp_data.graph_data_ave.value[1].toLocaleString():0}</span>
                            </div>
                        </td>
                    </tr>
                </tbody>
            </ReactBootStrap.Table >


            <ReactBootStrap.Table className="nas_graph_table ml-5" >
                <tbody>
                    <tr>
                        <th className="Table-header" colSpan={2}><span style={{ fontsize: "150%" }}>{props.data.headerinfo.total_header}</span></th>
                    </tr>
                    <tr>
                        <td style={{ width: "50%", borderRightStyle: "none", borderBottom: "none", textAlign: "center", verticalAlign: 'middle' }}>
                            <span style={{ fontSize: "200%" }}>{typeof(props.data.headerinfo.total_data) !== "undefined" ? props.data.headerinfo.total_data.toLocaleString():0}</span>
                        </td>
                        <td id="id_mfp_info_graph_data_all_div" rowSpan="3" style={{ borderLeftStyle: "none" }}>
                            <MFPPieGraphComponent data={{
                                mfp_data: props.data.mfp_data.graph_data_all,
                                id: "id_mfp_total_graph" + props.data.index,
                                tenantid:props.data.tenantid
                            }} />
                        </td>
                    </tr>
                    <tr>
                        <td style={{ borderTop: "none", borderRight: "none", borderBottom: "none" }}>
                            <div style={{ display: "flex", alignItems: "center" }}>
                                <span className="bar ml-2" style={{ width:"1.2rem",height:"1.2rem", background: "#B1E7F4" }}>
                                </span>
                                <span className="ml-1" >{typeof(props.data.mfp_data.graph_data_all.value[0]) !== "undefined" ? props.data.mfp_data.graph_data_all.value[0].toLocaleString():0}</span>
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <td style={{ borderTop: "none", borderRight: "none" }}>
                            <div style={{ display: "flex", alignItems: " center" }}>
                                <span className="bar ml-2" style={{ width:"1.2rem",height:"1.2rem", background: "#dcdcdc" }}></span>
                                <span className="ml-1">{typeof( props.data.mfp_data.graph_data_all.value[1]) !== "undefined" ?  props.data.mfp_data.graph_data_all.value[1].toLocaleString():0}</span>
                            </div>
                        </td>
                    </tr>
                </tbody>
            </ReactBootStrap.Table >

        </div>
    );
}
export default MFPSummaryTableComponent;
