//import modules
import * as ReactBootStrap from "react-bootstrap";
import "./../../StylesCSS/table.css";

///<summary>
// Fsecure list table component
///</summary>
const FSecureListTable = (props) => {
 
  return (
    <div>
        {props.data !== undefined && (
        <div style={{ height: "100%" }}>
          <ReactBootStrap.Table        
            style={{ height: "100%", marginLeft:"2%", marginRight:"2%" , marginTop:"2%", marginBottom:"2%",width:"95%"}}>
            {/* header contents */}
            <thead>
              {/* {props.pc_data.header.trim() != "" && */}
              <tr>
                <th scope="col" className="Table-header" style={{align: "center", border:"1px solid #000000", }}> NO </th>
                <th scope="col" className="Table-header" style={{align: "center", border:"1px solid #000000", }}> PC名 </th>
                <th colSpan="1" className="Table-header" style={{align: "center", border:"1px solid #000000", }}> Windowsバージョン </th>
                <th colSpan="1" className="Table-header" style={{align: "center", border:"1px solid #000000", }}> Computer Protectionの状況 </th>
                <th colSpan="1" className="Table-header" style={{align: "center", border:"1px solid #000000", }}> マルウェア定義ファイルの状況 </th>
              </tr>
            </thead>

            {/* table contents */}
            <tbody>
              { Object.keys(props.data).map((colname, idx) => (
                <tr key={idx}>
                  {/* displays index value */}
                  <td style={{ verticalAlign: "middle" }}>
                    <span style={{ textAlign: "center", color: "black" }}> {idx+1} </span>
                  </td> 

                  {/* displays pcname value */}
                  <td style={{ verticalAlign: "middle" }}>
                    <span style={{ textAlign: "center", color: "black" }}> {props.data[colname].pcname} </span>
                  </td>

                  {/* displays windows version value */}
                  <td style={{ verticalAlign: "middle" }}>
                    <span style={{ textAlign: "center", color: "black" }}> {props.data[colname].windows_version}</span>
                  </td>

                  {/* displays computer protection value with icon */}
                  <td style={{ verticalAlign: "middle" }}>
                    <span style={{ textAlign: "center", color:props.data[colname].computer_protection.color }}> {props.data[colname].computer_protection.value} </span>
                    <span style={{float:'right'}}> {props.data[colname].computer_protection.icon} </span>
                  </td>

                  {/* displays malware value */}
                  <td style={{ verticalAlign: "middle" }}>
                    <span style={{ textAlign: "center", color:props.data[colname].malware.color }}> {props.data[colname].malware.value}</span>
                    <span style={{float:'right'}} > {props.data[colname].malware.icon} </span>
                  </td>
                </tr>
              ))}
            </tbody>
          </ReactBootStrap.Table>
        </div>
    )}</div>
  );
};

export default FSecureListTable;
