import React, { useEffect, useState } from "react";
import * as ReactBootStrap from "react-bootstrap";
import "./../../StylesCSS/table.css";

const SecurityUsageListTable = (props) => {

  return (
    <div style={{ height: "100%" }}>
      <ReactBootStrap.Table        
        id="id_securitysoft_table"
        className="securitysoft_table mt-4"
        style={{ height: "100%" }}
      >
        <thead>
          {/* {props.pc_data.header.trim() != "" && */}
          <tr>
          <th
              scope="col" style={{width:"3%", background:"#FF6919 !important", borderBottom:"none", borderBottomColor:"#FF6919 !important",borderTop:"none" }}>
            </th>
            <th
             scope="col"
             className="Table-header"
             style={{
               align: "center",
               border:"1px solid #000000",
               fontWeight:"normal"
             }}>
              NO
            </th>
            <th
              scope="col"
              className="Table-header"
              style={{
                align: "center",
                border:"1px solid #000000",
                fontWeight:"normal"
              }}
            >
              PC名
            </th>
            <th
              colSpan="1"
              className="Table-header"
              style={{
                align: "center",
                border:"1px solid #000000",
                fontWeight:"normal"
              }}
            >
              Windowsバージョン
            </th>
            <th
              colSpan="1"
              className="Table-header"
              style={{
                align: "center",
                border:"1px solid #000000",
                fontWeight:"normal"
              }}
            >
              導入セキュリティソフト
            </th>
          </tr>
          {/* } */}
        </thead>

        <tbody>
          {Object.keys(props.data).map((colname, idx) => (
            <tr key={idx}>
                <td className="border-hide" style={{borderBottom:"none",borderBottomColor:"#FF6919 !important", background:props.data[colname].color , verticalAlign: "middle",borderTop:"none"}}>
                <span className="border-hide" style={{borderBottom:"none",borderBottomColor:"#FF6919 !important", textAlign: "center", width:"3%"}}>                  
                </span>
              </td>
              <td align="center">
                <span style={{color: "black" }}>
                  {idx+1}
                </span>
              </td>
              <td style={{ verticalAlign: "middle" }}>
                <span style={{ textAlign: "center", color: "black" }}>
                  {props.data[colname].pcname}
                </span>
              </td>
              <td style={{ verticalAlign: "middle" }}>
                <span style={{ textAlign: "center", color: "black" }}>
                  {props.data[colname].windows_version}
                </span>
              </td>
              <td style={{ verticalAlign: "middle" }}>
                <span style={{ textAlign: "center", color:props.data[colname].securitysoft === "未導入" ? props.data[colname].color : "black"}}>
                  {props.data[colname].securitysoft}
                </span>
                <span style={{float:'right'}} >
                {props.data[colname].icon}
                </span>
              </td>
            </tr>
          ))}
        </tbody>
      </ReactBootStrap.Table>
    </div>
  );
};
export default SecurityUsageListTable;
