export const parse_data = (faxInfo, selectedYear, selectedMon) => {
    const startDate = `${selectedYear}年${selectedMon}月01日`;
    const lastDateInMon = new Date(selectedYear, Number(selectedMon), 0);
    const totalDays = lastDateInMon.getDate();
    const endDate = `${selectedYear}年${selectedMon}月${totalDays}日`;

    // 横軸[01日,...,最終日]を作成
    const xLim = createDailyXLim(selectedYear, selectedMon, totalDays);

    // 縦軸[カウント,...]を作成
    const yLim = createYLim(faxInfo, totalDays, selectedYear, selectedMon);
    return {
        "start_date": startDate,
        "end_date": endDate,
        graph_data: [
            ["date", ...xLim],
            ["受信枚数", ...yLim],
        ],
    }
}

const createYLim = (faxInfo, numOfDay, selectedYear, selectedMon) => {
    const dailyBuckets = faxInfo.aggregations.sort_month.buckets[0].daily_buckets.buckets;
    const setMon = selectedMon;

    const yLim = [];
    for (let i = 1; i <= numOfDay; i++) {
        let setDate = (() => {
            if (i < 10) return `${selectedYear}-${setMon}-0${i}`;
            return `${selectedYear}-${setMon}-${i}`;
        })();
        let machData = dailyBuckets.filter(
            (prop) => prop.key_as_string === setDate
        );
        let yData = (() => {
            if (machData.length) return machData[0].doc_count;
            return 0;
        })();
        yLim.push(yData);
    }
    return yLim;
};

const createDailyXLim = (selectedYear, selectedMon, numOfDay) => {
    const dayOfWeekStr = ["(日)", "(月)", "(火)", "(水)", "(木)", "(金)", "(土)"];
    const xLim = [];
    for (let i = 1; i <= numOfDay; i++) {
        let today = new Date(Number(selectedYear), Number(selectedMon) - 1, i);
        let dayOfWeek = dayOfWeekStr[today.getDay()];
        let date = (() => {
            if (i < 10) return `0${i}日${dayOfWeek}`;
            return `${i}日${dayOfWeek}`;
        })();
        xLim.push(date);
    }
    return xLim;
};

