import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Dashboardcomponent from './Dashboardcomponent';
import {QueryData} from './queryComponent/nasquery';
import GettenantIdAction from './../Actions/tenantIdAction';

import MainHeaderComponent from './../components/MainHeaderComponent';
import * as ReactBootStrap from 'react-bootstrap';
import UpdateComponent from './UpdateComponent';
var  lsttenant=[];
const MainComponent=()=>{

    let dispatch = useDispatch();
    const [navstatus, setnavStatus] = useState(false);
    const [tenantid, setTenantid] = useState([]);
    const [print, setPrint] = useState(false);
    let reducerStateData = useSelector(state => state.searchComponent);
    
    //alert("reducerstate"+JSON.stringify(reducerStateData));
    useEffect(() => {

        let nasQry = new QueryData();
        let tenantQry = nasQry.tenantid_query();
        tenantQry["token"] =window.sessionStorage.getItem("token");
        dispatch(GettenantIdAction(tenantQry));
        

    }, []);

    function generatedatacomponent() {
        return (
            <div>
                <Dashboardcomponent navstatus={navstatus} tenantid={tenantid} print={print} resetPrint={resetPrint}/>
            </div>
        )
    }
    //display all tabs 
    const showNavBar = () => {
        setnavStatus(true);
    }
    const tenantids = (selectdtenantid) => {
        setTenantid(selectdtenantid)
    }
    const printDocument=()=>
    {
        setPrint(true);
    }
    const resetPrint=()=>
    {
        setPrint(false);
    }
    ///returns the render component
    return(
        <div>
            <MainHeaderComponent></MainHeaderComponent>
            <ReactBootStrap.Row id="id_wrapper" className="ml-2 mr-2" >
                        <ReactBootStrap.Card className="bg-transparent col-12 pl-0 pr-0 mt-3 pt-0"  >
                    {reducerStateData.lst_tenant_id.length != 0 && (
                        <UpdateComponent stateData={reducerStateData.lst_tenant_id} printDocument={printDocument} showNavBar={showNavBar} tenantids={tenantids} />)
                    } 
                    {
                        generatedatacomponent()
                    }
            </ReactBootStrap.Card>
            </ReactBootStrap.Row>
        </div>
    )

}

export default MainComponent;