//import modules
import React, { useEffect, useState } from 'react';
import c3       from 'c3'
import * as d3  from 'd3';

//initialize default data
let displayText = false;

///<summary>
// Fsecure graph component
///</summary>
const FsecureGraphComponent = (props) => {

    //invokes draw chart api if data exists
    useEffect(() => {
        if ( props.stateData.data) {
           drawChart();
        }
    }, [ props.stateData.data]);

    //api which initialize fsecure data in graph 
    const drawChart = () => {

        const newArray = props.stateData.data.graph_data[1].filter(n => n !== props.stateData.data.graph_data[1][0]);

        if (newArray.every(v => v === 0 || v === null)) {
            displayText = true;
        } else {
            let chart_data  = c3.generate({
                bindto: `#`+props.stateData.bindId ,
                size: {
                    width: 1050,
                    height: 300
                },
                data: {
                    x: 'date',
                    columns: props.stateData.data.graph_data,

                    type: 'bar',

                    colors: {
                        '件数': '#B1E7F4'
                    },
                    order: 'null'
                },
                grid: {
                    y: {
                        show: true
                    }
                },
                axis: {
                    x: {
                        type: "category",
                        tick: {
                            rotate: 0,
                            multiline: true,
                            fit: true
                        }
                    },
                    y: {
                        label: {
                            text: '件数',
                            position: 'outer-top'
                        },
                        tick: {
                            format: function(d) {
                                return d % 1 === 0 ? new Intl.NumberFormat('ja').format(d) : "";
                            }
                        }
                    }
                },
                legend: {
                    show: false
                },
                onrendered: function() {
                    console.log("lines F-Secure");
                    d3.select("#id_fsecure_graph")
                        .select("svg").select(".c3-axis.c3-axis-x")
                        .selectAll("g")
                        .selectAll("text")
                        .nodes()
                        .forEach(function(el, i) {
                            let x_text = d3.select(el).text();
                            let x_text_day = x_text.split("(")[0];
                            let x_text_day_of_week = "(" + x_text.split("(")[1];
                            d3.select(el).select("tspan").text(x_text_day);
                            d3.select(el)
                                .append("tspan")
                                .attr("x", "0")
                                .attr("dx", "0")
                                .attr("dy", ".91em")
                                .text(x_text_day_of_week);
                        });
                }
            });
        }

    }

    return(<div>
        <div id={props.stateData.bindId} style={{ marginLeft: "10%" }}>
        <div style={{ height: '13rem', fontSize: '30px', display: displayText ? "" : "None" }}>
                    <div style={{ margin: 'auto', paddingTop: '80px', textAlign: 'center' }}>
                        {' F-secureがブロックした脅威はありません。'}
                    </div>
                </div>
        </div>
    </div>);
}

export default FsecureGraphComponent