import React, { useEffect, useState } from 'react';
import * as ReactBootStrap from 'react-bootstrap';
import './../../StylesCSS/cssStyle.css';
const PCListAttentionSummaryTable = (props) => {
    const tableIndexes = [0,1,2];
    return (
        <div className="card mt-4 mb-3 pb-0">

            <div id="id_not_note_pc" align="center" className="non_display">
                <span style={{ fontSize: '150%', display: 'block' }}>危険なPCはありません。</span>
                <span style={{ fontSize: '150%', display: 'block' }}>詳細はPC個別ページをご確認ください。</span>
            </div>
            {props.pc_data.tableData[2] != "undefined" &&
                <ReactBootStrap.Table id="id_pc_note_table"  style={{marginBottom:"0"}} >
                    <thead>
                        <tr >
                            <th colSpan='5' className="security-header" style={{ borderBottom: '1px solid rgba(0, 0, 0)',fontSize:"1.25rem",fontWeight:"normal",verticalAlign:"middle",textIndent:"1rem" }} >
                                {props.pc_data.header_data}
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            tableIndexes.map((item, tableindex) =>(

                                (tableindex < props.pc_data.tableData.length)?
                                <tr>
                                    <td width='4%' align="center" className="border-hide-tl"><span style={{ fontSize: '100%'}}>{tableindex+1}. </span> </td>
                                    <td width='30%'> <span style={{ fontSize: '1.25rem' }}>{props.pc_data.tableData[tableindex].security_status_score.security_status_props.pc_name}</span></td>
                                    <td width='17%' style={{ borderRight: "None" }} align="center">
                                        <div id="id_pc1_status" className="oval mt-1"
                                            style={{
                                                backgroundColor: props.pc_data.tableData[tableindex].security_status_score.security_status_props.background,
                                                borderColor: props.pc_data.tableData[tableindex].security_status_score.security_status_props.border,
                                            }}>
                                            <span style={{
                                                textAlign: 'center',  color: props.pc_data.tableData[tableindex].security_status_score.security_status_props.color
                                                
                                            }}>
                                                {props.pc_data.tableData[tableindex].security_status_score.security_status_props.title} </span>
                                            <p style={{
                                                textAlign: 'center', fontSize: '125%', color: props.pc_data.tableData[tableindex].security_status_score.security_status_props.color,
                                                fontWeight:'bold'
                                            }}>
                                                {props.pc_data.tableData[tableindex].security_status_score.security_status_props.status}</p>
                                        </div>
                                    </td>

                                    <td width='17%' style={{ borderRight: "None", borderLeft: "None", width: '17%' }} align="center">
                                        <div id="id_pc1_status" className="oval mt-1" style={{
                                            backgroundColor: props.pc_data.tableData[tableindex].os_status_score.os_status_props.background,
                                            borderColor: props.pc_data.tableData[tableindex].os_status_score.os_status_props.border,
                                        }}>
                                            <span style={{
                                                textAlign: 'center', color: props.pc_data.tableData[tableindex].os_status_score.os_status_props.color,
                                            }}>
                                                {props.pc_data.tableData[tableindex].os_status_score.os_status_props.title} </span>
                                            <p style={{
                                                textAlign: 'center', fontSize: '125%', color: props.pc_data.tableData[tableindex].os_status_score.os_status_props.color,fontWeight:'bold'
                                            }}>
                                                {props.pc_data.tableData[tableindex].os_status_score.os_status_props.status}</p>
                                        </div>
                                    </td>
                                    <td width='17%' style={{ borderLeft: "None", borderRight:"None" }} align="center">
                                        <div id="id_pc1_status" className="oval mt-1"
                                            style={{
                                                backgroundColor: props.pc_data.tableData[tableindex].storage_life_score.storage_status_props.background,
                                                borderColor: props.pc_data.tableData[tableindex].storage_life_score.storage_status_props.border,
                                            }}>
                                            <span style={{
                                                textAlign: 'center', color: props.pc_data.tableData[tableindex].storage_life_score.storage_status_props.color,
                                            }}>
                                                {props.pc_data.tableData[tableindex].storage_life_score.storage_status_props.title} </span>
                                            <p style={{
                                                textAlign: 'center', fontSize: '125%', color: props.pc_data.tableData[tableindex].storage_life_score.storage_status_props.color,
                                                fontWeight:'bold'
                                            }}>
                                                {props.pc_data.tableData[tableindex].storage_life_score.storage_status_props.status}</p>
                                        </div>
                                    </td>
                                </tr>
                                 :
                                 <tr>
                                     <td width='4%' align="center" className="border-hide-tl">{tableindex + 1}.</td>
                                     <td width='30%' >---</td>
                                     <td colSpan="3"  className="border-hide-r mt-2" width='30%'>
                                         <div style={{marginTop:"2%", marginBottom:"1rem", marginLeft:"8rem"}}>---</div>
                                     </td>
                                 </tr>
                            ))
                            
                        }

                    </tbody>
                </ReactBootStrap.Table>
            }
        </div>
    );
}
export default PCListAttentionSummaryTable;