//import modules
import * as ReactBootStrap from 'react-bootstrap';

///<summary>
// Fsecure bar table
///</summary>
const FsecureBarTable = (props) => {
    
    return(
        <ReactBootStrap.Table className="mr-2 ml-2 mt-4 dis-flex" style={{width:"48.5%", float:props.data.float}} bordered>
            <thead>
                    <tr>
                        <th  className="security-header" style={{ fontSize:"1.25rem",fontWeight:"normal",verticalAlign:"middle"}} >
                            {props.data.table_header}
                        </th>
                    </tr>
            </thead>
            <tbody>
                <tr>
                    <td  align="center" width="80%" height="100%" style={{ border: "None" }}>
                        <span style={{ display: "flex" }} className="mr-2 ml-2 mt-4 dis-flex">
                            <span className="PCAgentSecuritybar" style={{ background: "#B1E7F4",color:"black", width: props.data.normal_value_width }}>
                                {props.data.normal_value}</span>&nbsp;
                            <span className="PCAgentSecuritybar" style={{ background:  "#FF6919", width: props.data.abnormal_value_width }}>
                                {props.data.abnormal_value}</span>&nbsp;
                        </span>
                    </td>
                </tr>

                <tr className="Fsecure_bar-bottom mt-2 ml-2 mb-1 dis-flex" style={{ display: 'flex' }}>
                    <td style={{ borderLeft: "None", borderTop: "None", borderBottom: "None", borderRight: "None",width:'35%' }}> 
                        <div className="dis-flex ai-center ml-3" style={{ display: "flex", alignItems:'center'}}>
                            <span className="bar ml-1" style={{ width: "20%", background: "#B1E7F4" }}></span>
                            <span className="ml-1">{props.data.normal_value_legend}</span>
                        </div>
                    </td>
                    <td style={{ borderLeft: "None", borderTop: "None", borderBottom: "None", borderRight: "None",width:'35%' }}> 
                        <div className="ml-5 dis-flex ai-center" style={{ display: "flex" }}>
                            <span className="bar ml-1" style={{ width: "20%", background: "#FF6919" }}></span>
                            <span className="ml-1">{props.data.abnormal_value_legend}</span>
                        </div>
                    </td>
                    
                </tr>
            </tbody>
        </ReactBootStrap.Table> 
    )
}

export default FsecureBarTable;