import * as ReactBootStrap from 'react-bootstrap';
import React, { useEffect, useState } from 'react';

const MFPOtherCircleComponent = (props) => {
    return (
       
        <div className="mt-1" style={{display:"flex"}}>
        <ReactBootStrap.Table id="id_mfpotherprintedave_graph_table" className="nas_graph_table mr-1">
            <tbody>
                <tr>
                    <th id="id_mfp_other_info_graph_data_ave_unit" scope="col" colSpan="2">
                        <span style={{fontSize:"1.45rem"}}>[月間平均] 印刷カウンター </span>
                        </th>
                </tr>
                <tr>
                    <td id="id_mfp_other_info_graph_data_ave" align="center" style={{borderRight:"none",borderBottom:"none"}}>
                        <span style={{fontSize: "200%"}}>{typeof(props.data.headerinfo.avg_header)!== "undefined" ? props.data.headerinfo.avg_header.toLocaleString():0}</span>
                    </td>
                    <td rowSpan="3" style={{borderLeft:"none"}}>
                        <div className="mfp_circle mt-2 mb-2" style={{fontSize: "150%"}}>情報なし</div>
                    </td>
                </tr>
                <tr>
                    <td align="center" style={{borderTop:"none", borderRightStyle:"none",borderBottom:"none"}}>
                        <div style={{display: "flex",alignItems: "center"}}>
                            <span className="bar ml-2" style={{width:"1.2rem",height:"1.2rem",background:"#B1E7F4"}}></span>
                                <span className="ml-1"> カラー ---</span>
                        </div>
                    </td>
                </tr>
                <tr>
                    <td align="center" style={{borderTop: "none", borderRight:"none"}}>
                        <div style={{display: "flex", alignItems: "center"}}>
                            <span className="bar ml-2" style={{width:"1.2rem",height:"1.2rem",background:"#dcdcdc"}}></span>
                                <span className="ml-1">モノクロ ---</span>
                        </div>
                    </td>
                </tr>
            </tbody>
        </ReactBootStrap.Table>

        <table id="id_mfpotherprintedall_graph_table" className="nas_graph_table ml-1">
            <tbody>
                <tr>
                    <th id="id_mfp_other_info_graph_data_all_unit" scope="col" colSpan="2">
                        <span style={{fontSize:"1.45rem"}}>{props.data.headerinfo.total_header}</span>
                        </th>
                </tr>
                <tr>
                    <td id="id_mfp_other_info_graph_data_all" align="center" style={{borderRight:"none",borderBottom:"none"}}>
                        <span style={{fontSize: "200%"}}>{typeof(props.data.headerinfo.total_data)!== "undefined" ? props.data.headerinfo.total_data.toLocaleString():0}</span>
                    </td>
                    <td rowSpan="3" style={{borderLeft:"none"}}>
                        <div className="mfp_circle mt-2 mb-2" style={{fontSize: "150%"}}>情報なし</div>
                    </td>
                </tr>
                <tr>
                    <td align="center" style={{borderTop:"none", borderRightStyle:"none",borderBottom:"none"}}>
                        <div style={{display: "flex",alignItems: "center"}}>
                            <span className="bar ml-2" style={{width:"1.2rem",height:"1.2rem",background:"#B1E7F4"}}></span>
                                <span className="ml-2">カラー ---</span>
                        </div>
                    </td>
                </tr>
                <tr>
                    <td align="center" style={{borderTop: "none", borderRight:"none"}}>
                        <div style={{display: "flex", alignItems: "center"}}>
                            <span className="bar ml-2"  style={{width:"1.2rem",height:"1.2rem",background:"#dcdcdc"}}></span>
                                <span className="ml-2">モノクロ ---</span>
                        </div>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
    );
}
export default MFPOtherCircleComponent;
