//import modules
import HeaderComponent from '../reusableComponents/HeaderComponent';
import FsecureComponent from './FsecureComponent';

///<summary>
// Fsecure main component
///</summary>
const FsecureMainComponent = (props) => {
    
    return (
        <div>
             < HeaderComponent data={{ 
                    header_data    : "F-SecureのPC保護状況",
                    row_data       : <FsecureComponent stateData ={props.stateData} />,
                    header_style   : "card-header",
                    header_height  : "60px"
            }}/>
        </div>
    )
}

export default FsecureMainComponent;