//import modules
import React            from 'react';
import FsecureBarTable  from './FsecureBarTable';

///<summary>
// Fsecure bar component
///</summary>
const FsecureBarComponent = (props) => {
    var computer_protection;
    var malware_info;

    //returns computer protection info
    const generate_computer_protection_info =() =>{
        return { 
            float                   : "left",
            table_header            : "Computer Protectionの状況",
            normal_value_width      : props.data.f_secure_computer_protection_info.normal.percent+"%",
            abnormal_value_width    : props.data.f_secure_computer_protection_info.abnormal.percent+"%",
            normal_value_legend     : "異常なし", 
            abnormal_value_legend   : "異常あり",
            normal_value            : props.data.f_secure_computer_protection_info.normal.value,
            abnormal_value          : props.data.f_secure_computer_protection_info.abnormal.value
        }
    }

    //returns computer protection info
    const generate_malware_info             = () =>{
        return {
            float                   : "right", 
            table_header            : "マルウェア定義ファイルの状態",
            normal_value_width      : props.data.f_secure_malware_info.update.percent+"%",
            abnormal_value_width    : props.data.f_secure_malware_info.no_update.percent+"%",
            normal_value_legend     : "14日以内に更新あり",
            abnormal_value_legend   : "更新なし",
            normal_value            : props.data.f_secure_malware_info.update.value,
            abnormal_value          : props.data.f_secure_malware_info.no_update.value
        }
    }
    
    if((undefined !== props.data) && ("NoHitError" !== props.data)) {
        computer_protection     = generate_computer_protection_info();
        malware_info            = generate_malware_info();
    }


    return(
        <div>
          <div className="dis-flex"> 
                {undefined !== computer_protection && (
                    <div>
                        <FsecureBarTable data = {computer_protection} />
                        <FsecureBarTable data =  {malware_info} />
                    </div>
                )}
            </div>
        </div>
    ) 
} 

export default FsecureBarComponent;