import React, { useEffect, useState } from 'react';
import * as ReactBootStrap from 'react-bootstrap';
import './../../StylesCSS/table.css';

const TableComponent = (props) => {

    const [columns, setcolumns] = useState([]);

    useEffect(() => {
        setcolumns(Object.keys(props.pc_data.tableData));
    }, [props.pc_data]);

    return (
        <div  style={{ height: "100%"}}>
            <ReactBootStrap.Table bordered id="pc-table-id" className='pc_detail_table nas_graph_table' style={{height:"100%"}}>
                <thead>
                    {props.pc_data.header.trim() != "" &&
                        <tr >
                            <th colSpan='4' className="Table-header" style={{borderBottom:'1px solid rgba(0, 0, 0)'}}>
                                {props.pc_data.header}
                            </th>
                        </tr>
                    }
                </thead>

                <tbody>
                    {
                        columns.map((colname, idx) => (
                            <tr key={idx}>
                                <td style={{verticalAlign:"middle"}}><span style={{ textAlign:'center',color: props.pc_data.tableData[colname].color}}>{props.pc_data.tableData[colname].title}</span>                                    
                                </td>
                                <td style={{verticalAlign:"middle"}}><span style={{ textAlign:'center',color: props.pc_data.tableData[colname].color}}>{props.pc_data.tableData[colname].value}</span>                     
                                </td>
                            </tr>
                        ))
                    }
                </tbody>
            </ReactBootStrap.Table>
        </div>
    );
}
export default TableComponent;