//name spaces
import { act } from "react-dom/test-utils";
import { combineReducers }  from "redux";
import initialState         from './../models/initialState';
import { NetworkParseData } from './../Saga-Reducer/NetworkDataParser'
import { fsecureparsedata } from './../Saga-Reducer/FsecureParser'

///#####################################################
/// Main method which invokes when an action is executed
///#####################################################
const InitialiseNetwork = (state, action) => {
    var network_info_response = "NoHitError";
    var nasresponse = action.nas_info;
    var pcresponse = action.pc_status;;
    var mfpresponse = action.mfp_data;;
    if (nasresponse === undefined || pcresponse === undefined || mfpresponse === undefined) {
        network_info_response = "NoHitError";
    }
    else if ((pcresponse.hits === undefined || pcresponse.hits.total === undefined) ||
        (mfpresponse.hits === undefined || mfpresponse.hits.total === undefined) ||
        (nasresponse.hits === undefined || nasresponse.hits.total === undefined)) {
        network_info_response = "NoHitError";
    }
    else if ((pcresponse.hits.total.value !== "undefined" && pcresponse.hits.total.value === 0) ||
        (mfpresponse.hits.total.value !== "undefined" && mfpresponse.hits.total.value === 0) ||
        (nasresponse.hits.total.value !== "undefined" && nasresponse.hits.total.value === 0)) {
        //handle error
        network_info_response = "NoHitError";
    }
    else {
        const pcInfo = pcresponse.aggregations.computer_list.buckets.map(bucket => {
            return {
                ...bucket.latest_data.hits.hits[0]._source,
                hdd_temperature: bucket.c_temperature.filter_hdd_c.avg_temperature.value,
                ip_list: bucket.ip_list.buckets
            };
        });
        var mfpInfo = mfpresponse.aggregations.mfp_list.buckets;

        network_info_response = NetworkParseData(pcInfo, mfpInfo, nasresponse);
    }

    return network_info_response;
}

export const searchComponent = (state = initialState, action) => {
    let networkData;
    switch (action.type) {
        case "get-tenantId":
            return { ...state, loading: true };
        case "get-tenantId_SUCCESS":
            return { ...state, lst_tenant_id: [...state.lst_tenant_id, ...action.lst_tentant_ids], loading: false };
        case "get-nasInfo":
            return { ...state, loadinggraph: true };
        case "get-nasInfo_SUCCESS":
            var agentData,respData;
            if ( !(action.tenantid in state.ES_query_result)) {
                state = { ...state,  ES_query_result: { ...state.ES_query_result, [action.tenantid]: {} } };
            }
            if ( !(action.tenantid in state.ES_query_response)) {
                state = { ...state,  ES_query_response: { ...state.ES_query_response, [action.tenantid]: {} } };
            }
            delete state.ES_query_result[action.tenantid]["nas_info"];
            delete state.ES_query_result[action.tenantid]["network_status"];
            agentData = state.ES_query_result[action.tenantid];
            agentData["nas_info"] = action.nas_info_data;
            respData = state.ES_query_response[action.tenantid];
            respData["nas_info"] = action.nas_response;
            state = { ...state, ES_query_result: { ...state.ES_query_result, [action.tenantid]: agentData }, ES_query_response: { ...state.ES_query_response, [action.tenantid]: respData } };
            networkData = InitialiseNetwork(undefined, state.ES_query_response[action.tenantid]);
            agentData = state.ES_query_result[action.tenantid];
            agentData["network_status"] = networkData;
            state = { ...state, ES_query_result: { ...state.ES_query_result, [action.tenantid]: agentData } };
            return state;
        case "get-pc-status":
            return { ...state, loadinggraph: true };

        case "get-pc-status_SUCCESS":
            var agentData,respData
            if ( !(action.tenantid in state.ES_query_result)) {
                state = { ...state,  ES_query_result: { ...state.ES_query_result, [action.tenantid]: {} } };
            }
            if ( !(action.tenantid in state.ES_query_response)) {
                state = { ...state,  ES_query_response: { ...state.ES_query_response, [action.tenantid]: {} } };
            }
            delete state.ES_query_result[action.tenantid]["pc_status"];
            delete state.ES_query_response[action.tenantid]["pc_info"];
            delete state.ES_query_response[action.tenantid]["pc_status"];
            delete state.ES_query_result[action.tenantid]["network_status"];
            delete state.ES_query_result[action.tenantid]["fsecure_data"];
            agentData = state.ES_query_result[action.tenantid];
            agentData["pc_status"] = action.pc_status_data;
            respData = state.ES_query_response[action.tenantid];
            respData["pc_status"] = action.pc_status_response;
            respData["pc_info"]  = action.pcInfo;
            state = { ...state, ES_query_result: { ...state.ES_query_result, [action.tenantid]: agentData }, ES_query_response: { ...state.ES_query_response, [action.tenantid]: respData } };
            networkData = InitialiseNetwork(undefined, state.ES_query_response[action.tenantid]);
            var fsecuredata = fsecureparsedata(state.ES_query_response[action.tenantid] );
            agentData["fsecure_data"] = fsecuredata;
            agentData = state.ES_query_result[action.tenantid];
            agentData["network_status"] = networkData;
            state = { ...state, ES_query_result: { ...state.ES_query_result, [action.tenantid]: agentData } };
            return state;

        case "get-fax-data":
            return { ...state, loadinggraph: true };
        case "get-fax_SUCCESS":
            if (!(action.tenantid in state.ES_query_result)) {
                state = { ...state, ES_query_result: { ...state.ES_query_result, [action.tenantid]: {} } };
            }
            delete state.ES_query_result[action.tenantid]["fax_data"];
            agentData = state.ES_query_result[action.tenantid];
            agentData["fax_data"] = action.fax_data;
            state = { ...state, ES_query_result: { ...state.ES_query_result, [action.tenantid]: agentData } };
            return state
        case "get-mfp-data":
            return { ...state, loadinggraph: true };
        case "get-mfp_SUCCESS":
            var agentData,respData
            
            if ( !(action.tenantid in state.ES_query_result)) {
                state = { ...state,  ES_query_result: { ...state.ES_query_result, [action.tenantid]: {} } };
            }
            if ( !(action.tenantid in state.ES_query_response)) {
                state = { ...state,  ES_query_response: { ...state.ES_query_response, [action.tenantid]: {} } };
            }
            delete state.ES_query_result[action.tenantid]["mfp_data"];
            delete state.ES_query_result[action.tenantid]["network_status"];
            agentData = state.ES_query_result[action.tenantid];
            agentData["mfp_data"] = action.mfp_data;
            respData = state.ES_query_response[action.tenantid];
            respData["mfp_data"] = action.mfp_response;
            state = { ...state, ES_query_result: { ...state.ES_query_result, [action.tenantid]: agentData }, ES_query_response: { ...state.ES_query_response, [action.tenantid]:respData } };
            networkData = InitialiseNetwork(undefined, state.ES_query_response[action.tenantid]);
            agentData = state.ES_query_result[action.tenantid];
            agentData["network_status"] = networkData;
            state = { ...state, ES_query_result: { ...state.ES_query_result, [action.tenantid]: agentData } };
            return state;

        case "get-application-data":
            return { ...state, loadinggraph: true };
        case "set-application-data":
            if (!(action.tenantid in state.ES_query_result)) {
                state = { ...state, ES_query_result: { ...state.ES_query_result, [action.tenantid]: {} } };
            }
            delete state.ES_query_result[action.tenantid]["portal_data"];
            agentData = state.ES_query_result[action.tenantid];
            agentData["portal_data"] = action.portal_data;
            state = { ...state, ES_query_result: { ...state.ES_query_result, [action.tenantid]: agentData } };
            return state

        case "setmfpstatus":
            if(action.tenantid in state.ES_query_result)
            {
            agentData = state.ES_query_result[action.tenantid];
            agentData["mfpstatus"] = action.value;
            return { ...state, ES_query_result: { ...state.ES_query_result, [action.tenantid]: agentData }, loadinggraph: false };
            }
        case "setmfpotherstatus":
            if(action.tenantid in state.ES_query_result)
            {
            agentData = state.ES_query_result[action.tenantid];
            agentData["mfpotherstatus"] = action.value;
            return { ...state, ES_query_result: { ...state.ES_query_result, [action.tenantid]: agentData }, loadinggraph: false };
            }
        case "setportalstatus":
            if(action.tenantid in state.ES_query_result)
            {
            agentData = state.ES_query_result[action.tenantid];
            agentData["portalstatus"] = action.value;
            return { ...state, ES_query_result: { ...state.ES_query_result, [action.tenantid]: agentData }, loadinggraph: false };
            }
        case "setnasstatus":
            if(action.tenantid in state.ES_query_result)
            {
            agentData = state.ES_query_result[action.tenantid];
            agentData["nasstatus"] = action.value;
            return { ...state, ES_query_result: { ...state.ES_query_result, [action.tenantid]: agentData }, loadinggraph: false };
            }
        case "setfaxstatus":
            if(action.tenantid in state.ES_query_result)
            {
            agentData = state.ES_query_result[action.tenantid];
            agentData["faxstatus"] = action.value;
            return { ...state, ES_query_result: { ...state.ES_query_result, [action.tenantid]: agentData }, loadinggraph: false };
            }
        case "deletetenantids":
            state = {...state, ES_query_result : [], ES_query_response : []}
            return state;
        case "setProgress":
            var progressbarData={"tenantid":action.tenantid,"value":action.value,"showProgressbar":action.showProgressbar,isClosebtnclicked:action.isClosebtnclicked}
            state = { ...state, progressValue: { ...state.progressValue,...progressbarData}}
            return state;

        case "set-fsecure-data":
            //condition updates tenant id if not eixsts 
            if ( !(action.tenantid in state.ES_query_result)) {
                state = { ...state,  ES_query_result: { ...state.ES_query_result, [action.tenantid]: {} } };
            }
            if ( !(action.tenantid in state.ES_query_response)) {
                state = { ...state,  ES_query_response: { ...state.ES_query_response, [action.tenantid]: {} } };
            }

            //deletes previous fsecure_data if exists
            delete state.ES_query_result[action.tenantid]["fsecure_data"];
            delete state.ES_query_response[action.tenantid]["fsecure_data"];
            
            //get response data for given tenant from ES_query_response of state & update 'fsecure_data'
            //add fesuredata to the response taken from action
            var  respData               = state.ES_query_response[action.tenantid];
            respData["fsecure_data"]    = { "data" : action.fsecure_data, "selectedmonth" : action.selectedmonth, "selectedyear" : action.selectedyear};
            
            //add the response data to ES_query_response of state
            state                       = { ...state,  ES_query_response: { ...state.ES_query_response, [action.tenantid]:respData } };
            
            //add fsecuredata from parser to agentData            
            //get fsecuredata which returns from parsing logic-fsecureparser
            var agentData               = state.ES_query_result[action.tenantid];
            agentData["fsecure_data"]   = fsecureparsedata(state.ES_query_response[action.tenantid]);
            
            //add agentData to ES_query_result of state
            state                       = { ...state, ES_query_result: { ...state.ES_query_result, [action.tenantid]: agentData } };
            
            return state; //returns state

        case "clear":
            try {
                //clears ES_query_response data
                state = {...state, ES_query_response : { ...state.ES_query_response, [action.tenantid]: {} } };
                return state;
            }catch{
                //do nothing
            };
    
        default:
            return state;
    }
}


const rootreducer = combineReducers({ searchComponent });
export default rootreducer;

