import React, { useEffect, useState } from "react";
import * as ReactBootStrap from "react-bootstrap";
import "./../../StylesCSS/table.css";

const NetworkTableComponent = (props) => {

  function getLenandSliceStr(str) {
    if(str === ''){
      return;
    }
    let resultLen = 0;
    let resultStr = "";
    for (let i = 0; i < str.length; i++) {
        let chr = str.charCodeAt(i);
        if (resultLen > 14) break;
        resultStr += str.substring(i, i + 1);
        if ((chr >= 0x00 && chr < 0x81) ||
            (chr === 0xf8f0) ||
            (chr >= 0xff61 && chr < 0xffa0) ||
            (chr >= 0xf8f1 && chr < 0xf8f4)) {
            resultLen += 1;
        } else {
            resultLen += 2;
        }
    }
    let result = [resultLen, resultStr];
    if (result[0]> 14 ) {
      return result[1]+ "...";
    } else{
      return result[1];
    };
};
  return (
    <div style={{ height: "100%" }}>
      <ReactBootStrap.Table className="border-hide">
        <div>
          <tbody>
            {/* add lines dynamically depending on device list */}
            <tr style={{ borderRight: 'none', borderTop: 'none', borderBottom: 'none' }}>
              <td style={{ borderRight: 'none',borderBottom: 'none',borderLeft: 'none',width: '50px' }}></td>
              {props.networkdata.networklist.map((c, idx) => {
                if (props.networkdata.networklist.length - 1 === idx)
                  return (
                    <React.Fragment key={idx}>
                      <td key={idx} style={{ borderLeft: 'none', borderBottom: 'none' }}></td>
                      <td key={idx+1} style={{ borderRight: 'none', borderTop: 'none', borderBottom: 'none' }}></td>
                    </React.Fragment>)
                else
                  return (
                    <React.Fragment key={idx}>
                      <td key={idx} style={{ borderLeft: 'none', borderBottom: 'none' }}></td>
                      <td key={idx+1} style={{ borderRight: 'none', borderLeft: 'none', borderBottom: 'none' }}></td>
                      </React.Fragment>
                   
                  )
              })}
            </tr>

            {/* add devices dynamically */}
            <tr>
            <td className="border-hide" style={{ width: '50px' }}></td>

{props.networkdata.networklist.map((colname, idx) => (
 
                <td key={idx} style={{ textAlign: 'center', borderLeft: 'none'}} className="border-hide pl-2" colSpan={2}>
                  {" "}
                  <span style={{ textAlign: 'center', display: 'block',paddingLeft:'20px' }}>
                    {props.networkdata.image}
                  </span>
                  <span style={{ textAlign: 'center', width: '100px',height:props.networkdata.networklist[idx].pcName !==''?'38px':'auto' }}>
                   {getLenandSliceStr(props.networkdata.networklist[idx].pcName)}
                  </span>{" "}
                  <span style={{ textAlign: 'center' }}>
                    {props.networkdata.networklist[idx].ipaddress}
                  </span>{" "}
                </td>
              ))
              }

            </tr>
          </tbody>
        </div>
      </ReactBootStrap.Table>
    </div>
  );
};
export default NetworkTableComponent;
