import { index } from 'd3';
import React, { useState, useEffect } from 'react';
import TableComponent from '../reusableComponents/TableComponent';
import TableIconTwoRowComponenet from './TableIconTwoRowComponent';
import TableIconFourRowComponenet from './TableIconFourRowComponent';
import PCStorageTable from './PCAgentStorageTable';
import HeaderComponent from '../reusableComponents/HeaderComponent';

const PCAgentComponent = (props) => {
    const [basicinfo, setrHeaderInfo] = useState({ header: '', tableData: {} })

    useEffect(() => {
        // props.data.pc_status_data.map((colname, idx) => (
            setrHeaderInfo({
                ...basicinfo, header: props.data.pc_status_data.pc_list_info[props.data.index].basic_info_header.basic_info_header,
                tableData: props.data.pc_status_data.pc_list_info[props.data.index].tableData
            })
        // ))
    }, [props.data.pc_status_data]);

    return (

            <div className='card-body'>
                {/* <HeaderComponent data={{ header_data: props.stateData[idx].security_info_header.security }} /> */}
                <div style={{ width: "40%"}} className="mb-3" >
                    <TableComponent pc_data={{
                        header: props.data.pc_status_data.pc_list_info[props.data.index].basic_info_header.basic_info_header,
                        tableData: props.data.pc_status_data.pc_list_info[props.data.index].tableData
                    }} />
                </div>
                <div style={{ display: "block", marginLeft: "auto", width: "60%" }} >
                   
                             <TableIconTwoRowComponenet pc_data={{
                                header_data: props.data.pc_status_data.pc_list_info[props.data.index].os_header.os,
                                icon: props.data.pc_status_data.pc_list_info[props.data.index].os_info_list.os_update.image,
                                tableData: props.data.pc_status_data.pc_list_info[props.data.index].os_info_list
                            }} />
                    
                             <TableIconTwoRowComponenet pc_data={{
                                header_data: props.data.pc_status_data.pc_list_info[props.data.index].security_info_header.security,
                                icon: props.data.pc_status_data.pc_list_info[props.data.index].security_info_list.firewallStatus.image,
                                tableData: props.data.pc_status_data.pc_list_info[props.data.index].security_info_list
                            }} />
                  
                            <TableIconFourRowComponenet pc_data={{
                                header_data: props.data.pc_status_data.pc_list_info[props.data.index].storage_info_header.storage,
                                icon: props.data.pc_status_data.pc_list_info[props.data.index].storage_info_list.temperature.image,
                                tableData: props.data.pc_status_data.pc_list_info[props.data.index].storage_info_list
                            }}/>
                </div>
            </div >

    );
}
export default PCAgentComponent;

