import React, { useEffect, useState } from "react";
import * as ReactBootStrap from "react-bootstrap";
import "./../../StylesCSS/table.css";
import NetworkTableComponent from "../NetworkStatusComponent/NetworkComponentTable";
import ExternalNetworkTableComponent from "../NetworkStatusComponent/ExternalNetworkComponentTable";
import router from "./../../images/Icon/router.png";
import nas from "./../../images/Icon/nas.png";
import mfp from "./../../images/Icon/mfp.png";
import pc from "./../../images/Icon/note_pc.png";
let nasimage = <img src={nas} alt="" width="40" height="40" />;
let mfpimage = <img src={mfp} alt="" width="40" height="40" />;
let pcimage = <img src={pc} alt="" width="40" height="40" />;

const NetworkComponent = (props) => {

  // maximum count for nas and mfp is 5  and displayed in single row each
  //maximum count for internal and external pc is 30 and 10 are displayed in each row
  let nasmfpCount = 5;
  let internalExternalCount = 10;

  const getPcSubset = (myArray, size) => {
    var index = 0;
    var arrayLength = myArray.length;
    var tempArray = [];
    var subSet = [];

    for (index = 0; index < arrayLength; index += size) {
      subSet = myArray.slice(index, index + size);
      tempArray.push(subSet);
    }
    return tempArray
  };

  var resultNas = getPcSubset(props.stateData.nas_list.ipaddress, nasmfpCount);
  var resultMfp = getPcSubset(props.stateData.mfp_list.ipaddress, nasmfpCount);
  var resultInternalPC = getPcSubset(props.stateData.pc_list_internal.ipaddress, internalExternalCount);
  var resultExternalPC = getPcSubset(props.stateData.pc_list_external.ipaddress, internalExternalCount);


  return (
    <div style={{ height: "100%" }}>
      <ReactBootStrap.Table
        bordered
        id="id_network_info_table"
        className="network_info_table"
        style={{border: "solid 3px #008EA6"}}
      >
        <thead>
          <tr>
            <th
              className="card-header"
              style={{
                fontSize: "100%",
                borderBottom: "1px solid rgba(0, 0, 0)",
              }}
            >
              PCのネットワーク接続状況              
            </th>
          </tr>
        </thead>
        <div className="card-body" style={{ display: "block" }}>
          {/* Network configuration diagram start */}
          <div></div>
          <div
            id="id_pc3_status"
            className="network_info_table_oval"
            style={{ background: "#008EA6", color: "white" }}
          >
            <div id="id_performance_low" className="circle">
              <span style={{ alignItems: "center", fontSize: "125%" }}>
                オフィス
              </span>
            </div>
          </div>
          <tbody>
            {/* router and its lines*/}
            <tr>
              <td className="border-hide" colSpan="2">
                <img src={router} alt="" width="80" height="80" />{" "}
              </td>
            </tr>
            {/* <!-- 3rd vertical line--> */}
            <tr>
            </tr>
            {/* nas, mfp and internal pclist */}
            <div>
              <ReactBootStrap.Table className="ml-5" >
                <tbody>
                  <tr><td className="ml-5" style={{ border: "solid 3px #008EA6", borderRight: 'none', borderTop: 'none', borderBottom: 'none' }}></td></tr>

                  <tr style={{ border: "solid 3px #008EA6", borderRight: 'none', borderTop: 'none', borderBottom: 'none',borderLeft: (resultMfp.length == 0 &&  resultInternalPC.length== 0) ? "none" : "solid 3px #008EA6" }}>
                    {resultNas.length>0 &&
                      <NetworkTableComponent
                        networkdata={{
                          networklist: resultNas[0],
                          image: nasimage
                        }}
                      />}
                  </tr>
                  <tr style={{ border: "solid 3px #008EA6", borderRight: 'none', borderTop: 'none', borderBottom: 'none',borderLeft: (resultInternalPC.length== 0 ) ? "none" : "solid 3px #008EA6"  }}>
                    {resultMfp.length>0 &&
                      <NetworkTableComponent
                        networkdata={{
                          networklist: resultMfp[0],
                          image: mfpimage
                        }}
                      />}
                  </tr>
                  {(resultInternalPC).length >0 &&
                  <tr style={{ border: "solid 3px #008EA6", borderRight: 'none', borderTop: 'none', borderBottom: 'none',borderLeft: (resultInternalPC).length==1 ?"none" : "solid 3px #008EA6"  }}>
                      <NetworkTableComponent
                        networkdata={{
                          networklist: resultInternalPC[0],
                          image: pcimage
                        }}
                      />
                  </tr>}
                  {(resultInternalPC).length> 1  &&
                  <tr style={{ border: "solid 3px #008EA6", borderRight: 'none', borderTop: 'none', borderBottom: 'none',borderLeft: (resultInternalPC).length== 2?"none" :"solid 3px #008EA6"  }}>
                      <NetworkTableComponent
                        networkdata={{
                          networklist: resultInternalPC[1],
                          image: pcimage
                        }}
                      />
                  </tr>}
                  {(resultInternalPC).length > 2 &&
                  <tr style={{ border: "solid 3px #008EA6", borderRight: 'none', borderTop: 'none', borderBottom: 'none',borderLeft: "none"   }}>
                      <NetworkTableComponent
                        networkdata={{
                          networklist: resultInternalPC[2],
                          image: pcimage
                        }}
                      />
                  </tr>}
                </tbody>
              </ReactBootStrap.Table>
            </div>
            {/*external pclist */}
            {resultExternalPC.length>0 && 
             <div style={{ border: "solid 3px #008EA6"}}>
             <div className="card-body" style={{ display: "block" }} >
               <div
                 id="id_pc3_status"
                 className="network_info_table_oval"
                 style={{ background: "#008EA6", color: "white" }}
               >
                 <div id="id_performance_low" className="circle" width="100%">
                   <span
                     style={{
                       alignItems: "center",
                       fontSize: "100%",
                       width: "100%",
                     }}
                   >
                     在宅・出先・他拠点
              </span>
                 </div>
               </div>
             </div>
             {(resultExternalPC).length > 0 &&
               <ExternalNetworkTableComponent
                 networkdata={{
                   networklist: resultExternalPC[0],
                   image: pcimage
                 }}
               />
             }
             {(resultExternalPC).length > 1 &&
               <ExternalNetworkTableComponent
                 networkdata={{
                   networklist: resultExternalPC[1],
                   image: pcimage
                 }}
               />
             }
             {
               (resultExternalPC).length > 2 &&
               <ExternalNetworkTableComponent
                 networkdata={{
                   networklist: resultExternalPC[2],
                   image: pcimage
                 }}
               />
             }
           </div>
            }
           
          </tbody>
        </div>
      </ReactBootStrap.Table>
    </div>
  );
};
export default NetworkComponent;
