import React, { useEffect, useState } from 'react';
import HeaderComponent from './../reusableComponents/HeaderComponent';
import PCAgentMainComponent from './PCAgentMainComponent'
const PCAgentComponent = (props) => {
    return (
        props.stateData[0].pc_list_info.length != 0 &&
        props.stateData[0].pc_list_info.map((colname, idx)  => (
            <div key={idx}>
                < HeaderComponent data={{ 
                    header_data: props.stateData[0].pc_list_info[idx].common_header.common_header,
                    row_data:<PCAgentMainComponent data={{
                        pc_status_data:props.stateData[0],
                        index:idx
                    }}/>,
                    header_style:"card-header",
                    header_height:"60px"}} />
            </div>
        ))
    );

}
export default PCAgentComponent;