
import { HttpService } from './../services/httpservice';
import { put, all, call, take, takeLatest, takeEvery } from 'redux-saga/effects';
import { parse_data } from './faxparsedata';
import { nas_parse_data } from './nas_info_data';
import { PCAgentData } from './PCAgentParser';
import { mfpparsedata } from './mfpparserdata'
import { NetworkParseData } from './NetworkDataParser'
import { ApplicationUsageParseData } from './ApplicationUsageParser'
import { fsecureDailyContamination } from './fsecureDailyContamination';
import { fsecurePcInfo } from './fsecurePcInfo';

//const pcAgentdata = new PCAgentData();
const serv = new HttpService();


const postQuery = (inputQuery) => {
    //function postQuery (inputQuery){
    //alert("post query called");
    const response = serv.postData(inputQuery).then((result) => result.data);
    return Promise.resolve(response);
}

const posttenantQuery = (inputQuery) => {
    //function postQuery (inputQuery){
    //alert("post query called");
    const response = serv.posttenantQuery(inputQuery).then((result) => result.data);
    return Promise.resolve(response);
}

const postagentQuery = (inputQuery) => {
    //function postQuery (inputQuery){
    //alert("post query called");
    const response = serv.postagentQuery(inputQuery).then((result) => result.data);
    return Promise.resolve(response);
}

function* getTenanatIdsGenerator(action) {
    //function* getTenanatIdsGenerator(){
    // read an input parameter for the action
    //const parameters = yield take('get-tenantId');
    //alert("parameters =>" + JSON.stringify(parameters));
    const resp      = yield call(posttenantQuery, action.inputQuery);
    const response  = JSON.parse(resp.body);
    let result      = response.aggregations.tenant.buckets.map(keyItem => keyItem.key);
    // put the response with the output action and data (aka payload) of the action
    yield put({
        type: 'get-tenantId_SUCCESS',
        lst_tentant_ids: result || [{ error: 'ERROR_OCCURED' }]
    });
}

function* actionWatcherForGetTenantIds() {
    //alert("get-tenantId");
    // using takeLatest operator , subscribe to the input action
    // and map the input action with Generator Function
    yield takeLatest('get-tenantId', getTenanatIdsGenerator);
}


function* getNASInfoGenerator(action) {
    //alert("saga =>" + JSON.stringify(action.inputQuery))
    const responsestr   = yield call(postagentQuery, action.inputQuery.nas_data);
    const response      = JSON.parse(responsestr.body);
    var nas_info_response;
    if (response.hits === undefined || response.hits.total === undefined) {
        nas_info_response = "NoHitError";
    }
    else if (response.hits.total.value !== "undefined" && response.hits.total.value === 0) {
        //handle error
        nas_info_response = "NoHitError";
    }
    else {
        nas_info_response = nas_parse_data(response, action.inputQuery.year, action.inputQuery.month);
    }
    //alert("getNASInfoGenerator =>" + JSON.stringify(response));

    //alert("saga =>" + JSON.stringify(nas_info_response));
    yield put({
        type: 'get-nasInfo_SUCCESS',
        nas_response:response,
        tenantid:response.tenantid,
        nas_info_data: nas_info_response || [{ error: 'ERROR_OCCURED' }]
       
    });

}

function* actionWatcherForGetNASJsonData() {
    //alert("get-nasInfo");
    yield takeEvery('get-nasInfo', getNASInfoGenerator);
}


function* getPCStatusInfoGenerator(action) {
    //alert("saga =>" + JSON.stringify(action.inputQuery))
    const responsestr   = yield call(postagentQuery, action.inputQuery);
    const responce      = JSON.parse(responsestr.body);

    let pcinfo;
    let pc_info_list;
    if (responce.hits === undefined || responce.hits.total === undefined) {
        pc_info_list = "NoHitError";
    }
    else if (responce.hits.total.value !== "undefined" && responce.hits.total.value === 0) {
        //handle error
        pc_info_list = "NoHitError";
    }
    else {
        const pcInfo = responce.aggregations.computer_list.buckets.map(bucket => {
            return {
                ...bucket.latest_data.hits.hits[0]._source,
                hdd_temperature: bucket.c_temperature.filter_hdd_c.avg_temperature.value,
                ip_list: bucket.ip_list.buckets
            };
        });
        pcinfo = pcInfo;
        pc_info_list = PCAgentData(pcInfo);
    }
    yield put({
        type: 'get-pc-status_SUCCESS',
        pcInfo: pcinfo,
        pc_status_response: responce,
        tenantid:responce.tenantid,
        pc_status_data: pc_info_list || [{ error: 'ERROR_OCCURED' }]
       
    });
}

function* actionWatcherForGetPCStatusJsonData() {
    //alert("get-nasInfo");
    yield takeEvery('get-pc-status', getPCStatusInfoGenerator);
}

function* getFaxInfoGenerator(action) {
    //alert("saga =>" + JSON.stringify(action.inputQuery))
    const responsestr   = yield call(postagentQuery, action.inputQuery.fax_data);
    const response      = JSON.parse(responsestr.body);

    let response_fax;
    if (response.hits === undefined || response.hits.total === undefined) {
        response_fax = "NoHitError";
    }
    else if (response.hits.total.value !== "undefined" && response.hits.total.value === 0) {
        //handle error
        response_fax = "NoHitError";
    } else {
        response_fax = parse_data(response, action.inputQuery.year, action.inputQuery.month);
    }
    yield put({
        type: 'get-fax_SUCCESS',
        tenantid:response.tenantid,
        fax_data: response_fax || [{ error: 'ERROR_OCCURED' }]
    });

}

function* actionWatcherForGetFaxJsonData() {
    //alert("get-nasInfo");
    yield takeEvery('get-fax-data', getFaxInfoGenerator);
}
function* getMFPInfoGenerator(action) {
    //alert("saga =>" + JSON.stringify(action.inputQuery))
    const responsestr   = yield call(postagentQuery, action.inputQuery.mfp_data);
    const response      = JSON.parse(responsestr.body);

    let toSharpMfpPrintCnt;
    let toOtherMfpPrintCnt;
    if (response.hits === undefined || response.hits.total === undefined) {
        toSharpMfpPrintCnt = "NoHitError";
        toOtherMfpPrintCnt = "NoHitError";
    }
    else if (response.hits.total.value !== "undefined" && response.hits.total.value === 0) {
        //handle error
        toSharpMfpPrintCnt = "NoHitError";
        toOtherMfpPrintCnt = "NoHitError";
    }
    else {
        var mfpInfo = response.aggregations.mfp_list.buckets;
        const toMfpPrintCnt = mfpInfo.map((mfpinfo) => {
            return mfpparsedata(mfpinfo, action.inputQuery.year, action.inputQuery.month)
        });


        toSharpMfpPrintCnt = toMfpPrintCnt.filter(mfpInfo => mfpInfo.is_sharp_mfp);

        toOtherMfpPrintCnt = toMfpPrintCnt.filter(mfpInfo => !mfpInfo.is_sharp_mfp);
        if(toSharpMfpPrintCnt.length === 0){
            toSharpMfpPrintCnt = "NoHitError"
        }
        if(toOtherMfpPrintCnt.length === 0){
            toOtherMfpPrintCnt = "NoHitError"
        }

    }


    let mfpData = {
            "sharp": toSharpMfpPrintCnt,
            "other": toOtherMfpPrintCnt
    }
       
    yield put({
        type: 'get-mfp_SUCCESS',
        mfp_response:response,
        tenantid:response.tenantid,
        mfp_data:mfpData || [{error: 'ERROR_OCCURED'}]
       
    });

}

function* actionWatcherForGetMFPJsonData() {
    yield takeEvery('get-mfp-data', getMFPInfoGenerator);
}

function* getPcPortalInfoGenerator(action){
    //var pcresponse = yield call(postQuery, action.inputQuery.portalQuery[0]);
    var responsestr   = yield call(postagentQuery, action.inputQuery.portalQuery[0]);
    const pcresponse    = JSON.parse(responsestr.body);

    //var mobileresponse = yield call(postQuery, action.inputQuery.portalQuery[1]);
    responsestr             = yield call(postagentQuery, action.inputQuery.portalQuery[1]);
    const mobileresponse    = JSON.parse(responsestr.body);

    var pcportal_response;
    var mobileportal_response;
    if (pcresponse.hits === undefined || pcresponse.hits.total === undefined) {
        pcportal_response = "NoHitError";
    }
    else if (pcresponse.hits.total.value !== "undefined" && pcresponse.hits.total.value === 0) {
        //handle error
        pcportal_response = "NoHitError";
    }
    else {
        pcportal_response = pcresponse.aggregations.operation_count.buckets;
        pcportal_response = ApplicationUsageParseData(pcportal_response, action.inputQuery.selectedYear, action.inputQuery.selectedMonth);
    }

    if (mobileresponse.hits === undefined || mobileresponse.hits.total === undefined) {
        mobileportal_response = "NoHitError";
    }
    if (mobileresponse.hits.total.value !== "undefined" && mobileresponse.hits.total.value === 0) {
        //handle error
        mobileportal_response = "NoHitError";
    }
    else {
        mobileportal_response = mobileresponse.aggregations.operation_count.buckets;
        mobileportal_response = ApplicationUsageParseData(mobileportal_response, action.inputQuery.selected_year, action.inputQuery.selected_mon);
    }

    yield put({
        type: 'set-application-data',
        tenantid:pcresponse.tenantid,
        portal_data:[pcportal_response, mobileportal_response] || [{error: 'ERROR_OCCURED'}]
    });
}

function* actionWatcherForGetApplicationJsonData(){
    //alert("get-nasInfo");
    yield takeEvery('get-application-data', getPcPortalInfoGenerator);
}


///<summary>
// Api fetches fsecure data from lms
///</summary>
function* getFSecureInfoGenerator(action) {

    //sends request to get fseure daily contamination - parse the response
    var responsestr                 = yield call(postagentQuery, action.inputQuery.fsecureQuery[0]);
    var fSecureDailyContamination   = fsecureDailyContamination(JSON.parse(responsestr.body));
    
    //sends request to get fseure daily contamination - parse the response
    responsestr                     = yield call(postagentQuery, action.inputQuery.fsecureQuery[1]);
    var fSecurePcInfo               = fsecurePcInfo(JSON.parse(responsestr.body));

    //sends response to updated in store
    yield put({
        type                : 'set-fsecure-data',
        tenantid            : JSON.parse(responsestr.body).tenantid,
        selectedmonth       : action.inputQuery.selectedMonth,
        selectedyear        : action.inputQuery.selectedYear,
        fsecure_data        : [fSecureDailyContamination, fSecurePcInfo] || [{error: 'ERROR_OCCURED'}]
    });
}

///<summary>
// Api invokes to fetch fsecure data from lms
///</summary>
function* actionWatcherForgetFSecureInfoGenerator() {
     yield takeEvery('get-fsecure', getFSecureInfoGenerator);
}

// complete the SAGA middleware to export all SAGAs generator functions so that
// redux store can use it
export default function* rootSaga() {
    yield all([actionWatcherForGetTenantIds(), actionWatcherForGetNASJsonData(), actionWatcherForGetPCStatusJsonData(), actionWatcherForGetFaxJsonData(), actionWatcherForGetMFPJsonData(),actionWatcherForGetApplicationJsonData(),actionWatcherForgetFSecureInfoGenerator()]);
}  