import React, { useEffect, useState } from 'react';   
import c3 from 'c3'
import './../../StylesCSS/style.css'
import * as d3 from 'd3';
import { useDispatch } from 'react-redux';
let displayText = false;
const FaxComponent=(props)=>{
    let dispatch = useDispatch();
    useEffect(()=>{
        if(props.data)
        {
            
            drawChart();
            dispatch({type:"setfaxstatus","tenantid":props.id.split('fax-chart-data')[1],value:true});
            //dispatch(setprogressstatus(50));
        }
    },[props.data]);
    const drawChart = () => {
        if(props.data === "NoHitError"){
            displayText = true;
        }
        else{
            const newArray = props.data.graph_data[1].filter(n => n !== props.data.graph_data[1][0]);
            if (newArray.every(v => v === 0 || v === null)) {
                displayText = true;
            }
            else {
            let chart_date = c3.generate({
                bindto: '#'+props.id,
                size: {
                    width: 1100,
                    height: 330
                },
                data: {
                    x: 'date',
                    columns:props.data.graph_data,
                    type: "bar",
                    colors: {
                        '受信枚数': '#B1E7F4'
                    },
                },
               
                axis: {
                    x: {
                        type: 'category',
                            tick: {
                                rotate: 0,
                                fit: true,
                                multiline: true,
                                outer:false,
                            
                        }
                    },
                    y: {
                        tick: {
                            outer:false,
                            format: function (d) {
                                return d % 1 === 0 ? d.toLocaleString() : "";
                            }
                        },
                        label: {
                            text: '枚数',
                            position: 'outer-top'
                        }
    
                    }
                },
                grid: {
                   
                    y: {
                        show: true
                    }
                },
                legend: {
                    show: false
                },
                onrendered: function () { 
                    try {
                        d3.select("#"+props.id)
                        .select("svg").select(".c3-axis.c3-axis-x")
                        .selectAll("g")
                        .selectAll("text")
                        .nodes()
                        .forEach(function(el, i) {
                            let x_text = d3.select(el).text();
                            let x_text_day = x_text.split("(")[0];
                            let x_text_day_of_week = "(" + x_text.split("(")[1];
                            d3.select(el).select("tspan").text(x_text_day);
                            d3.select(el)
                                .append("tspan")
                                .attr("x", "0")
                                .attr("dx", "0")
                                .attr("dy", ".91em")
                                .text(x_text_day_of_week);
                                
                            d3.selectAll(".c3-axis-y2-label")
                                .style("font-weight", "normal")
    
                            d3.selectAll(".c3-axis-y-label")
                                .style("font-weight", "normal")    
       
                        });
                        
                    } catch (error) {
                        
                    }      
                   
                }
                
            
            });
            }

        }
       
    
    }
    return (
        <div style={{marginLeft:"10%"}}>
            <div  id={props.id}>
                <div style={{ height: '13rem', fontSize: '30px', display: displayText ? "" : "None" }}>
                    <div style={{ margin: 'auto', paddingTop: '80px', textAlign: 'center' }}>
                        {'複合機から転送されたファクスはありません。'}
                    </div>
                </div>
            </div>   
        </div>
    );
}

export default FaxComponent;