
export const ApplicationUsageParseData = (AppCnt, selectedYear, selectedMon) => {

    const AppCntCore = AppCnt.filter(prop => !(prop.key === ""));
    const logTimeMonth = `${selectedMon}月`;
    const lastDateInMon = new Date(selectedYear, Number(selectedMon), 0);
    const totalDays = lastDateInMon.getDate();
    const endDate = `${selectedYear}年${selectedMon}月${totalDays}日`;
  
    const AppUsageInfo = aggsAppcnt(AppCntCore);
  
    return {
      graph_data: AppUsageInfo.graphData,
      graph_label_data: AppUsageInfo.graphLabelData,
      log_time: endDate,
      log_time_month: logTimeMonth,
    };
}

const aggsAppcnt = (data) => {
    // 総計
    const total_all = data.reduce((sum, elem) => {
      return sum + elem.doc_count;
    }, 0);
    // 降順に並べ替え
    const dataSorted = data.sort((a, b) => b.doc_count - a.doc_count);
  
    // Top5
    const top5 = dataSorted.slice(0, 5);
    const xNames = top5.map((prop) => prop.key);
  
    const xValue = top5.map((prop) => prop.doc_count);
  
    const xPercent = top5.map((prop) =>
      Math.round((prop.doc_count / total_all) * 100)
    );
  
    if (data.length >= 6) {
      // その他
      const others = dataSorted.slice(5);
      // その他の合計
      const totalOthers = others.reduce((sum, elem) => {
        return sum + elem.doc_count;
      }, 0);
      xNames.push("その他");
      xValue.push(totalOthers);
      xPercent.push(Math.round((totalOthers / total_all) * 100));
    } else {
      while (6 > xNames.length) {
        xNames.push(null);
        xValue.push(null);
        xPercent.push(null);
      }
    }
  
    return {
      graphData: [
        ["x", ...xNames],
        ["利用回数", ...xValue],
      ],
      graphLabelData: ["利用回数(%)", ...xPercent],
    };
  };

