import React from 'react'
import * as ReactBootStrap from 'react-bootstrap';

const NoDataExistsComponent = () => {
    return (
        <div style={{fontSize: '30px', height:'500px', position:'relative'}}>
        <div style={{margin:'0',  position: 'absolute',top: '50%', left: '50%', marginRight: '-50%',transform:'translate(-50%, -50%)'}}>
            <ReactBootStrap.Image  className="mb-2 mr-2" src="./../../images/bikkuri.png" alt="" width="40" height="40"/>
             <span>ログデータが存在しません。</span>
        </div>
        </div>
    )
}

export default NoDataExistsComponent;