import React, { useEffect, useState } from 'react';
import * as ReactBootStrap from 'react-bootstrap';
import './../../StylesCSS/cssStyle.css';
const SecuritySoftwareUsageStatusGraph = (props) => {   
    const softwareIntroduction = props.data.software_introduction.percent + "%";
    const sosSoftware = props.data.os_software.percent + "%";
    const notIntroduced = props.data.not_introduced.percent + "%";

    return (
        <ReactBootStrap.Table id="id_security_counterplan_table" >
            <thead>
                        {/* <tr >
                            <th colSpan='5' className="card-header" >
                                {props.pc_data.header_data}
                            </th>
                        </tr> */}
                </thead>
            <tbody>
                <tr>
                    <td  align="center" width="80%" height="100%" style={{ border: "None" }}>
                        <span style={{ display: "flex" }} className="mt-4 ml-4">
                        { softwareIntroduction!="0%" &&
                            <span className="PCAgentSecuritybar" style={{ background: "#7ED7ED", color:"#000000", marginLeft:"1rem", width: softwareIntroduction }}>
                                {props.data.software_introduction.value}</span>
                        }
                        { sosSoftware!="0%" &&
                            <span className="PCAgentSecuritybar" style={{ background: "#B1E7F4", color:"#000000", marginLeft:"1rem", width: sosSoftware }}>
                                {props.data.os_software.value}</span>
                        }
                            <span className="PCAgentSecuritybar" style={{ background: "#FF6919", marginLeft:"1rem", width: notIntroduced }}>
                                {props.data.not_introduced.value}</span>
                        </span>
                    </td>
                   
                </tr>

                <tr className="security-bar-bottom mt-0" style={{ display: 'flex' }}>
                        <td style={{ borderLeft: "None", borderTop: "None", borderBottom: "None", borderRight: "None",width:'20%' }}> 
                            <div className="mt-3 ml-2" style={{ display: "flex", alignItems:'center'}}>
                                <span className="bar ml-1" style={{ width: "20%", background: "#7ED7ED" }}></span>
                                <span className="ml-3">ソフト導入</span>
                            </div>
                        </td>
                        <td style={{ borderLeft: "None", borderTop: "None", borderBottom: "None", borderRight: "None",width:'20%' }}> 
                            <div className="mt-3 ml-2" style={{ display: "flex" }}>
                                <span className="bar ml-1" style={{ width: "20%", background: "#B1E7F4" }}></span>
                                <span className="ml-3">OS標準ソフト</span>
                            </div>
                        </td>
                        <td style={{ borderLeft: "None", borderTop: "None", borderBottom: "None", borderRight: "None" ,width:'20%'}}>
                            <div className="mt-3 ml-5" style={{ display: "flex" }}>
                                <span className="bar ml-1" style={{  width: "20%", background: "#FF6919" }}></span>
                                <span className="ml-3">未導入</span>
                            </div>
                        </td>
                    </tr>
            </tbody>
        </ReactBootStrap.Table>
    );
}
export default SecuritySoftwareUsageStatusGraph;