import React, { useEffect, useState } from 'react';
import HeaderComponent from './../reusableComponents/HeaderComponent';
import NasAgentComponent from './NasAgentComponent';
import FaxComponent from './FaxComponent';
const NAS_FAX_Component = (props) => {
    const [nasheader, setNasheader] = useState([]);
    const [faxheader, setFaxheader] = useState([]);
    const [headerinfo, setheaderinfo] = useState({nasheader:'', faxheader:''});
useEffect(()=>{
        if(props.data)
        {   
            const nas_header_data=props.data.nas_info.nas_name + "の情報";
            let fax_header_data="";
            //checking whether the fax data is undefined or not 
            if(typeof(props.data.fax_data) !=="undefined")
            {
             fax_header_data=`[日別]　NASへのファクス転送件数 (${props.data.fax_data.start_date +"~"+ props.data.fax_data.end_date})`;
            }
            setheaderinfo({...headerinfo, nasheader:nas_header_data, faxheader:fax_header_data});        }
    },[props.data]);
return(
    <div >   
    
    < HeaderComponent data= {{header_data: headerinfo.nasheader,row_data:<NasAgentComponent nas_info={props.data.nas_info} id={props.data.nas_id}/>,header_style:"card-header",header_height:"60px"}}/>
     < HeaderComponent data= {{header_data: props.data.fax_data === 'NoHitError'? '[日別]　NASへのファクス転送件数' :headerinfo.faxheader,row_data:<FaxComponent data={props.data.fax_data} id={props.data.fax_id}/>,header_style:"card-header",header_height:"60px"}}/>
     
    </div>
    )

}
export default NAS_FAX_Component;