//calculation and assessment for Pcs that need attention
const Top3InAttentionPc = (pcInfoScoreList) => {
  return pcInfoScoreList.map(prop => {
    //Calculating the danger count based on OS Storage and security score
    let dangerCnt = (() => {
      let cnt = 0
      if (prop.os_status_score < 2)         cnt += 1;
      if (prop.storage_status_score < 2)    cnt += 1;
      if (prop.security_status_score < 2)   cnt += 1;
      return cnt
    })();
    //if(dangerCnt===0) return; based on the latest implementation default pc data to be displayed
    //scoreSecurityStatus api gives the status of the PC security based on security score
    let security_status_props = scoreSecurityStatus(prop.security_status_score, prop.pc_name);
    //scoreOsStatus api gives the status of the PC os basd on os score
    let os_status_props       = scoreOsStatus(prop.os_status_score, prop.pc_name);
    //scoreStorageStatus api gives the  status of the PC storage based on storage score
    let storage_status_props  = scoreStorageStatus(prop.storage_status_score, prop.pc_name);

    //creating empty list for security os and storage status
    let security_status = [];
    let os_status       = [];
    let storage_status  = [];

    //assigning the values to the security os and storage 
    security_status = {...prop.security_status_score, security_status_props }
    os_status       = {...prop.os_status_score,       os_status_props       }
    storage_status  = {...prop.storage_status_score,  storage_status_props  }

    if (prop.pc_name == null || prop.pc_name == "undefined") {
      prop.pc_name = "-";
    }
    return {
      os_status_score       : os_status,
      storage_life_score    : storage_status,
      security_status_score : security_status,
      danger_cnt            : dangerCnt
    };
  }).sort((a, b) => b.danger_cnt - a.danger_cnt).slice(0, 3);
  //pcInfoScoreList.filter(item => item !== undefined && item !== null)
};

const Top3InAttentionPcList = (pcInfoScoreList) => {
  let pcNoteInfo            = Top3InAttentionPc(pcInfoScoreList)
  let pcNoteInfoDefault     = pcNoteInfo.filter(prop => prop === undefined);
  let pcNoteInfoDefault1    = [];
  pcNoteInfoDefault1        = pcNoteInfo.filter(prop => prop !== undefined);

  pcNoteInfoDefault.map((item, idx) => {
    let security_status_props = scoreSecurityStatus ("undefined", "-");
    let os_status_props       = scoreOsStatus       ("undefined", "-");
    let storage_status_props  = scoreStorageStatus  ("undefined", "-");
    
    let security_status = { security_status_props }
    let os_status       = { os_status_props       }
    let storage_status  = { storage_status_props  }

    pcNoteInfoDefault1  = [...pcNoteInfoDefault1,
    {
      os_status_score       : os_status,
      storage_life_score    : storage_status,
      security_status_score : security_status,
      danger_cnt            : 0
    }];
});
return pcNoteInfoDefault1;
};

//PC that needs attention properties for OS
const scoreOsStatus = (os_score, pc_name) => {
  let title = "OS"
  return (() => {
    if (1 == os_score)
      return { pc_name: pc_name, title: title, status: "危険", color: "white", background: "#FF6919",border:"#FBED50" };
    if (2 == os_score)
      return { pc_name: pc_name, title: title, status: "注意", color: "#FF6919", background: "none",border:"#FF6919" };
    if ("undefined" == os_score)
      return { pc_name: pc_name, title: title, status: "-", color: "black", background: "none",border:"black" };
    return { pc_name: pc_name, title: title, status: "安全", color: "#008EA6", background: "#B1E7F4",border: "#008EA6"};
  })();
};

//PC that needs attention properties for security
const scoreSecurityStatus = (security_score, pc_name) => {
  let title = "セキュリティ"
  return (() => {
    if (1 == security_score)
      return { pc_name: pc_name, title: title, status: "危険", color: "white", background: "#FF6919",border:"#FBED50" };
    if (2 == security_score)
      return { pc_name: pc_name, title: title, status: "注意", color: "#FF6919", background: "none",border:"#FF6919" };
    if ("undefined" == security_score)
      return { pc_name: pc_name, title: title, status: "-", color: "black", background: "none",border:"black" };
    return { pc_name: pc_name, title: title, status: "安全", color: "#008EA6", background: "#B1E7F4",border: "#008EA6" };
  })();
};

//PC that needs attention properties for storage
const scoreStorageStatus = (storage_score, pc_name) => {
  let title = "ストレージ"
  return (() => {
    if (1 >= storage_score)
      return { pc_name: pc_name, title: title, status: "危険", color: "white", background: "#FF6919",border:"#FBED50" };
    if (2 >= storage_score)
      return { pc_name: pc_name, title: title, status: "注意", color: "#FF6919", background: "#66000000" ,border:"#FF6919"};
    if ("undefined" == storage_score)
      return { pc_name: pc_name, title: title, status: "-", color: "black", background: "none",border:"black" };
    return { pc_name: pc_name, title: title, status: "安全", color: "#008EA6", background: "#B1E7F4",border: "#008EA6" };
  })();
};

const calcPercent = (_value, _sum) => {
  const percent = Math.round((_value / _sum) * 100 * 10) / 10;
  // percent補正 (0%, 5%, 5%以上)
  return (() => {
    if (percent <= 0) return 0;
    if (percent < 5) return 5;
    return percent;
  })();
};

//Calculate security measures
const tallySecuritySoftTo3Grade = (pcInfoScoreList) => {
  const cnt = pcInfoScoreList.reduce( 
    ({ secure, def, danger, sum }, next) => {
        // "無し"ならdanger+1,sum+1
        if (next.secSoftName == "無し")
          return { secure: secure, def: def, danger: danger + 1, sum: sum + 1 };
        // "Windows Defender"ならdef+1,sum+1
        if (next.secSoftName == "Windows Defender")
          return { secure: secure, def: def + 1, danger: danger, sum: sum + 1 };
        // それ以外ならsecure+1,sum+1
        return { secure: secure + 1, def: def, danger: danger, sum: sum + 1 };
      },
      { secure: 0, def: 0, danger: 0, sum: 0 }
    );
  
    const inputPercentList = [];
    for (let idx in cnt) {
      if (idx == "sum") continue;
      inputPercentList.push(calcPercent(cnt[idx], cnt.sum));
    }
  
    const sumPercent = inputPercentList.reduce((sum, next) => {
      return (sum += next);
    }, 0);
  
    const overPercent = sumPercent - 100;
    const overPercentDev2 = (sumPercent - 100) / 2;
    // 最小値のindex
    const idxMin = inputPercentList.indexOf(Math.min(...inputPercentList));
    // 最大値のindex
    const idxMax = inputPercentList.indexOf(Math.max(...inputPercentList));
    // 最大値の数
    const maxNum = inputPercentList.filter(
      (num) => num === inputPercentList[idxMax]
    ).length;
    // 5%+過剰％割る2以上ある値の数
    const difNum = inputPercentList.filter((prop) => prop > 5 + overPercentDev2)
      .length;
  
    const percent = (() => {
      // パーセントの合計が100を超える場合
      if (sumPercent > 100)
        return (() => {
          // 余剰%を引くことが可能な数が1かつ最大値を持つ数が1つの場合
          if (difNum === 1 && maxNum === 1)
            // 最大値から余剰パーセントを引いたリストを返す
            return inputPercentList.map((prop, i) => {
              if (i === idxMax) return prop - overPercent;
              return prop;
            });
          // それ以外の場合
          // 最小値以外から余剰パーセント割る2を引いたリストを返す
          return inputPercentList.map((prop, i) => {
            if (i !== idxMin) return prop - overPercentDev2;
            return prop;
          });
        })();
      // パーセントの合計が100を超えない場合
      return inputPercentList;
    })();
  
    return {
      "software_introduction": {
        percent: percent[0],
        value: `${cnt.secure}台`
      },
      "os_software": {
        percent: percent[1],
        value: `${cnt.def}台`
      },
      "not_introduced": {
        percent: percent[2],
        value: `${cnt.danger}台`
      }
  };
};


