///<summary>
// api parse fsecure dialy contimination reponse
///</summary>
function fsecureDailyContamination(response) {

    var fsecuredailycontaminationresponse = "NoHitError"; //default result value
    
    //Condition validates data 
    if ((undefined    === response.hits)             || (undefined === response.hits.total) ||
        (("undefined" !== response.hits.total.value) && (0 === response.hits.total.value))) {
          
        return fsecuredailycontaminationresponse; //returns invalid data result
    }
    else {

        //computes list of fsecureDailyContamination based on buckets
        fsecuredailycontaminationresponse = response.aggregations.daily_buckets.buckets.map(bucket => {
          //computes contamination count value
          const contamination_count = (() => {
              if (bucket.log.buckets[0]) return bucket.log.buckets[0].contamination.value;
              return 0
            })();

            //json object of contamination count
            return {
              date          : bucket.key_as_string,
              contamination : contamination_count
            };
          });

        return fsecuredailycontaminationresponse; //returns list of fsecureDaily contamination 
    }
}
export { fsecureDailyContamination };