import React, { useEffect, useState } from 'react';
import c3 from 'c3'
import * as d3 from 'd3';
import { useDispatch } from 'react-redux';
let displayText = false;
const ApplicationUsageGraph = (props) => {
    let dispatch = useDispatch();
    useEffect(() => {
        if (props.stateData.data) {
           
           drawChart();
           dispatch({type:"setportalstatus","tenantid":props.stateData.tenantid,value:true}); 
        }
    }, [props.stateData.data]);

    const drawChart = () => {
        if(props.stateData.data === 'NoHitError'){
            displayText = true;
        }else{
            let chart_date = c3.generate({
                bindto: `#`+props.stateData.bindId,
                size: { width: 1050, height: 400 },
                data: {
                    x: "x",
                    columns: props.stateData.data.graph_data,
                    type: 'bar',
                    colors: {
                        "利用回数": '#B1E7F4',
                    },
                    labels: {
                        format: {
                            "利用回数": function(v, id, i, j) {
                                if (props.stateData.data.graph_label_data[i + 1] === null) { return; };
                                return v + "回 / " + props.stateData.data.graph_label_data[i + 1] + "%";
    
                            }
                        }
                    }
                },
                bar: {
                    width: {
                        ratio: 0.5 // this makes bar width 50% of length between ticks
                    }
                },
                axis: {
                    rotated: true,
                    x: {
                        type: "category",
                        tick: {
                            rotate: 75,
                            multiline: false,
                            outer:false,
                        }
                    },
                    y: {
                        show: false
                    }
    
                },
                legend: {
                    show: false
                },
                grid: {
                    y: {
                        show: true
                    }
                },
                onrendered: function() {
                    try {
                        console.log("lines");
                        let x_list = [];
        
                        d3.select(`#`+props.stateData.bindId)
                            .select("svg")
                            .select(".c3-texts.c3-texts-利用回数").selectAll("text").nodes().forEach(function(el, i) {
                                d3.select(el).style("fill", "black");
                                d3.select(el).style("font-size", "12px");
                                x_list.push(Number(d3.select(el).attr("x")));
                            });
        
                        const aryMax = function(a, b) { return Math.max(a, b); }
                        let max_x_num = 0;
                        max_x_num = x_list.reduce(aryMax); // => 10
        
                        d3.select(`#`+props.stateData.bindId)
                            .select("svg")
                            .select(".c3-texts.c3-texts-利用回数").selectAll("text").nodes().forEach(function(el, i) {
                                d3.select(el).attr("x", max_x_num);
                            });
                        d3.select(`#`+props.stateData.bindId)
                            .select("svg")
                            .select(".c3-axis.c3-axis-x").selectAll("text").nodes().forEach(function(el, i) {
                                d3.select(el).style("font-size", "12px");
                            });
                        d3.selectAll(".c3 .c3-grid line" ) 
                            .style("stroke", "lightgray")
        
                        
                    } catch (error) {
                        
                    }
                 }
            });
        }
       
    }

    return(<div>
        <div id={props.stateData.bindId}>
        <div style={{ height: '13rem', fontSize: '30px', display: displayText ? "" : "None" }}>
                    <div style={{ margin: 'auto', paddingTop: '80px', textAlign: 'center' }}>
                        {' 期間中にCOCORO　OFFICEアプリの機能が使用されておりません。'}
                    </div>
                </div>
        </div>
    </div>);
    
}

export default ApplicationUsageGraph