import React, { useEffect, useState } from 'react';
import * as ReactBootStrap from 'react-bootstrap';
import './../../StylesCSS/cssStyle.css';
import bikkuri from './../../images/bikkuri.png';
const SummaryBarGraphComponenet = (props) => {       
    const capacity_percentage = props.data[0].storage_capacity_score.value + "%";
    
    const temperature_percentage = props.data[0].storage_temp_score.value + "%";

    const lifespan_percentage = props.data[0].storage_life_score.value + "%";

    const error_percentage = props.data[0].storage_error_score.value + "%";

    const os_percentage = props.data[0].osplatform_score.value + "%";

    const os_version_percentage = props.data[0].osUpdate_score.value + "%";

    const firewall_percentage = props.data[0].firewall_score.value + "%"; 

    const security_percentage = props.data[0].security_score.value + "%";    

    return (
        <div >
            <ReactBootStrap.Table className="bar_table">
            <tbody >
                    <tr>
                        <td align="center" className="border-hide-l" colspan="3" style={{width:"30%"}}>PC台数 : {props.data[0].TotalNumberOfPcCount}台</td>
                        <td align="left" style={{fontSize: '110%',width:"14%"}}>危険</td>
                        <td style={{width:"14%"}}></td>
                        <td align="center" style={{fontSize: '110%',width:"14%"}}>(状態)</td>
                        <td style={{width:"14%"}}></td>
                        <td  align="right" style={{fontSize: '110%',width:"14%"}}>安全</td>
                    </tr>
                    <tr>
                        <td className="border-hide-l" rowSpan="4" style={{ width: "1%"}}>
                            <span className="vertical-text mt-5" >ストレージ</span>
                            <span  style={{fontSize:"80%",marginLeft:"15%"}} className="vertical-text" > (平均点 :
                            <span  style={{fontSize:"100%"}} className="horizontal-text mt-1" >{props.data[0].storageAveScore}</span>
                            <span  className="vertical-text" >)</span>
                           </span>
                            </td>
                        <td style={{width:"5%"}}></td>
                        <td align="center" style={{ width: "20%"}}>{props.data[0].storage_capacity_score.image}容量</td>
                        <td>
                            <div >
                                <div className="bar" style={{fontSize: '110%', color: props.data[0].storage_capacity_score.color, width: capacity_percentage }}>{props.data[0].storage_capacity_score.value}点 </div>
                            </div>
                        </td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td ></td>
                    </tr>
                    <tr>   
                        <td></td>                     
                        <td align="center" >{props.data[0].storage_temp_score.image}平均温度</td>
                        <td id="id_storage_bar_temp">
                            <div >
                                <div className="bar" style={{fontSize: '110%',color: props.data[0].storage_temp_score.color, width: temperature_percentage }}>{props.data[0].storage_temp_score.value}点</div>
                            </div>
                        </td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td ></td>
                    </tr>
                    <tr>       
                        <td></td>                 
                        <td align="center" >{props.data[0].storage_life_score.image}稼働時間</td>
                        <td id="id_storage_bar_life">
                            <div>
                                <div className="bar" style={{fontSize: '110%', color: props.data[0].storage_life_score.color, width: lifespan_percentage }}>{props.data[0].storage_life_score.value}点</div>
                            </div>
                        </td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td ></td>
                    </tr>
                    <tr>  
                        <td></td>                      
                        <td align="center" >{props.data[0].storage_error_score.image}エラー回数</td>
                        <td id="id_storage_bar_errornum">
                            <div >
                                <div className="bar" style={{color: props.data[0].storage_error_score.color, width: error_percentage }}>{props.data[0].storage_error_score.value}点</div>
                            </div>
                        </td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td className="border-hide-l" rowSpan="2" >
                        <span className="vertical-text mb-4">OS </span>
                        <span  style={{fontSize:"80%",marginLeft:"15%"}} className="vertical-text" > (平均点 :
                        <span  style={{fontSize:"100%"}} className="horizontal-text mt-1" >{props.data[0].osAveScore}</span>
                        <span   className="vertical-text" >)</span>
                           </span>
                            </td>
                        <td></td>
                        <td align="center" >{props.data[0].osplatform_score.image}OSバージョン</td>
                        <td id="id_os_bar_update">
                            <div >
                                <div className="bar" style={{color: props.data[0].osplatform_score.color, width: os_percentage }}>{props.data[0].osplatform_score.value}点</div>
                            </div>
                        </td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td></td>
                        <td align="center" >{props.data[0].osUpdate_score.image}アップデート設定</td>
                        <td id="id_os_version_update">
                            <div >
                                <div className="bar" style={{color: props.data[0].osUpdate_score.color, width: os_version_percentage }}>{props.data[0].osUpdate_score.value}点</div>
                            </div>
                        </td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>

                    <tr>
                        <td className="border-hide-l" rowSpan="2">
                        <span className="vertical-text">セキュリティ </span>
                        <span  style={{fontSize:"80%",marginLeft:"15%"}} className="vertical-text" > (平均点 : 
                        <span  style={{fontSize:"100%"}} className="horizontal-text mt-1" >{props.data[0].securityAveScore}</span>
                        <span  className="vertical-text" >)</span>
                        </span>
                        </td>     
                        <td></td>                   
                        <td align="center" >{props.data[0].firewall_score.image}ファイアウォール</td>
                        <td id="id_security_bar_firewall">
                            <div >
                                <div className="bar" style={{color: props.data[0].firewall_score.color, width: firewall_percentage }}>{props.data[0].firewall_score.value}点</div>
                            </div>
                        </td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr> 
                    <tr>    
                        <td ></td>                    
                         <td align="center" >{props.data[0].security_score.image}セキュリティソフト</td>
                        <td id="id_security_bar_soft">
                            <div >
                                <div className="bar" style={{color: props.data[0].security_score.color, width: security_percentage }}>{props.data[0].security_score.value}点</div>
                            </div>
                        </td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                </tbody>
            </ReactBootStrap.Table>
        </div>
    );
}
export default SummaryBarGraphComponenet;


