//import modules
import React, { useEffect, useState }   from 'react';
import FsecureGraphComponent            from './FsecreGraphComponent';
import HeaderComponent                  from '../reusableComponents/HeaderComponent';
import FSecureListTable                 from './FsecureListTable';
import FsecureBarComponent              from './FsecureBarComponent';

///<summary>
// Fsecure main component
///</summary>
const FsecureComponent = (props) => {

    //create headerinfo object
    const [headerinfo, setheaderinfo] = useState({ graph_header: '', list_table_header:''});

    //initialize headerinfo object
    useEffect(() => {
        if (props.stateData) {
            let dateSplit   = props.stateData.f_secure_block_info.log_time.split("月");
            let date        = dateSplit[0] + "月";
            let graphHeader = `[日別] ブロックした脅威の件数（${date})`;
            setheaderinfo({graph_header:graphHeader, list_table_header:"各PCの状態"});
        }
    }, [props.stateData]);

   
    return (
      <div>
        <FsecureBarComponent data={props.stateData} />
        
        < HeaderComponent   data={{
                header_data     : headerinfo.graph_header,
                row_data        : <FsecureGraphComponent stateData ={{
                                        bindId : "id_fsecure_graph" + props.tenantid,
                                        data   : props.stateData.f_secure_block_info
                                    }}/>,
                header_style    : "card-header",
                header_height   : "60px"
            }} />

         < HeaderComponent  data={{
                header_data     : headerinfo.list_table_header,
                row_data        : <FSecureListTable data={props.stateData.f_secure_pc_info} />,
                header_style    : "card-header",
                header_height   : "60px"
            }} />
        </div>
    )
}

export default FsecureComponent;