import * as ReactBootStrap from 'react-bootstrap';


const NasAgentTable = (props) => {
    return (
        <div style={{marginTop:"1.25rem"}}  >
            <ReactBootStrap.Table className="nas_graph_table">
                <thead>
                        <tr>
                            <th colSpan='4' className="Table-header" style={{fontSize:"1.45rem",borderBottom:'1px solid rgba(0, 0, 0)'}}>
                            基本情報
                            </th>
                        </tr>
                </thead>
                <tbody>
                        <tr>
                            <td rowSpan="2">バージョン</td>
                            <td> 拡張サービス:{props.data.hardware_type}</td>
                            <td rowspan="2">IPアドレス</td>
                            <td rowspan="2">{props.data.nas_ipaddress}</td>
                        </tr>

                        <tr>
                            <td>ファームウェア:{props.data.nas_firmware_version}</td>
                        </tr>

                        <tr>
                            <td >シリアル番号</td>
                            <td>{props.data.sharp_serial_number}</td>
                            <td>MACアドレス</td>
                            <td>{props.data.nas_hostname}</td>
                        </tr> 
                        
                </tbody>
         </ReactBootStrap.Table>
         </div>
    )};
    export default NasAgentTable