//namespaces 
import React, { useEffect, useState } from 'react';
import c3 from 'c3';
import * as d3 from 'd3';
import './../../StylesCSS/style.css'
import { useDispatch } from 'react-redux';
///########################################
///Nas info component
///#######################################
const NasInfoComponent = (props) => {
    let dispatch = useDispatch()
    //Invoke draw chart only if the nas_data is modified
    useEffect(() => {
        if (props.data) {
            setTimeout(() => {
                dispatch({ type: "setnasstatus","tenantid":props.id.split('id_nas_graph')[1], value: true });
            }, 200);
            drawChart();

        }
    }, [props.data]);

    //api which reads the nas_data and creates a chart
    const drawChart = () => {
        let chartc = c3.generate({
            bindto: "#"+props.id,
            size: {
                width: 1100,
                height: 330
            },
            data: {
                x: 'date',
                columns: props.data,
                colors: {
                    '使用容量': '#B1E7F4',
                    '空き容量': '#dcdcdc',
                    'ファイル数': '#FF6919'
                },
                axes: {
                    'ファイル数': 'y2'
                },
                type: 'bar',
                types: {
                    'ファイル数': 'line'

                },
                groups: [
                    ['使用容量', '空き容量']
                ],
                order: 'null'
            },
           
            axis: {

                x: {
                    type: 'category',
                    tick: {
                        rotate: 0,
                        fit: true,
                        multiline: false,
                        outer:false,
                    }
                },
                y:{
                    tick: {
                        outer:false,
                    },
                    label: {
                        text:'容量 (TB)',
                        position: 'outer-middle'},
                    },
                y2: {
                    show: true,
                    min: 0,
                    padding: { top: 0, bottom: 0 },
                    label: {
                        text: 'ファイル数',
                        position: 'outer-middle'
                    },
                    tick: {
                        outer:false,
                        format: function (d) {
                            return d % 1 === 0 ? d.toLocaleString() : "";
                        }
                    } 
                }

            },
            grid: {
               
                y: {
                    show: true
                }
            },
            legend: {
                position: 'bottom'
            },
            onrendered: function () {   
                try {
                    const legendElementUsed = d3.select("#"+props.id)
                        .select("svg")
                        .select(".c3-legend-item.c3-legend-item-使用容量")
                    legendElementUsed.select("text").attr("x", 442);
                    legendElementUsed.select("line").attr("x1", 417);
                    legendElementUsed.select("line").attr("x2", 437);
                    legendElementUsed.select("line").attr("stroke-width", 18);
                    const legendElementFree = d3.select("#"+props.id)
                        .select("svg")
                        .select(".c3-legend-item.c3-legend-item-空き容量")
                    legendElementFree.select("line").attr("x1", 501);
                    legendElementFree.select("line").attr("stroke-width", 18);
                    let legendCircle = d3.select("#"+props.id)
                        .select("svg")
                        .select(".c3-legend-item.c3-legend-item-ファイル数")
                        .select("circle")
                        .nodes();
                    if (legendCircle.length) return;
                    const legendLine = d3.select("#"+props.id)
                        .select("svg")
                        .select(".c3-legend-item.c3-legend-item-ファイル数")
                        .select("line")
                    const legendLineStart = legendLine.attr("x1");
                    const legendLineEnd = legendLine.attr("x2");
                    const legendCircleCenter = Number(legendLineEnd) - ((Number(legendLineEnd) - Number(legendLineStart)) / 2);
                    d3.select("#"+props.id)
                        .select("svg")
                        .select(".c3-legend-item.c3-legend-item-ファイル数")
                        .select("line")
                        .remove();
                    d3.select("#"+props.id)
                        .select("svg")
                        .select(".c3-legend-item.c3-legend-item-ファイル数")
                        .insert("circle")
                        .attr("r", "2.5")
                        .attr("cx", legendCircleCenter)
                        .attr("cy", "4")
                        .attr("fill", "rgb(255, 105, 25)")
                        .attr("stroke", "rgb(255, 105, 25)");

                        //remove filling of line in d3 chart for PDF
                        d3.selectAll("#"+ props.id + " .c3-chart-line.c3-target.c3-target-ファイル数").nodes().forEach(function (el, i) {
                            d3.select(el).style("fill", "none");
                      
                        d3.selectAll(".c3-axis-y2-label")
                            .style("font-weight", "normal")

                        d3.selectAll(".c3-axis-y-label")
                            .style("font-weight", "normal")    
   
                        });

                } catch (error) {
                }    
                   
            }
        });
    }
        

    return (
        <div style={{ marginLeft: "10%" }}>
            <div id={props.id}></div>
        </div>
    );

}

export default NasInfoComponent;