import logviewerUtility from './logviewerUtility'

//creating object for the logviewerutility
const utility = new logviewerUtility()

export const PCInfoscore = (pcInfo) => {
  if (pcInfo == null || pcInfo == "undefined") {
    return;
  }
  //calling checkpcagentversion api using utility object 
  const versionFrag = utility.CheckPCAgentVersion(pcInfo.pcagent_version)
  
  // Extract smart information for C drive
  const smartInfo = (() => {
    const smartInfoC = pcInfo.smart_info.filter((prop) =>
      prop.drive_letter.includes("C")
    );
    // Cドライブが取得できない場合は満点扱い
    if (smartInfoC.length === 0)
      return [
        {
          seek_error_rate: 0,
          power_on_hours: 0,
        },
      ];
    return smartInfoC;
  })();

  // セキュリティソフト名の抽出　(無し or 名前)
  const secSoftName = extractSecSofName(pcInfo);

  const pcInfoScore = {
    pc_name                : pcInfo.computer_name,
    storage_capacity_score : Number(scoreDriveSize(pcInfo)),
    storage_temp_score     : Number(scoreDriveTemperature(pcInfo,versionFrag)),
    storage_life_score     : Number(scoreLifeLimit(smartInfo,versionFrag)),
    storage_errornum_score : Number(scoreDriveError(smartInfo,versionFrag)),
    securitysoft_score     : Number(scoreAntiVirusSoft(secSoftName)),
    osupdate_score         : Number(scoreOsUpdate(pcInfo,versionFrag)),
    firewall_score         : Number(scoreFirewall(pcInfo)),
    osplatform_score       : Number(scoreOsplatform(pcInfo)),
  };

  // セキュリティ,OS,ストレージの3項目での点数に集約
  const aggregateTo3ParamScore = aggregateTo3Param(pcInfo, pcInfoScore);

  return {
    ...pcInfoScore,
    ...aggregateTo3ParamScore,
    sortScore   : scoreAttentionPc(aggregateTo3ParamScore),
    pcInfo      : pcInfo,
    secSoftName : secSoftName,
  };  
};

function extractDriveSize(_pcInfo, driveName) {
    // 使用容量の値を追加
    const storageList = _pcInfo.storage.map((prop) => {
      const usedStorageSize = Math.round(
        prop.all_storage_size - prop.avail_storage_size
      );
      return {
        ...prop,
        usedStorageSize: usedStorageSize
      }
    });
    const Drive = storageList.find((prop) => prop.name === driveName);
    return {
      driveUsedSize: Drive.usedStorageSize,
      driveTotalSize: Drive.all_storage_size,
    };
  }
  
  const extractSecSofName = (_pcInfo) => {
    // セキュリティソフトの判定(True のリストを取得)
    let secOnlist = _pcInfo.anti_virus_software.filter((prop) => prop.enabled);
    // 2つ以上あるならWindows Defenderを除外
    if (secOnlist.length >= 2) {
      secOnlist = secOnlist.filter((prop) => prop !== "Windows Defender");
    }
    return (() => {
      if (secOnlist.length === 0) return "無し";
      return secOnlist[0].name;
    })();
  };
  
  const scoreDriveSize = (_pcInfo) => {
    // HDD容量採点
    const cDrive = extractDriveSize(_pcInfo, "C");
    const driveSizeUsedPercentage = Math.round(
      (cDrive.driveUsedSize / cDrive.driveTotalSize) * 100
    );
  
    return (() => {
      if (driveSizeUsedPercentage > 90) return 1;
      if (90 >= driveSizeUsedPercentage && driveSizeUsedPercentage > 70) return 2;
      return 3;
    })();
  };
  
  const scoreDriveTemperature = (_pcInfo,_versionFrag) => {
    // HDD温度採点
    return (() => {
      //return temperatue 1 when versionfrag is false and temperatue is greater than 100
      if ((!_versionFrag) &&(_pcInfo.hdd_temperature > 50)) return 1;
      //return temperatue 1 when versionfrag is false and temperatue is greater than 100
      if ((!_versionFrag) &&(50 >= _pcInfo.hdd_temperature && _pcInfo.hdd_temperature > 40)) return 2;
      return 3;
    })();
  };
  
  const scoreLifeLimit = (_smartInfo,_versionFrag) => {
    // HDD寿命採点
    const lifLimitPercentage = Math.round(
      (_smartInfo[0].power_on_hours / 10000) * 100
    );
    return (() => {
      //return lifelmt 1 when versionfrag is false and lifelimitpercentage is greater than 100
      if((!_versionFrag) && (lifLimitPercentage > 100)) return 1
      //return lifelmt 2 when versionfrag is false and lifelimitpercentage is greater than 100 and less than 70
      if ((!_versionFrag) && (100 >= lifLimitPercentage && lifLimitPercentage > 70)) return 2
      return 3;
    })();
  };
  
  const scoreDriveError = (_smartInfo,_versionFrag) => {
    // エラー回数採点
    return (() => {
      //return errorCount 1 when versionfrag is false and lifelimitpercentage is greater than 100
      if ((!_versionFrag) &&(_smartInfo[0].seek_error_rate > 1)) return 1;
      //return errorCount 2 when versionfrag is false and lifelimitpercentage is greater than 100 and less than 70
      if ((!_versionFrag) && (1 >= _smartInfo[0].seek_error_rate) && (_smartInfo[0].seek_error_rate > 0))
      return 2;
      return 3;
    })();
  };
  
  const scoreFirewall = (_pcInfo) => {
    return (() => {
      if (!_pcInfo.is_firewall_on) return 1;
      return 3;
    })();
  };
  
  const scoreAntiVirusSoft = (_secSoftName) => {
    return (() => {
      if (_secSoftName === "無し") return 1;
      if (_secSoftName === "Windows Defender") return 3;
      return 3;
    })();
  };
  
  const scoreOsplatform = (_pcInfo) => {
    /*
    const osVersion = (() => {
      if (
        pcInfo.os_major_version === undefined ||
        pcInfo.os_minor_version === undefined ||
        pcInfo.os_build === undefined
      )
        return "(-)";
      return `(${pcInfo.os_major_version}.${pcInfo.os_minor_version}.${pcInfo.os_build})`;
    })();
    */
  
    const osPlatform = _pcInfo.os_platform.toLowerCase();
    return (() => {
      if (osPlatform.includes("windows 10") || osPlatform.includes("windows 8.1"))
        return 3;
      return 1;
    })();
  };
  
  const scoreOsUpdate = (_pcInfo,versionFrag) => {
   // "0"自動更新,"1"自動更新無効"
   const noAutoUpdate = _pcInfo.no_auto_update;
   // "4"全自動, "3"通知有り, "2"通知有り,"通知無し"
   const updateOption = _pcInfo.auto_update_options;
   return (() => {
     // 空なら満点に仮定
     if(versionFrag)                                                return 3;
     if (noAutoUpdate === "1")                                      return 1;
     if (updateOption === "0"       &&  noAutoUpdate === "0")       return 3
     if (updateOption === "-1"      || updateOption === "-1")       return 3;
     if (updateOption === "0"       || updateOption === "")         return 3;
     if (noAutoUpdate === undefined || updateOption === undefined)  return 3;
     if (noAutoUpdate === "0"       && updateOption === "4")        return 3;
     if (noAutoUpdate === "0"       && updateOption === "3")        return 2;
     if (noAutoUpdate === "0"       && updateOption === "2")        return 2;
     return 1;
   })();
  
    /*
    // 今の時刻
    const nowDate = new Date();
    // 今から24ヵ月前
    const date2YearsAgo = nowDate.setFullYear(nowDate.getFullYear() - 2);
    // 今から3ヵ月前
    const date3MonAgo = nowDate.setMonth(nowDate.getMonth() - 3);
    // "xxxx-xx-xxTxx:xx:xx.xxxZ" -> [xxxx,xx,xx]
    const dateList = pcInfo.last_install_update.slice(0, 10).split("-");
    const updateDate = new Date(dateList[0], dateList[1], dateList[2]);
    return (() => {
      if (updateDate <= date2YearsAgo) return 1;
      if (date2YearsAgo < updateDate && updateDate <= date3MonAgo) return 2;
      return 3;
    })();
    */
  };
  
  const judgeSafetyOrAttentionOrDanger = (scoreList) => {
    /*
    1<=スコアが<2存在するものが1項目でもあれば、1点(danger)
    2<=スコア<3が存在するものが1項目でもあれば、2点(Attention)
    */
    const dangerList = scoreList.filter((score) => 1 <= score && score < 2);
    const AttentionList = scoreList.filter((score) => 2 <= score && score < 3);
    return (() => {
      if (dangerList.length > 0) return 1;
      if (AttentionList.length > 0) return 2;
      return 3;
    })();
  };
  
  const aggregateTo3Param = (pcInfo, pcInfoScore) => {
    // セキュリティ項目のスコアリスト
    const securityStatusScoreList = [ pcInfoScore.firewall_score, pcInfoScore.securitysoft_score];
    // OS項目のスコアリスト
    const osStatusScoreList = [ pcInfoScore.osupdate_score,       pcInfoScore.osplatform_score  ];
  
    // ストレージ項目のスコアリスト
    const storageStatusScoreList = [
      pcInfoScore.storage_capacity_score,
      pcInfoScore.storage_temp_score,
      pcInfoScore.storage_life_score,
      pcInfoScore.storage_errornum_score,
    ];
  
    return {
      security_status_score : judgeSafetyOrAttentionOrDanger(securityStatusScoreList),
      os_status_score       : judgeSafetyOrAttentionOrDanger(osStatusScoreList      ),
      storage_status_score  : judgeSafetyOrAttentionOrDanger(storageStatusScoreList ),
    };
  };
  
  const ScoreAttention = (_score, _magnification) => {
    return (() => {
      if (_score === 3) return 0 * _magnification;
      if (2 <= _score && _score < 3) return 10 * _magnification;
      return 100 * _magnification;
    })();
  };
  
  const scoreAttentionPc = (aggregateTo3ParamScore) => {
    const securityStatusAttentionScore = ScoreAttention(aggregateTo3ParamScore.security_status_score,3);
    const osStatusAttentionScore       = ScoreAttention(aggregateTo3ParamScore.os_status_score,      2);
    const storageLifeAttentionScore    = ScoreAttention(aggregateTo3ParamScore.storage_status_score, 1);
    const attentionScore               = securityStatusAttentionScore + osStatusAttentionScore + storageLifeAttentionScore;
    return attentionScore;
  };
