import danger from './../images/danger.png'
import safe from './../images/safety.png'
import note from './../images/note.png'
export const nas_parse_data = (response,selectedYear,selectedMon) => {
    var nasInfo = response.aggregations.nas_list.buckets;
    var last_data = nasInfo[0].latest_data.hits.hits[0]._source;
    var graph_data = createGraphData(nasInfo[0].monthly_buckets.buckets,selectedMon);
    const nas_strage_status = createNasStrageStatus(last_data);
    const log_time = `${selectedYear}年${selectedMon}月`;
    const dateBefore11Mon = new Date(Number(selectedYear), Number(selectedMon) -12, 1);
    const start_date = _dateToStr(dateBefore11Mon);
    const nas_basicinfo = {VMのバージョン: last_data.hardware_type, IPアドレス: last_data.nas_ipaddress, シリアル番号: last_data.sharp_serial_number, MACアドレス: last_data.device_id };
    const nas_name = (() => {
        if (last_data.nas_hostname == undefined || last_data.nas_hostname == "")
          return `BP-X1STシリーズ_${last_data.sharp_serial_number}`;
        return last_data.nas_hostname;
      })();
      
    return {
        nas_name: last_data.nas_hostname,
        nas_basic_info: last_data,
        graph_data: graph_data,
        start_date: start_date,
        end_date: log_time,
        nas_strage_status: nas_strage_status,
        log_time: log_time,
    };

}
const _dateToStr = (_date) => {
    // dateを 2020-01-01 のstrに変換
    const year = String(_date.getFullYear());
    const mon = _date.getMonth() + 1;
    const mon0Complement = (() => {
      if (mon >= 10) return String(mon);
      return "0" + String(mon);
    })();
    return `${year}年${mon0Complement}月`;
}

const yLimPadding0 = (_argList, _thisMom, _targetKey) => {
    const yLim = [];
    const xLim = _xLimPaddingMon([_thisMom]);
    const paddingSetting = null;
    while (yLim.length < 12) {
      let colum = yLim.length;
      let dataInThisMon = _argList.filter((prop) => prop.date === xLim[colum]);
      if (dataInThisMon.length === 0) {
        yLim.push(paddingSetting);
      } else {
        yLim.push(dataInThisMon[0][_targetKey]);
      }
    }
    return yLim;
  };
  const _xLimPaddingMon = (xLim) => {
    /*
      リスト先頭に一月前を追加の繰り返しにより横軸を12月分に埋める
      input ["10月","11月"]
      output ["12月","01月"..."10月","11月"]　
      */
    const calender = [
        "01月",
        "02月",
        "03月",
        "04月",
        "05月",
        "06月",
        "07月",
        "08月",
        "09月",
        "10月",
        "11月",
        "12月",
    ];
    let offset = calender.indexOf(xLim[0]);
    const xLimPadding = [...xLim];
    while (xLimPadding.length < 12) {
        offset = (() => {
            if (offset === 0) return 11;
            return (offset -= 1);
        })();
        // リスト先頭に一月前を追加
        xLimPadding.unshift(calender[offset]);
    }
    return xLimPadding;
};
const createNasStrageStatus = (last_data) => {
    const nasTotalSizeTB = MBToTB(last_data.avail_storage_size + last_data.used_storage_size)
    // 少数第一位まで丸める
    const nasTotalSizeTB_1 = Math.round(nasTotalSizeTB * 10) / 10;
    const nasUsedSizeTB = MBToTB(last_data.used_storage_size)
    const nasUsedSizeTB_1 = Math.round(nasUsedSizeTB * 10) / 10;
    const value = `${nasUsedSizeTB_1} TB / ${nasTotalSizeTB_1} TB`
    const value_per = Math.round(
        (last_data.used_storage_size /
            (last_data.avail_storage_size + last_data.used_storage_size)) *
        100
    );
    let html_template_status_safety = <img src={safe} />;;
    let html_template_status_note = <img src={note} />;;
    let html_template_status_danger =<img src={danger} />;
    const score = (() => {
        if (value_per > 90) return { color: "#FF6919", status: "danger", image: html_template_status_danger };
        if (90 >= value_per && value_per > 70)
            return { color: "#FF6919", status: "note", image: html_template_status_note };
        return { color: "#000000", status: "safety", image: html_template_status_safety };
    })();
    
    return {
        value: value,
        value_per: value_per,
        color: score.color,
        status: score.status,
        image:score.image
    }
};

const extractLogWithKey = (_buckets, _extractKey) => {
    return (() => {
        if (_buckets.length === 0) return null;
        if (_buckets[0][_extractKey].buckets[0].key == -1) return null;
        if (_extractKey === "file") return _buckets[0][_extractKey].buckets[0].key;
        return MBToTB(_buckets[0][_extractKey].buckets[0].key);
    })();
};

const extractLogWithFileKey = (_buckets, _extractKey) => {
    return (() => {
        if (_buckets.length === 0) return null;
        if (_buckets[0][_extractKey].buckets[0].key == -1) return null;
        return _buckets[0][_extractKey].buckets[0].key;
    })();
};

const createGraphData = (buckets,selectedMon) => {
    const thisMom = `${selectedMon}月`;
    const xLim = [thisMom];
    const xLimPadding = _xLimPaddingMon(xLim);

    const logList = buckets.map((prop) => {
        return {
            used: extractLogWithKey(prop.log.buckets, "used"),
            avail: extractLogWithKey(prop.log.buckets, "avail"),
            file: extractLogWithFileKey(prop.log.buckets, "file"),
            date: prop.key_as_string.slice(5, 7) + "月",
        };
    });

    return [
        ["date", ...xLimPadding],
        ["使用容量", ...yLimPadding0(logList, thisMom, "used")],
        ["空き容量", ...yLimPadding0(logList, thisMom, "avail")],
        ["ファイル数", ...yLimPadding0(logList, thisMom, "file")],
    ];
};
const MBToTB = (size) => {
    return Math.round((size / (1024 * 1024)) * 1000) / 1000;
};
const addaxisLabel = (unit, argList) => {
    return [unit, ...argList]
}