//namespaces 
import React, { useEffect, useState } from 'react';
import c3 from 'c3'
import './../../StylesCSS/style.css'
import * as d3 from 'd3'
import { useDispatch } from 'react-redux';
///########################################
///MFP info component
///#######################################
const MFPPieGraphComponent = (props) => {
    let dispatch = useDispatch();
    //Invoke draw chart only if the data is modified
    useEffect(() => {
        if (props.data.mfp_data) {
            drawChart();
            dispatch({type:"setmfpstatus","tenantid":props.data.tenantid,value:true}); 

        }
    }, [props.data.mfp_data]);

    //api which reads the nas_data and creates a chart
    const drawChart = () => {
        let mfp_printedave_chart = c3.generate({
            bindto: "#" + props.data.id,
            size: { width: 120, height: 120 },
            data: {
                columns: props.data.mfp_data.value,
                type: 'pie',

                groups: [
                    ['カラー', 'モノクロ']
                ],
                colors: {
                    'カラー': '#B1E7F4',
                    'モノクロ': '#dcdcdc'
                },
                order: 'asc'
            },
             legend: {
                    show: false
                },
            // legend: {
            //     position: 'right'
            // },
            pie: {
                label: {
                    format: function(value, ratio, id) {
                        // return value.toLocaleString();
                        return `${Math.round(ratio * 100)}%`;
                    },
                    threshold: 0.2
                }
            },
            onrendered: function () {
                try {
                    d3.selectAll("#" + props.data.id + " .c3-chart-arc.c3-target.c3-target-カラー").nodes().forEach(function (el, i) {
                        d3.select(el).select("text").style("fill", "black");
                    });
                    d3.selectAll("#" + props.data.id + " .c3-chart-arc.c3-target.c3-target-モノクロ").nodes().forEach(function (el, i) {
                        d3.select(el).select("text").style("fill", "black");
                    });
                    
                } catch (error) {
                    
                }
               
                // d3.selectAll("#" + props.data.id + " .c3-legend-item.c3-legend-item-カラー").nodes().forEach(function (el, i) {

                //     d3.select(el).select("text").text("カラー  "+props.data.mfp_data.value[0][1] + "枚");

                // });
                // d3.selectAll("#" + props.data.id + " .c3-legend-item.c3-legend-item-モノクロ").nodes().forEach(function (el, i) {

                //     d3.select(el).select("text").text("モノクロ  "+props.data.mfp_data.value[1][1] + "枚");

                // });
            }


        });
    }


    return (
        <div>

            <div id={props.data.id}></div>


        </div>
    );

}

export default MFPPieGraphComponent;