//import config file
const { AGENTSETTING } = require('./../config')

export class Logviewerutility
{
  //Method to check the pc agent version
   CheckPCAgentVersion = (inputVersion) =>{

        if (!inputVersion) return true;
       //fetching pcagent version from the config file 
        const pcAgentStandard       = AGENTSETTING.pcAgent;
        //split the pc agent version based on the index 
        const inputVersionSplit     = inputVersion.split(".");
        //split the pcAgentStandard version based on the index 
        const standardVersionSplit  = pcAgentStandard.split(".");
        //compares the input version with the standard version  
        for (let versionindex in inputVersionSplit) {
          if (Number(inputVersionSplit[versionindex] < Number(standardVersionSplit[versionindex])))
            return true;
        }
        //comparing the length of both input version and standard version
        if (inputVersionSplit.length < standardVersionSplit.length){
             return true;
        }
        return false;
      };

  calcTotalScore = (summaryGraphPram) => {
      let sumValue = 0;
      let avg      = 0;
    for (let index in summaryGraphPram) 
      {
        sumValue += summaryGraphPram[index];
      }
    return (avg = Math.round(sumValue / Object.keys(summaryGraphPram).length));
      };
}
 
export default Logviewerutility