import React, { useEffect, useState } from 'react';
import * as ReactBootStrap from 'react-bootstrap';


const TableComponent_rc = (props) => {
    const [columns, setcolumns] = useState([]);

    useEffect(() => {
        setcolumns(Object.keys(props.data.row_data));
    }, [props.data]);
    return (
        <div style={{marginTop:"1.25rem"}} >
            <ReactBootStrap.Table className="nas_graph_table">
                <thead>
                    {props.data.header_data.trim() != "" &&
                        <tr >
                            <th colSpan='4' className="Table-header" style={{fontSize:"1.45rem",borderBottom:'1px solid rgba(0, 0, 0)'}}>
                                {props.data.header_data}
                            </th>
                        </tr>
                    }
                </thead>
                <tbody>
                    {

                        columns.map((colname, idx) => (
                            idx % 2 == 0 &&
                            <tr key={idx}>
                                <td  >

                                    {columns[idx]}
                                </td>
                                <td  >
                                    {props.data.row_data[columns[idx]]}
                                </td>
                                <td >
                                    {columns[idx + 1]}
                                </td>
                                <td >
                                    {props.data.row_data[columns[idx + 1]]}
                                </td>

                            </tr>


                        )
                        )

                    }
                </tbody>
            </ReactBootStrap.Table>
        </div>
    );
}
export default TableComponent_rc;