import React, { useEffect, useState } from 'react';
import { QueryData } from './queryComponent/nasquery';
import GetNASInfoAction from './../Actions/nasinfoAction';
import GetPCStatusAction from './../Actions/PCStatusAction';
import GetFaxAction from './../Actions/faxAction';
import '../StylesCSS/cssStyle.css'
import { useDispatch, useSelector } from 'react-redux';
import Tenantmanagement from './TenantManagement/Tenantmanagement';
import gettenantIdAction from '../Actions/tenantIdAction';
import GetMFPAction from '../Actions/mfpaction';
import GetApplicationStatus from '../Actions/ApplicationUsageAction';
import Dashboardcomponent from './Dashboardcomponent';
import * as ReactBootStrap from 'react-bootstrap';
import ProgressBarComponent from './ProgressBarComponent';
const LogviewerComponent = () => {
  let filedownload = {}
  let dispatch = useDispatch();
  const [tenantData, setTenantList] = useState([]);
  const [date, setDate] = useState('');
  let reducerStateData = useSelector(state => state.searchComponent.lst_tenant_id);
  const [multiPrint, setMultiPrint] = useState(false);
  useEffect(() => {
    let nasQry = new QueryData();
    let tenantQry = nasQry.tenantid_query();
    tenantQry["token"] =window.sessionStorage.getItem("token");
    dispatch(gettenantIdAction(tenantQry));
  }, []);
  const dateToStr = (date) => {
    // converting date to string
    const year = String(date.getFullYear());
    const mon = date.getMonth() + 1;
    const mon0Complement = (() => {
      if (mon >= 10) return String(mon);
      return "0" + String(mon)
    })();
    const day = date.getDate();
    const day0Complement = (() => {
      if (day >= 10) return String(day);
      return "0" + String(day)
    })();
    return `${year}-${mon0Complement}-${day0Complement}T00:00:00Z`
  };
  const defineDateRange = (selectedYear, selectedMon) => {
    //starting date of selected month
    const startDate = new Date(Number(selectedYear), Number(selectedMon) - 1, 1);
    //end date of selected month
    const endDate = new Date(Number(selectedYear), Number(selectedMon), 0);
    // 11 month before date 
    const dateBefore11Mon = new Date(Number(selectedYear), Number(selectedMon) - 12, 1);
    // 12 month before date
    const dateBefore12Mon = new Date(Number(selectedYear), Number(selectedMon) - 13, 1);
    // 翌月初めの日付を取得
    const nextMonStartDate = new Date(Number(selectedYear), Number(selectedMon), 1);
    // 翌月末の日付を取得
    const nextMonEndDate = new Date(Number(selectedYear), Number(selectedMon) + 1, 0);

    return {
      start: dateToStr(startDate),
      end: dateToStr(endDate),
      endDateBefore1Mon: dateToStr(endDate),
      before11Mon: dateToStr(dateBefore11Mon),
      before12Mon: dateToStr(dateBefore12Mon),
      nextMonStartDate: dateToStr(nextMonStartDate)
    }
  };
  //let now = reducerStateData.progressstatus;
  function getTenantlist(input, date) {
    setTenantList(input);
    setMultiPrint(true);
    filedownload = {};
    dispatch({ type: "setProgress", tenantid: 'PDF出力を始めます', value: 0, showProgressbar: true, isClosebtnclicked: false });
    if (input.length != 0) {
      input.map((item) => {
        let selected_date = new Date(date);
        let tenant_id = item;
        let selected_year = selected_date.getFullYear();
        let selected_mon=("0" + (selected_date.getMonth() + 1)).slice(-2);
        setDate(`${selected_year}年${selected_mon}月`)
        const dateRange = defineDateRange(selected_year, selected_mon)
        let queryObj = new QueryData();
        //fetching nas data
        let nasdata = queryObj.nasinfo_query(tenant_id, dateRange.before11Mon, dateRange.nextMonStartDate);
        let nasObj = { nas_data: nasdata, year: selected_year, month: selected_mon };
        dispatch(GetNASInfoAction(nasObj));

        let pcstatusdata = queryObj.pcstatus_query(tenant_id, dateRange.start, dateRange.nextMonStartDate);
        dispatch(GetPCStatusAction(pcstatusdata));

        //fetching fax data
        let faxdata = queryObj.fax_query(tenant_id, dateRange.before11Mon, dateRange.nextMonStartDate, dateRange.start, dateRange.nextMonStartDate)
        let faxObj = { fax_data: faxdata, year: selected_year, month: selected_mon };
        dispatch(GetFaxAction(faxObj));

        //fetching mfp data
        let mfpdata = queryObj.mfp_query(tenant_id, dateRange.before12Mon, dateRange.nextMonStartDate, dateRange.start, dateRange.nextMonStartDate)
        let mfpObj = { mfp_data: mfpdata, year: selected_year, month: selected_mon };
        dispatch(GetMFPAction(mfpObj));

        //let networkstatusdata = queryObj.pcstatus_query(boardData.TenantID);

        //Fetch application data
        let portal_query = queryObj.application_query(tenant_id, dateRange.start, dateRange.nextMonStartDate);
        dispatch(GetApplicationStatus({ portalQuery: portal_query, selectedYear: selected_year, selectedMonth: selected_mon }));

      })
    }
  }

  const resetMultiPrint = () => {
    setMultiPrint(false);
  }

  const generatedatacomponent = () => {
    return (
      <div>
        {
          tenantData.map((colname) => {
            return <Dashboardcomponent tenantid={colname} IsTenantManagement={true} filedownload={filedownload} selected_date={date} 
            multiprint ={multiPrint} multiresetPrint = {resetMultiPrint}
            />
          })
        }
      </div>
    )
  }
  return (
    <div className="main">
      <div className="hide" >

        {/* <MainComponent data={tenantData}/> */}
        <ReactBootStrap.Row id="id_wrapper" className="ml-2 mr-2" >
          <ReactBootStrap.Card className="bg-transparent col-12 pl-0 pr-0 mt-3 pt-0"  >
            {
              generatedatacomponent()
            }

          </ReactBootStrap.Card>
        </ReactBootStrap.Row>

      </div>
      <div className="nothide">
        <Tenantmanagement getTenantlist={getTenantlist} data={reducerStateData} />
        {/* <ProgressBar now={now} animated className="progressbar" max="100" min="0" /> */}
        <ProgressBarComponent  />
      </div>
    </div>
  );
}

export default LogviewerComponent;
