import React, { useEffect, useState } from 'react';
import HeaderComponent from '../reusableComponents/HeaderComponent';
import TableComponent_rc from '../reusableComponents/TableComponent_rc';
import MFPComponent from './MFPSharpGraphComponent'
import * as ReactBootStrap from 'react-bootstrap';
import MFPSummaryTableComponent from './MFPSummaryTableComponent'
const NasAgentComponent = (props) => {
    const [headerinfo, setheaderinfo] = useState({ table_header: '', daily_header: '', month_header: '', avg_data: '', total_header: '', total_data: '' });
    useEffect(() => {
        if (props.data) {
            gen_header_data();
        }
    }, [props.data]);
   
    const gen_header_data = () => {
        const start_day = (typeof (props.data.mfp_data.graph_data_day.start_date) !== "undefined" ? props.data.mfp_data.graph_data_day.start_date : "");
        const end_day = (typeof (props.data.mfp_data.graph_data_day.end_date) !== "undefined" ? props.data.mfp_data.graph_data_day.end_date : "");
        const dailyheader = `[日別]　印刷カウンター (${start_day}~ ${end_day})`;
        const month_start_day = (typeof (props.data.mfp_data.graph_data_month.start_date) !== "undefined" ? props.data.mfp_data.graph_data_month.start_date : "");
        const month_end_day = (typeof (props.data.mfp_data.graph_data_month.end_date) !== "undefined" ? props.data.mfp_data.graph_data_month.end_date : "");
        const monthheader = `[月別]　印刷カウンター (${month_start_day}~ ${month_end_day})`;
        const avg_data = (typeof (props.data.mfp_data.graph_data_ave.value_sum) !== "undefined" ? props.data.mfp_data.graph_data_ave.value_sum : "");
        const totaldata = (typeof (props.data.mfp_data.graph_data_all.value_sum) !== "undefined" ? props.data.mfp_data.graph_data_all.value_sum : "");
        const totalheader = '[累計] 印刷枚数(' + (typeof (props.data.mfp_data.graph_data_all.start_date) !== "undefined" ? props.data.mfp_data.graph_data_all.start_date : "") + ')';
        setheaderinfo({ ...headerinfo, table_header: "基本情報", daily_header: dailyheader, month_header: monthheader, avg_header: avg_data, total_header: totalheader, total_data: totaldata })
    }
    return (
        <div >
            {/* section for basic info table */}
            <TableComponent_rc data={{ header_data: headerinfo.table_header, row_data: props.data.mfp_data.mfp_basicinfo }} />
            {/* section for day wise graph */}
            < HeaderComponent data={{
                header_data: headerinfo.daily_header,
                row_data: <MFPComponent data={{
                    mfp_data: props.data.mfp_data.graph_data_day,
                    id: "id_mfp_day_graph" + props.data.index
                }} />,
                header_style:"Table-header",
                table_border_style:"nas_graph_table",
                header_border:'1px solid rgba(0, 0, 0)'
            }} />
            {/* section for monthly graph */}
            < HeaderComponent data={{
                header_data: headerinfo.month_header,
                row_data: <MFPComponent data={{
                    mfp_data: props.data.mfp_data.graph_data_month,
                    id: "id_mfp_month_graph" + props.data.index
                }} />,
                header_style:"Table-header",
                table_border_style:"nas_graph_table",
                header_border:'1px solid rgba(0, 0, 0)'
            }} />
            {/* section for pie chart table */}
            <MFPSummaryTableComponent data={{mfp_data:props.data.mfp_data,index:props.data.index,headerinfo:headerinfo,tenantid:props.data.tenantid}}/>
        </div>
    )

}
export default NasAgentComponent;