import React, { useEffect, useState } from 'react';
import HeaderComponent from './../reusableComponents/HeaderComponent';
import SummaryComponent from './SummaryBarGraphComponent';
import AssessmentTable from './PCAssessmentTable';
import PCListAttentionSummaryTable from './PCListAttentionSummaryTable';
import SecuritySoftwareInstalledDevicesGraph from './SecuritySoftwareInstalledDevicesGraph';

const PCOverallEvaluationComponent = (props) => {
    return (
        <div className="card" >
            <div className="card-body mr-5" style={{ display: "block" }}>
                <SummaryComponent data={props.stateData[0].pc_score.pc_evaluation_info[0].score_assesments} />
                < AssessmentTable data={props.stateData[0].pc_score.pc_evaluation_info} />
                        <PCListAttentionSummaryTable pc_data={{
                            header_data: "注意が必要なPC",
                            tableData: props.stateData[0].pc_score.pc_note_info
                        }} />
                        <SecuritySoftwareInstalledDevicesGraph pc_data={{
                            header_data: "セキュリティソフト導入台数",
                            tableData: props.stateData[0].pc_score.security_measures_info
                        }} />
            </div>
        </div>
    )
}
export default PCOverallEvaluationComponent;