function width(percent, width) {
    return percent * width;
}
function percentvalue(value) {
    return value.percent === 0 ? '' : value.value;
}


function addnameAndimgrow(txt, img) {
    return {
        columnGap: 10,
        columns: [
            { margin: [0, 7, 0, 0], image: img === "" ? 'transparentimg' : img.props.src, width: 15, height: 15, },
            { margin: [0, 7, 0, 2], text: txt },
        ],

    };
}
function oval_svg(status,textline_dimension) {
    return '<svg width="132" height="50" viewBox="0 0 130 60" ><g><rect x="-10" y="5" rx="30" ry="30" width="132" height="50" style="fill:' + status.background + ';stroke:' + status.border + ';stroke-width:3;opacity:0.8" /><text text-anchor="start" x="'+ textline_dimension+'" y="25" line-height= "1.2" font-size="15" fill="' + status.color + '">'
        + status.title + '</text> <text text-anchor="end" x="80" y="50" font-size="22.5"  font-weight="bold" fill="' + status.color + '">' +
        status.status +
        '</text></g></svg>';
}

