export function applicationusagetable(portaldata, index,tenantid) {

    const headerdata = ["PC版アプリケーション利用状況 (△月利用分）", "モバイル版アプリケーション利用状況 (△月利用分）"]
    var charttable = common_svgchart('portal'+tenantid + index,headerdata[index], [0,-50,0,0],'#e9ecef', [5,(index%2===0)?65:10,5,10], [8,8,8,8],'','other');
    charttable["table"]["widths"] = ["99.5%"];
    charttable["table"]["heights"] = [10,190];
    charttable["layout"] = 'noBorders';
    function add_header() {

        if (index % 2 == 0) {
            sessionStorage.pagenumber = Number(sessionStorage.pagenumber) + 1;
            page_numbers.push(sessionStorage.pagenumber);
            var header = headerTable('COCORO OFFICE アプリケーション利⽤状況');
            return header;
        }

    }
    return {
        unbreakable: true,
        pageBreak: add_pagebreak(index),
        
        stack: [
            add_header(),
           roundedTable(percent_to_point_calc("30",page_height),percent_to_point_calc("98",page_width),'#696969',5,(index%2!==0)?10:65),
           
            charttable
           ],
           //layout:'noBorders'

    };
}

