import { useEffect, useState } from 'react';
import HeaderComponent from '../reusableComponents/HeaderComponent';
import ApplicationUsageGraph from './ApplicationUsageGraph';

const ApplicationUsageComponent = (props) =>{
    const pcportal = `PC版アプリケーション利用状況 (${props.stateData[0].log_time_month}利用分）`
    const mobileportal = `モバイル版アプリケーション利用状況  (${props.stateData[0].log_time_month}利用分)`


    const [header, setheader] = useState([]);
    useEffect(() => {
        setheader([pcportal,mobileportal]);
    }, [props.stateData]);

    return(
        props.stateData.length != 0 &&
        props.stateData.map((portaldata, idx) => (
            <div key={idx} >
                < HeaderComponent data={{ 
                    header_data: header[idx],
                    row_data:<ApplicationUsageGraph stateData= {{data:portaldata, bindId : "portal" + props.tenantid+ idx,tenantid:props.tenantid}}/>,
                    header_style:"card-header",
                    header_height:"60px"}} />
            </div>
        ))
    );
}

export default ApplicationUsageComponent;