//modules to import for the component
import React, { useEffect, useState, useContext } from 'react';
import './../StylesCSS/login.css';
import { HttpService }    from './../services/httpservice';
import { Redirect }       from 'react-router';

//object for calling validation api
var service     =   new HttpService();

///<summary>
// Login component 
///</summary>
const LoginComponent = (props) => {

        //user info object which 
        const[usrinfo,          setusrinfo]           = useState({"uid":"", "pwd":""});
        const[isLoginSuccess,   setIsLoginSuccess]    = useState(false);
        const[isLoginFailed,    setIsLoginFailed]     = useState(false);
    

        //updates the userid credentilas
        const handleOnChange = (evt) =>{
            setusrinfo({...usrinfo, [evt.target.name]:evt.target.value});
            setIsLoginFailed(false);
        }
        
        //login button click event
        const onloginClick = () =>{
    
            //authenticate user info
            service.postLoginData(usrinfo).then((response)=>{
                //get the response from the server
                let authCrendentials = response.data;
    
                //validate the response
                if (200 === authCrendentials.statusCode){
                    window.sessionStorage.setItem("token",authCrendentials.body);
                    //redirect to main page if credentilas succeed
                    setIsLoginSuccess(true);  
                    //store the token in store
                    //dispatch({"type":"oauth", "token" : authCrendentials.body});
    
                } else{
                    setIsLoginFailed(true); //set failed to true if it fails
                }
    
            });//end of service call
    
        } //end of onloginclick
    
        //redirects if login succeed
        if (isLoginSuccess){
            return(<Redirect to={{ pathname: "/logvw"}} />)
        }

        return (
            <div>
                <div className="main-header">
                    <div>ログイン</div>
                </div>
                <div id="main-container" className="login-container">
                    <div className="oauthLogo">
                        <img id="id_logo" src="./images/logo/COCORO_OFFICE_レポート_logo.png" alt="" />
                    </div>
                    <div id="form-content">
                        <table className="form-table" >
                            <tbody>
                                <tr>
                                    <th scope="col"></th>
                                </tr>
                                <tr>
                                    <td>
                                        <div>
                                            <label id="lblUserName" for="UserName">ID</label>
                                        </div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <input id="UserName" style={{height:"24px",backgroundColor:"transparent"}} 
                                                name="uid" type="text"  maxlength="50" autocapitalize="off" 
                                                autocomplete="off" lang="en-US"  onChange = { (evt) => handleOnChange(evt) } />
                                        <span className="field-validation-valid" data-valmsg-for="UserName" data-valmsg-replace="true"></span>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div>
                                            <label id="lblPassword" htmlFor="Password">パスワード</label>
                                        </div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <input id="Password" name="pwd" style={{height:"24px",backgroundColor:"transparent"}} 
                                                type="password" maxlength="50" onChange = { (evt) => handleOnChange(evt) } />
                                        <span className="field-validation-valid" data-valmsg-for="Password" data-valmsg-replace="true"></span>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                        <div style={{display: 'flex',  justifyContent:'center', alignItems:'center'}}>
                            {
                                (isLoginFailed)?
                                    <div style={{display: 'flex',  justifyContent:'center', alignItems:'center', color: 'red', fontSize:9}}>
                                        <p>
                                            User Id (or) Password is wrong, Please try again...
                                        </p>
                                    </div> :
                                    <div/>
                            }
                        </div>
                        <div className="main-actions">
                            <button id="btnLogin" className="btn btn-info ml-0" type="button" value="ログイン" onClick={onloginClick}>
                                ログイン
                            </button>
                        </div>

                    </div>
                </div>


            </div>
        )
}

export default LoginComponent