import React, { useState, useEffect } from "react";
import MainComponent from "../MainComponent";
const TenantList = (props) => {
    
    return (
        <div>
            <div className="tenantinfobox">
                <div className="image_box">
                    <img src="images/ic_home_off.png" height="72" width="72" alt="Home Off image" />
                   
                    {/* <input type="checkbox" id="scales" className="disabled_checkbox" name="scales" /> */}
                </div>
                <div >
                    <p for="scales" className="fs-150 fw-bold">{props.data.item.TenantName}</p>
                    <p className="mt-1 mb-1 ml-3 tenant-border">販売店名 : ○○</p>
                    <p className="mt-1 mb-1 ml-3 tenant-border">企業名 : シャープマーケティングジャパン株式会社様</p>
                </div>
                <div>
                <input  id="scales" className=" ml-2 mb-5 mr-5" name="scales"
                        onChange={event => {
                            let checked = event.target.checked;
                            props.settenantState(
                                props.data.tenantState.map(data => {
                                    if (props.data.item.id === data.id) {
                                        data.select = checked;
                                    }
                                    return data;
                                })
                            );
                        }}
                        type="checkbox"
                        checked={props.data.item.select}
                    ></input>
                    {/* <button id="id_btn_details" type="button" className="btn btn-info ml-2 mb-2 mr-2" onClick={report_display}>
                       
                        レポート表示
                    </button> */}
                </div>
            </div>

        </div>
    )
}

export default TenantList