import React, { Component } from 'react';  
import * as ReactBootStrap from 'react-bootstrap';
import './../../src/StylesCSS/stylesheet.css';


const MainHeaderComponent = () =>{

    return ( 
       <ReactBootStrap.Container className="main-header-1-container"  fluid>
           <ReactBootStrap.Row className="color_cocorooffice_white  pt-2 pb-1 ">
               <ReactBootStrap.Col > 
                   <ReactBootStrap.Button id="sidebarCollapse" variant="light" className="pt-0 pb-0 mt-0 mb-0 pl-1 pr-1 float-right" style={{backgroundColor: "white"}} disabled>
                    <i className="fa fa-bars main-header-1-hambuger-menu">
                    </i>
                </ReactBootStrap.Button>
                </ReactBootStrap.Col>
           </ReactBootStrap.Row>
           <ReactBootStrap.Row id="id_headerpanel"  className=" color_cocorooffice_white collapse show pl-3 pr-2 pt-2">
            <ReactBootStrap.Col className="mb-0 mt-0" >
            <ReactBootStrap.Button id="id_link_dashboard"  variant ="transparent" className="header_button header_button_selected rounded-0 btn pt-1 pb-1" aria-pressed="true" >
                <i name="home_icon" className="fa fa-home nav-icons-on align-middle" height={24} />
             &nbsp;  ホームページ
			</ReactBootStrap.Button>
            </ReactBootStrap.Col>
            <ReactBootStrap.Col className="mb-0 mt-0 ml-0 mr-0 pt-0 pl-0 pr-2  pb-0" >
                <ReactBootStrap.Button id="id_title" variant ="light" className="pt-0 pb-0 pl-1 pr-1 ml-2 mr-0   float-right" style={{backgroundColor:"white", borderColor:"white"}} disabled>
                <i className="fa fa-sync-alt nav-icons-on align-middle" height={24} /> 
                </ReactBootStrap.Button>
            </ReactBootStrap.Col>
           </ReactBootStrap.Row>
       </ReactBootStrap.Container>
    )
}
export default MainHeaderComponent
