import * as ReactBootStrap from 'react-bootstrap';
import './../../StylesCSS/cssStyle.css';
const PCAssessmentTableComponenet = (props) => {
    return ( 
        <div>
            <div className="card-body mt-3" style={{background:"#DCDCDC",width: "100%",height:"14.8rem"}}>
                <ReactBootStrap.Table borderless id="id_assessment_table" bgcolor="white" style={{width:"15%",height:"12rem"}}>
                    <tbody> 
                        <tr>
                            <td align="center">
                                <span style={{fontSize: '2rem'}}>総合評価</span>
                            </td>
                        </tr>
                        <tr>
                            <td align="center" >
                                <span style={{color:"#008EA6", fontSize: '2.1875rem'}}>{props.data.assessment_points}</span>
                            </td>
                        </tr>
                        <tr style={{height:"50"}}>
                            <td align="center" >
                                <span>平均点</span>
                                <span style={{color: "#008EA6"}}> :{props.data.assessment_score} 点</span></td>
                        </tr>
                    </tbody>
                </ReactBootStrap.Table>
                <textarea className="ml-3" name="example" rows="4" cols="40" style={{resize:"none", width:"80%", fontSize: '12pt',background:'white'}} defaultValue={props.data.textarea_data}  disabled id="id_assessment_textarea">
                    
                </textarea>
            </div>
            {/* <div style={{textAlign: 'right', fontSize: '120%'}}>
                <span>詳細は個別PCレポートを参照ください</span>
            </div> */}
        </div>
    );
}
export default PCAssessmentTableComponenet;


