export const NetworkParseData = (pc_response, mfp_response, nas_response) => {
  var nasdata = nas_response.aggregations.nas_list.buckets;

  // Get NAS IP list
  const nasInfoList = (() => {
    return nasdata.map((prop) => {
      return {
        nas_ipaddress: prop.latest_data.hits.hits[0]._source.nas_ipaddress,
      };
    });
  })();

  // Get mfp IP list
  const mfpIpAddressList = (() => {
    if (typeof mfp_response === "") return [];
    return mfp_response.map((prop) => {
      return { mfp_ipaddress: prop.latest_data.hits.hits[0]._source.address };
    });
  })();

  // Get PCagent IP list
  const pcList = (() => {
    if (nasInfoList.length >= 1)
      return extractPcListWithNas(pc_response, nasInfoList);
    return extractPcListWithoutNas(pc_response);
  })();

  //set name and ip for device
  let nasNetworkInfo = {
    ipaddress: nasInfoList.map((nasinfo) => {
      return { ipaddress: nasinfo.nas_ipaddress, pcName: "" };
    }),
  };

  let mfpNetworkInfo = {
    ipaddress: mfpIpAddressList.map((mfpinfo) => {
      return { ipaddress: mfpinfo.mfp_ipaddress, pcName: "" };
    }),
  };

  let pcInternalNetworkInfo = {
    ipaddress: eliminateDuplication(pcList[0], "pcName").map((pcinfo) => {
      return { ipaddress: pcinfo.ip, pcName: pcinfo.pcName };
    }),
  };

  let pcExternalNetworkInfo = {
    ipaddress: eliminateDuplication(pcList[1], "pcName").map((pcinfo) => {
      return { ipaddress: pcinfo.ip, pcName: pcinfo.pcName };
    }),
  };

  return {
    nas_list: nasNetworkInfo,
    mfp_list: mfpNetworkInfo,
    pc_list_internal: pcInternalNetworkInfo,
    pc_list_external: pcExternalNetworkInfo,
  };
};

const ipFrom4To3 = (ip) => {
  const ipSplit = ip.split(".");
  return ipSplit[0] + "." + ipSplit[1] + "." + ipSplit[2];
};

const isInternalIP = (standardIPList, inputIP) => {
  const inputIP3 = ipFrom4To3(inputIP);
  return standardIPList.includes(inputIP3);
};

const createPcnameIPList = (pcInfoList) => {
  const pcnameIpList = [];
  for (let idx in pcInfoList) {
    let ipList = pcInfoList[idx].ip_list;
    for (let i in ipList) {
      let ipItems = ipList[i];
      pcnameIpList.push({
        pcName: pcInfoList[idx].computer_name,
        macaddress: pcInfoList[idx].macaddress,
        ip: ipItems.key,
        cnt: ipItems.doc_count,
      });
    }
  }

  return pcnameIpList;
};

const extractPcListWithNas = (pcInfoList, nas_info_list) => {
  //Set the internal as the IP address of the NAS
  //Get 3-digit IP
  const nasIpAddress3List = nas_info_list.map((prop) =>
    ipFrom4To3(prop.nas_ipaddress)
  );

  //Create IP list [{pcName: '4HDD-PC1', ip: '192.168.1.5', cnt: 27} ...]
  const pcnameIpList = createPcnameIPList(pcInfoList);

  return pcnameIpList
    .map((ele_) => {
      if (isInternalIP(nasIpAddress3List, ele_.ip))
        return { internal: ele_, external: null };
      return { internal: null, external: ele_ };
    })
    .reduce(
      (accum, next) => {
        if (next.internal) accum[0].push(next.internal);
        if (next.external) accum[1].push(next.external);
        return accum;
      },
      [[], []]
    );
};

const fetchMostIP = (pcnameIpList) => {
  // {ip :cnt,...}
  const IPcntList = pcnameIpList
    .map((prop) => {
      return {
        ip: ipFrom4To3(prop.ip),
        cnt: prop.cnt,
      };
    })
    .reduce((accum, next) => {
      if (!accum.hasOwnProperty(next.ip)) {
        accum[next.ip] = next.cnt;
        return accum;
      } else {
        accum[next.ip] += next.cnt;
        return accum;
      }
    }, {});

  let offset = 0;
  let mostIP = "";
  for (let key in IPcntList) {
    if (IPcntList[key] > offset) {
      offset = IPcntList[key];
      mostIP = key;
    }
  }

  return [mostIP];
};

const extractPcListWithoutNas = (pcInfoList) => {
  //Create IP list [{pcName: '4HDD-PC1', ip: '192.168.1.5', cnt: 27} ...]
  const pcnameIpList = createPcnameIPList(pcInfoList);
  //Get most IPs ["xx.xx.xx"]
  const standardIPList = fetchMostIP(pcnameIpList);
  return pcnameIpList
    .map((ele_) => {
      if (isInternalIP(standardIPList, ele_.ip))
        return { internal: ele_, external: null };
      return { internal: null, external: ele_ };
    })
    .reduce(
      (accum, next) => {
        if (next.internal) accum[0].push(next.internal);
        if (next.external) accum[1].push(next.external);
        return accum;
      },
      [[], []]
    );
};

const eliminateDuplication = (_array, _key) => {
  if (_array.length === 0) return _array;
  return _array.reduce((arr, v) => {
    if (!arr.some((obj) => obj[_key] === v[_key])) {
      arr.push(v);
    }
    return arr;
  }, []);
};
