import React, { useEffect, useState } from 'react';
import HeaderComponent from './../reusableComponents/HeaderComponent';
import NasInfoComponent from './NasInfoComponent';
import NasAgentTable from '../NASAgentComponent/NasAgentTable';
import NASTableGraphComponent from './NASTableGraphComponent';
const NasAgentComponent = (props) => {
    const [headerinfo, setheaderinfo] = useState({header:'', basicheader:'', storageheader:''});
useEffect(()=>{
        if(props.nas_info)
        {   const NASSTARTDAY=typeof(props.nas_info.start_date) !== "undefined" ? props.nas_info.start_date : "";
            const NASENDDAY=typeof(props.nas_info.end_date) !== "undefined" ? props.nas_info.end_date : "";
            const header_data=`[月別]　ストレージ容量の遷移 (${NASSTARTDAY+"~"+NASENDDAY})`
            const basic_header="基本情報";
            const NASSTRAGELOGTIME=typeof(props.nas_info.log_time) !== "undefined" ? props.nas_info.log_time : "";
            const storage_header=`ストレージ容量 ${NASSTRAGELOGTIME}時点`
            setheaderinfo({...headerinfo, header:header_data, basicheader:basic_header, storageheader:storage_header})
        }
    },[props.nas_info]);
return(
    <div >
     <NasAgentTable data={props.nas_info.nas_basic_info}/>
     < HeaderComponent data= {{header_data: headerinfo.header,row_data:<NasInfoComponent data={props.nas_info.graph_data} id={props.id}/>,header_style:"Table-header",table_border_style:"nas_graph_table",header_border:'1px solid rgba(0, 0, 0)'}}/>
     <NASTableGraphComponent data={{header_data: headerinfo.storageheader,row_data:props.nas_info.nas_strage_status}}/>
    </div>
    )

}
export default NasAgentComponent;