import bikkuri from './../images/bikkuri.png';

export const SecurityParseData = (_pcInfoScoreList, _toSummary) => {
    // Security table data
  const securitySoftInfo = _pcInfoScoreList.map(pcInfoScore => {

    const pcInfo = pcInfoScore.pcInfo;
    return {
      "pcname": pcInfo.computer_name,
      "windows_version": pcInfo.os_platform,
      "securitysoft": extractSecSofName(pcInfo),
    };
});

let statusIcon = <img src={bikkuri}  style={{width:"25px",height:"25px"}}/>;

const noSecSoftPCList = securitySoftInfo.filter((prop) => {
  return prop.securitysoft === "未導入";
}).map(pcItem => ({...pcItem, color: '#FF6919',icon:statusIcon}));

const defaultSecSoftPCList = securitySoftInfo.filter((prop) => { 
  return prop.securitysoft === "Windows Defender";
}).map(pcItem => ({...pcItem, color: '#B1E7F4',icon:''}));

const secSoftPCList = securitySoftInfo.filter((prop) =>{
  return (!(prop.securitysoft === "Windows Defender" || prop.securitysoft === "未導入"));
}).map(pcItem => ({...pcItem, color: '#7ED7ED',icon:''}));

return {
  security_soft_info: [...noSecSoftPCList, ...defaultSecSoftPCList, ...secSoftPCList],
  securityMeasuresInfo : _toSummary.security_measures_info
}
};

const extractSecSofName = (pcInfo) => {
    // get security software list (True)
    let secOnlist = pcInfo.anti_virus_software.filter((prop) => prop.enabled);
    // Remove more than one Windows Defender
    if (secOnlist.length >= 2) {
      secOnlist = secOnlist.filter((prop) => prop !== "Windows Defender");
    }
    return (() => {
      if (secOnlist.length === 0) return "未導入";
      return secOnlist[0].name;
    })();
};



