import React, { useEffect, useState } from 'react';
import * as ReactBootStrap from 'react-bootstrap';
import './../../StylesCSS/cssStyle.css';
const SecuritySoftwareInstalledDevices = (props) => {   
    const softwareIntroduction = props.pc_data.tableData.software_introduction.percent + "%";
    const sosSoftware = props.pc_data.tableData.os_software.percent + "%";
    const notIntroduced = props.pc_data.tableData.not_introduced.percent + "%";

    return (
        <ReactBootStrap.Table id="id_security_counterplan_table" bordered>
            <thead>
                        <tr >
                            <th colSpan='5' className="security-header" style={{ fontSize:"1.25rem",fontWeight:"normal",verticalAlign:"middle",textIndent:"1rem"}} >
                                {props.pc_data.header_data}
                            </th>
                        </tr>
                </thead>
            <tbody>
                <tr>
                    <td rowSpan="3" align="center" width="80%" height="100%" style={{ border: "None" }}>
                        <span style={{ display: "flex"}} className="ml-5">
                        { softwareIntroduction!="0%" &&
                            <span className="PCAgentSecuritybar" style={{ background: "#7ED7ED", color:"#000000", marginLeft:"0.60rem", width: softwareIntroduction }}>
                                {props.pc_data.tableData.software_introduction.value}</span>
                        }
                        { sosSoftware!="0%" &&
                            <span className="PCAgentSecuritybar" style={{ background: "#B1E7F4", color:"#000000", marginLeft:"0.60rem", width: sosSoftware }}>
                                {props.pc_data.tableData.os_software.value}</span>
                        }
                            <span className="PCAgentSecuritybar" style={{ background: "#FF6919", marginLeft:"0.60rem", width: notIntroduced }}>
                                {props.pc_data.tableData.not_introduced.value}</span>
                        </span>
                    </td>                    
                    <td style={{ borderLeft: "None" , borderTop: "None",borderBottom:"None"}}> <span  colSpan="2" align="right">
                        <div className="mt-1 ml-5" style={{ display: "flex" }}>
                            <span className="bar" style={{ background:"#7ED7ED"}}></span>
                            <div className="ml-1">
                                <span>ソフト導入</span>
                            </div>
                        </div>
                    </span>
                    </td>
                </tr>
                <tr>
                    <td style={{ borderLeft: "None" , borderTop: "None",borderBottom:"None"}}> <span colSpan="2" align="right" >
                        <div className="mt-1 ml-5" style={{ display: "flex" }}>
                            <span className="bar" style={{ background: "#B1E7F4" }}></span>
                            <div className="ml-1">
                                <span>OS標準ソフト</span>
                            </div>
                        </div>
                    </span>
                    </td>
                </tr>
                <tr>
                    <td style={{ borderLeft: "None" , borderTop: "None",borderBottom:"None" }}> <span colSpan="2" align="right" >
                        <div className="mt-1 ml-5" style={{ display: "flex" }}>
                            <span className="bar" style={{  background: "#FF6919" }}></span>
                            <div className="ml-1">
                                <span>未導入</span>
                            </div>
                        </div>
                    </span>
                    </td>
                </tr>
            </tbody>
        </ReactBootStrap.Table>                 
    );
}
export default SecuritySoftwareInstalledDevices;