import React, { useEffect, useState } from 'react';
import HeaderComponent from '../reusableComponents/HeaderComponent';
import MFPComponent from './MFPSharpComponent'
const MFPMainComponent = (props) => {
    return (
        props.stateData.length != 0 &&
        props.stateData.map((colname, idx) => (
            <div key={idx} >
                < HeaderComponent data={{ 
                    header_data: (typeof (props.stateData[idx].mfp_name) !== "undefined" ? `複合機 : ${ props.stateData[idx].mfp_name } の情報`: ""),
                    row_data:<MFPComponent data={{
                        mfp_data:props.stateData[idx],
                        index:props.tenantid+idx,
                        tenantid:props.tenantid
                    }}/>,
                    header_style:"card-header",
                    header_height:"60px"}} />
            </div>
        ))
    );

}
export default MFPMainComponent;