import React, { useEffect, useState } from 'react';
import SecurityUsageStatusGraph from './SecurityUsageStatusGraph'
import SecurityUsageListTable from './SecurityUsageListTable'

const SecurityUsageStatusComponent = (props) => {
    return (
        <div className="card" >
            <div className="card-body mr-5" style={{ display: "block" }}>
                <SecurityUsageStatusGraph data={props.stateData[0].security_status.securityMeasuresInfo} />                
            </div>
            <div className="card-body mr-5" style={{ display: "block" }}>
                <SecurityUsageListTable data={props.stateData[0].security_status.security_soft_info} />                
            </div>
        </div>
    )
}
export default SecurityUsageStatusComponent;