//namespaces 
import React, { useEffect, useState } from 'react';
import c3 from 'c3';
import * as d3 from 'd3';
import './../../StylesCSS/style.css'

///########################################
///MFP info component
///#######################################
let displayText = false;
const MFPGraphComponent = (props) => {

    //Invoke draw chart only if the data is modified
    useEffect(() => {
        if (props.data) {
            drawChart();
            
        }
    }, [props.data]);

    //api which reads the nas_data and creates a chart
    const drawChart = () => {
        const newArray = props.data.mfp_data.value[1].filter(n => n !== props.data.mfp_data.value[1][0]);
        const newArray2 = props.data.mfp_data.value[2].filter(n => n !== props.data.mfp_data.value[2][0]);
        if (newArray.every(v => v === 0 || v === null) && newArray2.every(v => v === 0 || v === null)) {
            displayText = true;
        }
        else {
        let chartc = c3.generate({
            bindto: "#"+props.data.id,
            size: {
                width: 1050,
                height: 350
            },
            data: {
                x: 'date',
                columns:props.data.mfp_data.value,
                type: 'bar',
                groups: [
                    ['カラー', 'モノクロ']
                ],
                colors: {
                    'カラー': '#B1E7F4',
                    'モノクロ': '#dcdcdc'
                },
                order: 'null'
            },
            grid: {
                y: {
                    show: true
                }
            }, 
            axis: {
                x: {
                    type: "category",
                    tick: {
                        rotate: 0,
                        multiline: true,
                        fit: true,
                        outer:false
                    }
                },
                y: {
                   // Removed y axis label as it is not in current implementation
                    // label: {
                    //     text: '枚数',
                    //     position: 'outer-top',

                    // },
                    tick: {
                        outer:false,
                        format: function(d) {
                            return d % 1 === 0 ? d.toLocaleString() : "";
                        }
                    }
                    
                }
            },
           
            legend: {
                position:'bottom',
                show:true
            },
            onrendered: function () { 
                try {
                    d3.selectAll(".c3-axis-x .tick text")
                    .style("font-weight", "normal")
                    .style("font-size", "10px");
                    
                d3.selectAll(".c3-axis-y .tick text")
                    .style("font-weight", "normal")
                    .style("font-size", "10px");

                d3.selectAll(".c3 .c3-grid line")
                    .style("stroke", "darkgrey")

            const legendElementColor = d3.select( "#"+props.data.id).select("svg").select(".c3-legend-item.c3-legend-item-カラー");
                        legendElementColor.select("text").attr("x", 510);
                        legendElementColor.select("text").attr("y", 15);
                        legendElementColor.select("line").attr("x1", 480);
                        legendElementColor.select("line").attr("x2", 500);
                        legendElementColor.select("line").attr("y1", 13);
                        legendElementColor.select("line").attr("y2", 13);
                        legendElementColor.select("line").attr("stroke-width", 18);
            const legendElementMono = d3.select("#"+props.data.id).select("svg").select(".c3-legend-item.c3-legend-item-モノクロ");
                        legendElementMono.select("text").attr("x", 590);
                        legendElementMono.select("text").attr("y", 16);
                        legendElementMono.select("line").attr("x1", 560);
                        legendElementMono.select("line").attr("x2", 580);
                        legendElementMono.select("line").attr("stroke-width", 18);
                        legendElementMono.select("line").attr("y1", 13);
                        legendElementMono.select("line").attr("y2", 13);
                    
                } catch (error) {
                    
                }      
               
            }
            
        });
         }
    }
    return (
        <div style={{marginLeft:"10%"}}>
           
            <div id={props.data.id}>
                 <div style={{ height: '13rem', fontSize: '30px', display: displayText ? "" : "None" }}>
                    <div style={{ margin: 'auto', paddingTop: '80px', textAlign: 'center' }}>
                        {'この期間には印刷が行われておりません。'}
                    </div>
                </div>
            </div>
        </div>
    );

}

export default MFPGraphComponent;