import React, { useState, useEffect } from "react";
import TenantList from "./TenantList";
import DatePicker, { registerLocale } from "react-datepicker";
import * as ReactBootStrap from 'react-bootstrap';
import ja from "date-fns/locale/ja"; // the locale you want
import '../../StylesCSS/stylesheet.css'
import {useDispatch} from 'react-redux';
registerLocale("ja", ja); // register it with the name you want

function Tenantmanagement(props) {
  const [tenantState, settenantState] = useState([]);
  const [month_data, setBoardData] = useState('');
  let dispatch = useDispatch();

  useEffect(() => {
    //let response = serv.postData(query).then(response => {
    //let tenantState = response.data.aggregations.tenant.buckets.map(keyItem => keyItem.key);
    if (props.data) {
      settenantState(
        props.data.map((colname, idx) => {
          return {
            select: false,
            id: idx,
            TenantName: colname
          };
        })
      );
    }



  }, [props.data]);
  function button_click() {
    if (month_data == '') return;
    let lst_tenantids = [];
    let result = tenantState.filter(i => i.select === true);
    result.map((item) => lst_tenantids.push(item.TenantName));
    dispatch({"type":"deletetenantids"});
    props.getTenantlist(lst_tenantids, month_data);
  }
  const selectAll = () => {
    settenantState(
      tenantState.map(d => {
        d.select = true;
        return d;
      })
    );
  }
  const deselectAll = () => {
    settenantState(
      tenantState.map(d => {
        d.select = false;
        return d;
      })
    );
  }
  return (

    <div  >
      {tenantState.length !== 0 && (
        <div>
          <div className="card ml-3  mr-3 " >
            {
              tenantState.map((colname, idx) => {
                return <TenantList data={{ item: colname, tenantState: tenantState }} settenantState={settenantState} key={idx} />
              })
            }


          </div>
          <div style={{ background: "black", height: "8%" }} className="footer ml-3 mr-3" >
            <button id="id_btn_details" style={{ width: "10%" }} type="button" className="btn btn-primary ml-2 mb-2 mr-2 mt-2" onClick={selectAll}>

              Select all
      </button>
            <button id="id_btn_details" type="button" style={{ width: "10%" }} className="btn btn-danger ml-2 mb-2 mr-2 mt-2" onClick={deselectAll} >

              Deselect all
      </button>


            <div style={{ display: "flex", float: "right" }} className="mt-2">
              <span className="mt2" style={{ color: "white" }}>集計期間 : </span>
              <div className="ml-2">
                {/* <input readonly type="text" className="form-control" id="id_collect_data" /> */}
                <ReactBootStrap.InputGroup className="customdatepicker">
                  <DatePicker className="form-control" selected={month_data}
                    onChange={
                      (evt) =>
                        setBoardData(evt)
                    }
                    showMonthYearPicker
                    dateFormat="yyyy-MM"
                    locale="ja"
                    openToDate={month_data ? '' : new Date((new Date().getFullYear()), (new Date().getMonth()) - 1, (new Date()).getDate())}
                  >
                  </DatePicker>
                </ReactBootStrap.InputGroup>
              </div>
              <button id="id_btn_allpdf" type="button" className="btn btn-primary ml-1 mb-1 mr-1" onClick={button_click}>PDF一括出力</button>
            </div>

          </div>
        </div>
      )}
    </div>

  );
}

export default Tenantmanagement;
