///<summary>
// api returns fsecure pc info parsed data
///</summary>
function fsecurePcInfo(response) {

    //condition validates for valid data
    if ((undefined ===  response.hits)                || (undefined === response.hits.total) || 
        (("undefined" !== response.hits.total.value)  && (0 === response.hits.total.value))) {
        
        return "NoHitError";; //returns default fsecurepcinfo data
    }
    else {
        
        return response.aggregations.pc_list.buckets.map(bucket => bucket.latest_data.hits.hits[0]._source);
    }
}

export { fsecurePcInfo };