import React, { useEffect, useState } from 'react';
import * as ReactBootStrap from 'react-bootstrap';
const HeaderComponent = (props) => {
    return (
        <div >
            <ReactBootStrap.Table bordered id="pc-table-id" className={props.data.table_border_style}>
                <thead >
                    
                        <tr >
                            <th colSpan='4' className={props.data.header_style} style={{fontSize:"1.45rem", fontWeight:"normal",height:props.data.header_height, borderBottom:props.data.header_border}}>
                                {props.data.header_data}
                            </th>
                        </tr>
                    
                </thead>
                <tbody>
                    <tr>
                        
                        <td >
                            {props.data.row_data}
                        </td>
                    </tr>
                </tbody>
            </ReactBootStrap.Table>
        </div>
    );
}
export default HeaderComponent;