import React, { useEffect, useState } from 'react';
import {QueryData} from './queryComponent/nasquery';
import GetNASInfoAction from './../Actions/nasinfoAction';
import GetPCStatusAction from './../Actions/PCStatusAction';
import GetFaxAction from './../Actions/faxAction';
import GetMFPAction from './../Actions/mfpaction'
import GetFSecureAction from './../Actions/FSecureAction';
import GetNetworkAction from './../Actions/NetworkAction'
import GetApplicationStatus from './../Actions/ApplicationUsageAction'
import DropDownComponent from './reusableComponents/DropdownComponent';
import DatePicker, { registerLocale }  from "react-datepicker";
import { useDispatch } from 'react-redux';

import * as ReactBootStrap from 'react-bootstrap';
import ja from "date-fns/locale/ja"; // the locale you want
registerLocale("ja", ja); // register it with the name you want

const UpdateComponent=(props)=>{

    const initialState = { lst_tenant_id: [], TenantID: '', month_data: '', is_auto_enabled: false, auto_update: false, range: '', selectedYear: '', ES_query_result: {} };
    const [boardData, setBoardData] = useState(initialState);

    let dispatch = useDispatch();

    useEffect(() => {

        if (props.stateData) {

            setBoardData({ ...boardData, lst_tenant_id: props.stateData, TenantID: props.stateData[0] });
        }

    }, [props.stateData])

    const getSelectedTenantID = (evt) => {
        setBoardData({ ...boardData, TenantID: evt });
    }
    const dateToStr = (date) => {
        // converting date to string
        const year = String(date.getFullYear());
        const mon = date.getMonth() + 1;
        const mon0Complement = (() => {
            if (mon >= 10) return String(mon);
            return "0" + String(mon)
        })();
        const day = date.getDate();
        const day0Complement = (() => {
            if (day >= 10) return String(day);
            return "0" + String(day)
        })();
        return `${year}-${mon0Complement}-${day0Complement}T00:00:00Z`
    };
    const defineDateRange = (selectedYear, selectedMon) => {
        //starting date of selected month
        const startDate = new Date(Number(selectedYear), Number(selectedMon) - 1, 1);
        //end date of selected month
        const endDate = new Date(Number(selectedYear), Number(selectedMon), 0);
        // 11 month before date 
        const dateBefore11Mon = new Date(Number(selectedYear), Number(selectedMon) - 12, 1);
        // 12 month before date
        const dateBefore12Mon = new Date(Number(selectedYear), Number(selectedMon) - 13, 1);
        // 翌月初めの日付を取得
        const nextMonStartDate = new Date(Number(selectedYear), Number(selectedMon), 1);
        // 翌月末の日付を取得
        const nextMonEndDate = new Date(Number(selectedYear), Number(selectedMon) + 1, 0);

        return {
            start: dateToStr(startDate),
            end: dateToStr(endDate),
            endDateBefore1Mon: dateToStr(endDate),
            before11Mon: dateToStr(dateBefore11Mon),
            before12Mon: dateToStr(dateBefore12Mon),
            nextMonStartDate:dateToStr(nextMonStartDate),
            nextMonEndDate: dateToStr(nextMonEndDate),
        }
    };
    const updateClickEvent = () => {

        if (boardData.month_data == '') return;

        let selected_date = new Date(boardData.month_data);
        let selected_year = selected_date.getFullYear();
        let selected_mon=("0" + (selected_date.getMonth() + 1)).slice(-2);
        const dateRange = defineDateRange(selected_year, selected_mon)

        let queryObj = new QueryData();

        //clear the response @ the store
        dispatch({"type" : "clear", "tenantid" : boardData.TenantID});

        //fetching nas data
        let nasdata = queryObj.nasinfo_query(boardData.TenantID, dateRange.before11Mon, dateRange.nextMonStartDate);
        let nasObj = { nas_data: nasdata, year:selected_year, month: selected_mon};
        dispatch(GetNASInfoAction(nasObj));

        let pcstatusdata = queryObj.pcstatus_query(boardData.TenantID, dateRange.start, dateRange.nextMonStartDate);
        dispatch(GetPCStatusAction(pcstatusdata));

        //fetching fax data
        let faxdata = queryObj.fax_query(boardData.TenantID, dateRange.before11Mon, dateRange.nextMonStartDate, dateRange.start, dateRange.nextMonStartDate)
        let faxObj = { fax_data: faxdata, year:selected_year, month: selected_mon };
        dispatch(GetFaxAction(faxObj));

        //fetching mfp data
        let mfpdata = queryObj.mfp_query(boardData.TenantID, dateRange.before12Mon, dateRange.nextMonStartDate, dateRange.start, dateRange.nextMonStartDate)
        let mfpObj = { mfp_data: mfpdata, year: selected_year, month: selected_mon };
        dispatch(GetMFPAction(mfpObj));

        //let networkstatusdata = queryObj.pcstatus_query(boardData.TenantID);

        //Fetch application data
        let portal_query = queryObj.application_query(boardData.TenantID, dateRange.start, dateRange.nextMonStartDate);
        dispatch(GetApplicationStatus({ portalQuery: portal_query, selectedYear: selected_year, selectedMonth: selected_mon }));

        //Fetch Fsecure data
        let fsecure_query = queryObj.fsecure_Query({tenantID:boardData.TenantID, dateRangeStart:dateRange.start, dateRangeNextMonStart:dateRange.nextMonStartDate,dateRangeNextMonEnd:dateRange.nextMonEndDate, })
        dispatch(GetFSecureAction({fsecureQuery : fsecure_query,selectedYear: selected_year, selectedMonth: selected_mon }))
        
        //display all tabs
        props.showNavBar();

        props.tenantids(boardData.TenantID);
                }
    return (
        <div>
            {boardData.lst_tenant_id.length !== 0 &&
                (
                    <ReactBootStrap.Card.Header className="bg-light pt-0 pb-5">
                        <br />
                        <ReactBootStrap.Card.Text>ホームページ</ReactBootStrap.Card.Text >
                        <ReactBootStrap.Card.Text>レポート設定: </ReactBootStrap.Card.Text >
                        <ReactBootStrap.Table borderless className="setting-table">
                            <tbody>
                                <tr>
                                    <td className="table-td">
                                        <ReactBootStrap.Card.Text className="mb-0">集計期間: </ReactBootStrap.Card.Text >
                                    </td>
                                    <td width="35%" className="pt-0 pb-0">
                                        <ReactBootStrap.InputGroup className="customDatePickerWidth">
                                            <DatePicker className="form-control" selected={boardData.month_data}
                                                onChange={(evt) => setBoardData({ ...boardData, month_data: evt })}
                                                showMonthYearPicker
                                                dateFormat="yyyy-MM"
                                                locale="ja"
                                                openToDate={boardData.month_data ? '' : new Date((new Date().getFullYear()), (new Date().getMonth()) - 1, (new Date()).getDate())}
                                            >
                                            </DatePicker>
                                        </ReactBootStrap.InputGroup>
                                    </td>
                                </tr>
                                <tr>
                                    <td className="tenant-text">
                                        <ReactBootStrap.Card.Text className="mb-0">テナントID: </ReactBootStrap.Card.Text >
                                    </td>
                                    <td>

                                        <ReactBootStrap.Dropdown className="form-control pr-0 pl-0 pt-0 pb-0 ">
                                            <ReactBootStrap.Dropdown.Toggle variant="white" id="dropdown-basic" className="form-control dropdown-button">
                                                {boardData.TenantID}
                                            </ReactBootStrap.Dropdown.Toggle>
                                            <ReactBootStrap.Dropdown.Menu className="dropdown-menu">
                                                {
                                                    boardData.lst_tenant_id.map((value, idx) => (
                                                        <ReactBootStrap.Dropdown.Item key={idx} value={value} onSelect={() => getSelectedTenantID(value)}>{value}</ReactBootStrap.Dropdown.Item>
                                                    ))
                                                }
                                            </ReactBootStrap.Dropdown.Menu>
                                        </ReactBootStrap.Dropdown>
                                    </td>
                                </tr>
                            </tbody>
                        </ReactBootStrap.Table>
                        <ReactBootStrap.Button id="id_btn_allprintPDF" className="btn btn-info ml-3 float-right" disabled >一括PDF出力</ReactBootStrap.Button>
                        <ReactBootStrap.Button id="id_btn_printPDF" className="btn btn-info ml-3 float-right" onClick={props.printDocument} >PDF出力</ReactBootStrap.Button>
                        <ReactBootStrap.Button id="id_btn_update" className="btn btn-info ml-0 float-right" onClick={updateClickEvent}>更新</ReactBootStrap.Button>
                    </ReactBootStrap.Card.Header>
                )
            }
        </div>

    )
}

export default UpdateComponent;