//import modules 
import bikkuri from './../images/bikkuri.png';

///<summary>
// api parse fsecure data based on pcinfo and fsecure response
///</summary>
export const fsecureparsedata = (agentData) => {

    //validates for pc_info and fsecure data exists
    if (!agentData.hasOwnProperty("pc_info") || !agentData.hasOwnProperty("fsecure_data")) {
        return "NoHitError";
    }
    
    //if data exists, condition validate whether pc_info and fsecure_data is valid or not
    if( "NoHitError" ===  agentData.pc_info             || "NoHitError" === agentData.fsecure_data.data[0] ||
        "NoHitError" === agentData.fsecure_data.data[1] || undefined === agentData.fsecure_data.data) {

        return "NoHitError"; //return default result
    }

    //initialize pcinfo and fsecure data
    var _pcInfoList                 = agentData.pc_info
    var _fSecurePcInfo              = agentData.fsecure_data.data[1];
    var _fSecureDailyContamination  = agentData.fsecure_data.data[0];
    var _selectedYear               = agentData.fsecure_data.selectedyear;
    var _selectedMon                = agentData.fsecure_data.selectedmonth;
    
    //initilaize date range variables
    const lastDateInMon             = new Date(Number(_selectedYear), Number(_selectedMon), 0);
    const totalDays                 = lastDateInMon.getDate();
    const endDate                   = `${_selectedYear}年${_selectedMon}月${totalDays}日`;

    // 更新判定の基準になる日
    const offsetDate                = new Date(Number(_selectedYear), Number(_selectedMon), 1);
    // 基準日から14日前
    const offsetDate14DayAgo        = offsetDate.setDate(offsetDate.getDate() - 14);

    // 横軸[01日,...,最終日]を作成
    const xLim                              = _createDailyXLim(_selectedYear, _selectedMon, totalDays);

    // 縦軸[カウント,...]を作成
    const yLim                              = createYLim(_fSecureDailyContamination.slice(0, _fSecureDailyContamination.length),
                                                        totalDays, _selectedYear,_selectedMon);
    const _fSecurePCInfo                    = createPCTable(_pcInfoList, _fSecurePcInfo, offsetDate14DayAgo);
    const fSecureComputerProtectionInfo     = createFSecureComputerProtectionInfo( _fSecurePCInfo );
    const f_secure_malware_info             = createFSecureMalwareInfo(_fSecurePCInfo);

    return {
        f_secure_block_info : {
            graph_data      : [ ["date", ...xLim], ["件数", ...yLim], ],
            log_time        : endDate,
        },
        f_secure_pc_info                    : _fSecurePCInfo,
        f_secure_computer_protection_info   : fSecureComputerProtectionInfo,
        f_secure_malware_info               : f_secure_malware_info,

    }; //returns computed fsecure data

}
   

///<summary>
// api computes YLim data 
///</summary>
const createYLim = (dailyBuckets, numOfDay, _selectedYear, _selectedMon) => {  
        
    //initialize default
    const setMon = _selectedMon;
    const yLim = [];

    //loop calculates list of yLim data
    for (let index = 1; index <= numOfDay; index++) {

        //initialize setDate to filter
        let setDate     = (() => {
            if (index < 10) return `${_selectedYear}-${setMon}-0${index}`;
            return `${_selectedYear}-${setMon}-${index}`;
        })();

        //calculate machData based on setDate
        let machData    = dailyBuckets.filter((prop) => prop.date === setDate);

        //calculate yData
        let yData       = (() => {
            if (machData.length) return machData[0].contamination;
            return null;
        })();

        yLim.push(yData);
    }

    return yLim; //returns list of yLim data
}

///<summary>
// api computes DailyXLim data 
///</summary>
const _createDailyXLim = (selectedYear, selectedMon, numOfDay)  => {

    //initialize default values
    const dayOfWeekStr = ["(日)", "(月)", "(火)", "(水)", "(木)", "(金)", "(土)"];
    const xLim = [];

    //loop calcualtes xLim Data
    for (let index = 1; index <= numOfDay; index++) {

        //initialize day date range
        let today       = new Date(Number(selectedYear), Number(selectedMon) - 1, index);

        //calcuate day of week based on day date range
        let dayOfWeek   = dayOfWeekStr[today.getDay()];

        //calculate date based on calcualted day and dayofweek
        let date        = (() => {
            if (index < 10) return `0${index}日${dayOfWeek}`;
            return `${index}日${dayOfWeek}`;
        })();

        xLim.push(date); //appends to the list
    }

    return xLim; //returns list of xLim data
};

///<summary>
// api computes findWindowsVersion 
///</summary>
const findWindowsVersion = (_pcInfoList, fSecurePCName) => {

    //calculate list of pcinfo extracted value
    const pcInfoExtracted       = (() => {
        if (typeof _pcInfoList  === "string") return [];
        return _pcInfoList.filter((pcInfo) => fSecurePCName === pcInfo.computer_name);
    })();

    // 一致する名前がない場合
    if (pcInfoExtracted.length === 0) return "-";
    return pcInfoExtracted[0].os_platform;          //returns os platform value
};

///<summary>
// api computes isUpdatedSecuritySoft 
///</summary>
const isUpdatedSecuritySoft = (fSecure, offsetDate14DayAgo) => {

    // 最後にFsecureサーバーに接続した日([2020,04,29])
    const lastConnection            = fSecure.last_connection.slice(0, 10).split(/-/);

    const lastConnectionDate        = new Date(Number(lastConnection[0]), 
                                        Number(lastConnection[1]),Number(lastConnection[2]));

    // 最後にセキュリティソフトを更新した日([2020,04,29])
    const virusDefinitionUpdated    = fSecure.virus_definition_updated.slice(0, 10).split(/-/);

    const virusDefinitionUpdatedDate = new Date(Number(virusDefinitionUpdated[0]),
                                        Number(virusDefinitionUpdated[1]),
                                        Number(virusDefinitionUpdated[2]));

    // 最後にFsecureサーバーに接続した日が14以内かつ最後のアップデートが14日前以降の場合 false
    return ((lastConnectionDate.getTime()           >= offsetDate14DayAgo) &&
            (virusDefinitionUpdatedDate.getTime()   < offsetDate14DayAgo)); //returns bool state
};

//initialize member variable
let statusIcon = <img src={bikkuri}  style={{width:"25px",height:"25px"}}/>;

///<summary>
// api creates pctable structure 
///</summary>
const createPCTable = (_pcInfoList, _fSecurePcInfo, offsetDate14DayAgo) => {

    //loop returns fsecure pcinfo
    return _fSecurePcInfo.map((fSecure) => {

        //calculates computer protection data in json format
        const computerProtection    = (() => {
            if ( "Critical"  === fSecure.overall_protection_status )
                return { value: "異常あり", color: "#FF6919", icon:statusIcon };
            return { value: "異常なし", color: "#000000" ,icon:''};
        })();

        //calculates malware in json format
        const malware               = (() => {
            if (isUpdatedSecuritySoft(fSecure, offsetDate14DayAgo))
                return { value: "14日以内に更新あり", color: "#000000" , icon:''};
            return { value: "更新なし", color: "#FF6919", icon:statusIcon };
        })();

        //returns fsecure pctable data in json object
        return {
            pcname              : fSecure.computer,
            windows_version     : findWindowsVersion(_pcInfoList, fSecure.computer),
            computer_protection : computerProtection,
            malware             : malware,
            last_connection     : fSecure.last_connection.slice(0, 10).replace("-", "年").replace("-", "月"),
        };
    });
};

///<summary>
// api returns grade 
///</summary>
const divide2Grade = (_value1, _value2, _sum) => {

    //calculate percent values
    const percent1          = calcPercent(_value1, _sum);
    const percent2          = calcPercent(_value2, _sum);
    const inputPercentList  = [percent1, percent2];
    const sumPercent        = inputPercentList.reduce((sum, next) => { return (sum += next); }, 0);
    const overPercent       = sumPercent - 100;

    // 最大値のindex
    const idxMax            = inputPercentList.indexOf(Math.max(...inputPercentList));

    return (() => {

        // パーセントの合計が100を超える場合
        if (sumPercent > 100)
            return (() => {
                // 最大値から余剰パーセントを引いたリストを返す
                return inputPercentList.map((prop, index) => {
                    if (index === idxMax) return prop - overPercent;
                    return prop;
                });
            })();

        // パーセントの合計が100を超えない場合
        return inputPercentList;
    })();
};

///<summary>
// api computes percent data 
///</summary>
const calcPercent   = (_value, _sum) => {

    // percent補正 (0%, 5%, 5%以上)
    const percent   = Math.round((_value / _sum) * 100 * 10) / 10;
    // percent補正 (0%, 5%, 5%以上)
    return (() => {
        if (percent <= 0) return 0;
        if (percent < 5) return 5;
        return percent;
    })();
};

///<summary>
// api to create fsecure computer protection info
///</summary>
const createFSecureComputerProtectionInfo = (_fSecurePCInfo) => {

    // PC総計
    const totalPCCnt        = _fSecurePCInfo.length;
    // 異常ありPCの数
    const abnormalPCCnt     = _fSecurePCInfo.filter((pcInfo) => pcInfo.computer_protection.value == "異常あり").length;
    // 異常なしPCの数
    const normalPCCnt       = totalPCCnt - abnormalPCCnt;
    const outputPercentList = divide2Grade( abnormalPCCnt, normalPCCnt, totalPCCnt );

    return {
        abnormal    : {
            percent : outputPercentList[0],
            value   : `${abnormalPCCnt}台`,
        },
        normal      : {
            percent : outputPercentList[1],
            value   : `${normalPCCnt}台`,
        },
    };
};

///<summary>
// api to create fsecure malware info
///</summary>
const createFSecureMalwareInfo = (_fSecurePCInfo) => {

    // PC総計
    const totalPCCnt        = _fSecurePCInfo.length;

    // 更新無しPCの数
    const noUpdatePCCnt     = _fSecurePCInfo.filter((pcInfo) => pcInfo.malware.value == "更新なし").length;
    // 更新有りPCの数
    const updatedPCCnt      = totalPCCnt - noUpdatePCCnt;
    const outputPercentList = divide2Grade( updatedPCCnt, noUpdatePCCnt, totalPCCnt );

    return {
        update      : {
            percent : outputPercentList[0],
            value   : `${updatedPCCnt}台`,
        },
        no_update   : {
            percent : outputPercentList[1],
            value   : `${noUpdatePCCnt}台`,
        },
    };
};

