import React, { useEffect, useState } from 'react';
import * as ReactBootStrap from 'react-bootstrap';
import './../../StylesCSS/cssStyle.css';
const NASTableGraphComponent = (props) => {
    const percentage=props.data.row_data.value_per+"%";
    let textMessage = '';
    if (props.data.row_data.value_per > 90) {
        textMessage = `ストレージの使用容量が90%を超えました。`;
    } else if (props.data.row_data.value_per < 70) {
        textMessage = '十分な空き容量があります。';
    } else {
        textMessage = `ストレージの使用容量が70%を超えました。`;
    }
    return (
        <div >
            <ReactBootStrap.Table className="nas_graph_table"> 
            <thead>
                        <tr >
                            <th colSpan='4' className="Table-header" style={{fontSize:"1.45rem", borderBottom:'1px solid rgba(0, 0, 0)'}}>
                                {props.data.header_data}
                            </th>
                        </tr>
                </thead>
                    <tbody>
                        <tr>
                            <td rowSpan="2" align="center" style={{ width: "10%" }}>{props.data.row_data.image}</td>
                            <td><span style={{ fontSize: '120%', color: props.data.row_data.color }}>容量</span></td>
                            <td><span style={{ fontSize: '120%', color: props.data.row_data.color }}>{props.data.row_data.value}</span></td>
                            <td rowSpan="2"><span style={{ fontSize: '130%' }}>{textMessage}</span></td>
                        </tr>
                        <tr>
                            <td id="id_nas_graph_strage_table_bar" align="left" colSpan="2" style={{ width: "50%" }}>
                                <div className="bar_container">
                                    <div className="nasbar" style={{ width: percentage }}>{percentage}</div>
                                </div>
                            </td>
                        </tr>
                    </tbody>
                
            </ReactBootStrap.Table>
        </div>
    );
}
export default NASTableGraphComponent;