import React, { useEffect, useState } from "react";
import * as ReactBootStrap from "react-bootstrap";
import "./../../StylesCSS/table.css";

const ExternalNetworkTableComponent = (props) => {

  return (
    
  <div style={{ height: "100%" }}>
      <ReactBootStrap.Table className="border-hide" >
        <div>
          <tbody>
            {/* add lines dynamically depending on device list */}
            <tr style={{ borderRight: 'none', borderTop: 'none', borderBottom: 'none' }} >
              {props.networkdata.networklist.map((c, idx) => {
                  return (
                    <>
                      <td key={idx} style={{  border:'none' }} className="ml-4"></td>
                      <td key={idx} style={{  border:'none'}} className="ml-4"></td>
                    </>

                  )
              })}
            </tr>

            {/* add devices dynamically */}
            <tr>
              <td className="border-hide" style={{ width: '50px' }}></td>

              {props.networkdata.networklist.map((colname, idx) => (
      
                <td key={idx} style={{ textAlign: 'center', borderLeft: 'none' }} className="border-hide pl-4 ml-4" colSpan={2}>
                  {" "}
                  <span style={{ textAlign: 'center', display: 'block', width: '70px',paddingLeft:'15px' }}>
                    {props.networkdata.image}
                  </span>
                  <span style={{ textAlign: 'left', width: '80px', wordWrap: 'break-word' }} className="pl-0 pr-0">
                    {props.networkdata.networklist[idx].pcName}
                  </span>{" "}
                </td>
              ))
              }
            </tr>
          </tbody>
        </div>
      </ReactBootStrap.Table>
    </div>
  );
};
export default ExternalNetworkTableComponent;
