import React, { useEffect, useState } from 'react';
import NAS_FAX_Component from './NASAgentComponent/NAS_FAX_Component'
import "react-datepicker/dist/react-datepicker.css";
import "@fortawesome/fontawesome-free/js/all.js";
import PcAgentData from "./PCAgentComponents/PCAgentComponent";
import MFPAgentData from "./MFPComponent/MFPSharpMainComponent";
import PCOverallEvaluationComponent from "./PCAgentComponents/PCOverallEvaluationComponent";
import * as ReactBootStrap from "react-bootstrap";
import * as pdfFile from "./PdfReport";
import { addBusinessDays } from "date-fns";
import NetworkComponent from "./NetworkStatusComponent/NetworkComponent";
import SecurityUsageStatusComponent from "./SecurityUsageStatusComponents/SecurityUsageStatusComponent";
import ApplicationUsageComponent from "./ApplicationUsageStatusComponents/ApplicationUsageComponent";
import MFPOtherComponent from "./MFPComponent/MFPOtherComponent";
import NoDataExistComponent from "./reusableComponents/NoDataExistComponent";
import { useDispatch, useSelector } from "react-redux";
import HttpService from "../services/httpservice";
import * as JSZip from "jszip";
import { saveAs } from "file-saver";
import ProgressBarComponent from "./ProgressBarComponent";
import FsecureMainComponent from './FsecureComponent/FsecureMainComponent';

var pdfDocGenerator = {};
var lstofdocparams = [];
var docParams = [];
var docDef    = [];

const Dashboardcomponent = (props) => {
    var zip = new JSZip();
    let dispatch = useDispatch();
    var tenantid;
    const initialState = { lst_tenant_id: [], ES_query_result: {}, showNav: false };
    const [boardData, setBoardData] = useState(initialState);
    let reducerStateData = useSelector(state => state.searchComponent.ES_query_result);
    let lst_tenantid = useSelector(state => state.searchComponent.lst_tenant_id);
    let isClosebtnclicked = useSelector((state) => state.searchComponent.progressValue.isClosebtnclicked);
    if (props.tenantid)
        tenantid = props.tenantid;
    var  nas_id="id_nas_graph"+tenantid;
    var fax_id="fax-chart-data"+tenantid;

    useEffect(() => {
        if (lst_tenantid) {
            setBoardData({ ...boardData, lst_tenant_id: lst_tenantid });
        }
        if (props.IsTenantManagement && !props.filedownload.hasOwnProperty(tenantid))
            props.filedownload[tenantid] = { "filedownloaded": true };
        if (reducerStateData.hasOwnProperty(tenantid)) {

            setBoardData({ ...boardData, lst_tenant_id: lst_tenantid, ES_query_result: reducerStateData[tenantid] });
        }
        if (props.multiprint) {
            props.multiresetPrint();
            lstofdocparams = [];
            pdfDocGenerator = {};
            docDef = [];
          }
    }, [reducerStateData])

    const printDocument = () => {
        var params = {hasSummary : false,  pageNum :[], summaryDefaultText:''};
        var mfp_sharp_ids   = ['id_mfp_day_graph'+tenantid,'id_mfp_month_graph'+tenantid];
        var mfp_other_ids   = ['id_mfp_day_graph_other'+tenantid,'id_mfp_month_graph_other'+tenantid];
        pdfFile.page_numbers.length=0
        var data_exists     = 0;
       function validatedata(type){
        params.pageNum=pdfFile.page_numbers;
        switch(type) {
            case 'summary':
                if(boardData.ES_query_result.hasOwnProperty("pc_status") && boardData.ES_query_result["pc_status"] !== 'NoHitError') {
                    data_exists += 1;
                    params.hasSummary = true;
                    params.summaryDefaultText = boardData.ES_query_result.pc_status[0].pc_score.pc_evaluation_info.textarea_data
                    return  pdfFile.summarytable(boardData.ES_query_result.pc_status[0])
                }
                break;
            case 'network':
                if(boardData.ES_query_result.hasOwnProperty("network_status") && boardData.ES_query_result["network_status"] !== 'NoHitError') {
                    data_exists += 1;
                    return  pdfFile.netwroktable(boardData.ES_query_result.network_status)
                }
                break;
            case 'pcagent':
                if(boardData.ES_query_result.hasOwnProperty("pc_status") && boardData.ES_query_result["pc_status"] !== 'NoHitError') {
                    data_exists += 1;
                    let pc              = boardData.ES_query_result.pc_status[0].pc_list_info;
                    return pc.map(function(data, index) { 
                        return pdfFile.pctable(data, index);
                    })
                }
                break;
            case 'securityusage':
                    if(boardData.ES_query_result.hasOwnProperty("pc_status") && boardData.ES_query_result["pc_status"] !== 'NoHitError') {
                        data_exists += 1;
                        return   pdfFile.securityusagetable_pagewise(boardData.ES_query_result.pc_status[0].security_status)
                    }
                    break;
            case 'nastable':
                if (boardData.ES_query_result.hasOwnProperty("nas_info") && boardData.ES_query_result["nas_info"] !== 'NoHitError') {
                        data_exists += 1;
                        return pdfFile.nastable(boardData.ES_query_result.nas_info,tenantid)
                }
                break;
            case 'faxtable':
                if (boardData.ES_query_result.hasOwnProperty("nas_info") && boardData.ES_query_result["nas_info"] !== 'NoHitError') {
                        data_exists += 1;
                        return pdfFile.faxtable(tenantid)
                }
                break;
            case 'mfpsharp':
                 if (boardData.ES_query_result.hasOwnProperty("mfp_data") && boardData.ES_query_result.mfp_data["sharp"] !== 'NoHitError') {
                    data_exists += 1;
                    let mfp_sharp       = boardData.ES_query_result.mfp_data["sharp"];
                    return mfp_sharp.map(function(data, index) {
                            return pdfFile.mfptable_sharp(data, index, mfp_sharp_ids,tenantid);
                    })
                }
                break;
            case 'mfpother':
                if (boardData.ES_query_result.hasOwnProperty("mfp_data") && boardData.ES_query_result.mfp_data["other"] !== 'NoHitError') {
                    data_exists += 1;
                    let mfp_other       = boardData.ES_query_result.mfp_data["other"];
                    return mfp_other.map(function(data, index) {
                            return pdfFile.mfptable_other(data, index,mfp_other_ids,tenantid);
                    })
                }
                break;
            case 'portaldata':
                if(boardData.ES_query_result["portal_data"][0] !== 'NoHitError' ||
                    boardData.ES_query_result["portal_data"][1] !== 'NoHitError') {
                    data_exists += 1;
                     let portaldata      = boardData.ES_query_result.portal_data;
                    return  portaldata.map(function(data, index) {
                            return pdfFile.applicationusagetable(data, index,tenantid);
                    })
                }
                break;
            default:
                
            }
        };
        sessionStorage.setItem('headercount', 1);  
        sessionStorage.setItem('pagenumber', 0);  

        
        var docDefinition = {
            
            pageSize: 'A4', pageMargins: [0,0, 0, 0],
            //Set Document MetaData Application name as NULL
            info: {
                creator:' ',
              },
            content: [
                pdfFile.coverpage(),
                validatedata('summary'),
                validatedata('network'),
                validatedata('pcagent'),
                validatedata('securityusage'),
                validatedata('nastable'),
                validatedata('faxtable'),
                validatedata('mfpsharp'),
                validatedata('mfpother'),
                validatedata('portaldata')                
            ],
            
            styles: {
                subheader:{
                    margin:[0,20,0,0],
                },
                pctableheader:{
                    margin:[0,3,0,3],
                    bold:true,
                    fillColor:'#CCCCCC'
                }, 
                pctabletext:{
                    margin: [0, 14, 0, 14]
                } ,
                tableExample: {
                    margin: [0, 5, 0, 5],
                    unbreakable: true,
                },
                tableHeader: {
                    fillColor:'#CCCCCC',
                    fontSize: 10,
                   // margin:[5,5,5,5],
                    bold:true,
                    color: 'black'
                },
                piechart: {
                    margin: [5,5,5,5]
                },
               
            },
            images: {
               
                transparentimg: pdfFile.transpernt_img
            },
            
            defaultStyle: {
                font: 'GenShinGothic',
                fontSize: 10,
                columnGap: 0,
            }
        }

        //tenant does not have data
        if( data_exists === 0){
            dispatch({ type: "setProgress", tenantid:tenantid,value: 100,showProgressbar:false });
            return;
        }

        //add tenantid which has data        
        docDef.push({tenantID: tenantid, docdefinition: docDefinition, Params:params})
        

        var fileName = tenantid;         
        var docParamslst = {
            tenantid: tenantid,
            params: params,
            filename: fileName,
        };

        //pdfMake.createPdf(docDefinition).download('Report.pdf');
        var pdfservice   =  new HttpService();
        try 
        {
            pdfservice.postPDFDocData(docDefinition, docParamslst).then((resp) => 
            {
                console.log(resp.data); 
                fileName = resp.data.filename;  
                if (!props.IsTenantManagement) 
                {
                    pdfservice.getPDFStream(resp.data).then((resp) => 
                    {
                        var respdata = resp.data;
                        const linkSource = `data:application/pdf;base64,${resp.data.pdfbase64}`;
                        const downloadLink = document.createElement("a");
                        const fileName = "Report.pdf";
                        downloadLink.href = linkSource;
                        downloadLink.download = fileName;
                        downloadLink.click();
                        pdfFile.page_numbers.length = 0;
                        docDef = [];        
                    });
                } 
                else 
                {       
                    lstofdocparams[lstofdocparams.length] = resp.data; 
                        if (Object.keys(lstofdocparams).length === docDef.length && !isClosebtnclicked) 
                        {
                            lstofdocparams.map((item, idx) => 
                            {
                                pdfservice.getPDFStream(item).then((resp) => 
                                {
                                    try
                                    {
                                    pdfDocGenerator[resp.data.tenantid] = _base64ToArrayBuffer(resp.data.pdfbase64);
                                    var percentage=Math.round((Object.keys(pdfDocGenerator).length / docDef.length) * 10 * 100) / 10;
                                    dispatch({ type: "setProgress", tenantid:resp.data.tenantid,value: percentage,showProgressbar:true });
                
                                    }
                                    catch(exception)
                                    {
                                        pdfDocGenerator[resp.data.tenantid] = ""
                                    }
                                    if (Object.keys(pdfDocGenerator).length === docDef.length)
                                    {
                                        var folderOneName, folderTwoName;
                                        for (var item in pdfDocGenerator) 
                                        {
                                            if(item === "undefined") continue;
                                            folderOneName = zip.folder(props.selected_date + '_利用状況レポート');
                                            folderTwoName = folderOneName.folder('シャープマーケティングジャパン株式会社');
                                            folderTwoName.file(item + ".pdf", pdfDocGenerator[item]);
                                        }
                                        zip.generateAsync({ type: "blob" }).then(content => 
                                        {
                                            saveAs(content, "cocoro_report.zip");
                                            pdfDocGenerator = {};
                                            pdfFile.page_numbers.length = 0;
                                            docDef = [];
                                            lstofdocparams = [];
                                            setTimeout(function() {
                                                dispatch({ type: "setProgress", showProgressbar:false });
                                              }, 500);                        
                                            // window.location.reload(false);
                                        });                            
                                    }                        
                                });
                            });
                        }
                }
            });
        } 
        catch (exception) 
        {
            console.log("exception", exception);
        }
    };
    

    function _base64ToArrayBuffer(base64) {
        var binary_string = window.atob(base64);
        var len = binary_string.length;
        var bytes = new Uint8Array(len);
        for (var i = 0; i < len; i++) {
            bytes[i] = binary_string.charCodeAt(i);
        }
        return bytes.buffer;
    }
    if(props.print)
    {
        printDocument();
        props.resetPrint();
    }
    if (props.IsTenantManagement && reducerStateData.hasOwnProperty(tenantid)
    && (reducerStateData[tenantid].nasstatus || (reducerStateData[tenantid].hasOwnProperty("nas_info") && reducerStateData[tenantid].nas_info == 'NoHitError'))
    && (reducerStateData[tenantid].mfpstatus || (reducerStateData[tenantid].hasOwnProperty("mfp_data") && reducerStateData[tenantid].mfp_data["sharp"] == 'NoHitError'))
    && (reducerStateData[tenantid].mfpotherstatus || (reducerStateData[tenantid].hasOwnProperty("mfp_data") && reducerStateData[tenantid].mfp_data["other"] == 'NoHitError'))
    && (reducerStateData[tenantid].faxstatus || (reducerStateData[tenantid].hasOwnProperty("fax_data") && reducerStateData[tenantid].fax_data == 'NoHitError'))
    && (reducerStateData[tenantid].portalstatus || (reducerStateData[tenantid].hasOwnProperty("portal_data") && (reducerStateData[tenantid].portal_data[0] == 'NoHitError' || reducerStateData[tenantid].portal_data[1] == 'NoHitError')))
    && (props.filedownload.hasOwnProperty(tenantid) && props.filedownload[tenantid].filedownloaded)) {
        props.filedownload[tenantid] = { "filedownloaded": false };
        printDocument();
    }
    return (
        <div>
            {boardData.lst_tenant_id.length !== 0 && (
                <div>
                    <ReactBootStrap.Card.Body id="id_dashboard_all" className="pl-3 pr-2 pt-3 pb-3" style={{ display: props.navstatus ? 'block' : 'none' }}>
                        <ReactBootStrap.Tab.Container id="tab-container" >
                            <ReactBootStrap.Row className="col-12">
                            <ReactBootStrap.Nav id="navs-row" >
                                    <ReactBootStrap.Nav.Item>
                                        <ReactBootStrap.Nav.Link eventKey="1" className="nav-style  nav-tabs2 ">サマリー</ReactBootStrap.Nav.Link>
                                    </ReactBootStrap.Nav.Item>
                                    <ReactBootStrap.Nav.Item>
                                        <ReactBootStrap.Nav.Link eventKey="2" className="nav-style nav-tabs2"  >個別PC情報</ReactBootStrap.Nav.Link>
                                    </ReactBootStrap.Nav.Item>
                                    <ReactBootStrap.Nav.Item>
                                        <ReactBootStrap.Nav.Link eventKey="3" className="nav-style nav-tabs2" >NAS情報</ReactBootStrap.Nav.Link>
                                    </ReactBootStrap.Nav.Item>
                                    <ReactBootStrap.Nav.Item>
                                        <ReactBootStrap.Nav.Link eventKey="4" className="nav-style nav-tabs2" >MFP情報(SHARP製)</ReactBootStrap.Nav.Link>
                                    </ReactBootStrap.Nav.Item>
                                    <ReactBootStrap.Nav.Item>
                                        <ReactBootStrap.Nav.Link eventKey="5" className="nav-style nav-tabs2">MFP情報(他社製)</ReactBootStrap.Nav.Link>
                                    </ReactBootStrap.Nav.Item>
                                    <ReactBootStrap.Nav.Item>
                                        <ReactBootStrap.Nav.Link eventKey="6" className="nav-style nav-tabs2">機器構成図</ReactBootStrap.Nav.Link>
                                    </ReactBootStrap.Nav.Item>
                                    <ReactBootStrap.Nav.Item>
                                        <ReactBootStrap.Nav.Link eventKey="7" className="nav-style nav-tabs2">アプリ利用状況</ReactBootStrap.Nav.Link>
                                    </ReactBootStrap.Nav.Item>
                                    <ReactBootStrap.Nav.Item>
                                        <ReactBootStrap.Nav.Link eventKey="8" className="nav-style nav-tabs2">セキュリティソフト利用状況</ReactBootStrap.Nav.Link>
                                    </ReactBootStrap.Nav.Item>
                                    <ReactBootStrap.Nav.Item>
                                        <ReactBootStrap.Nav.Link eventKey="9" className="nav-style nav-tabs2">F-Secureの状況</ReactBootStrap.Nav.Link>
                                    </ReactBootStrap.Nav.Item>
                                </ReactBootStrap.Nav>


                            </ReactBootStrap.Row>
                            <ReactBootStrap.Row id="tabs-row" className="mt-4 ml-1 mr-2">
                                <ReactBootStrap.Tab.Content id="all-dashboard" style={{width:'100%'}}>
                                    <ReactBootStrap.Tab.Pane eventKey="1" id='summary_pcstatus_id' >
                                    {(!props.IsTenantManagement &&boardData.ES_query_result.hasOwnProperty("pc_status") &&
                                            boardData.ES_query_result["pc_status"] !== 'NoHitError') ?
                                            <PCOverallEvaluationComponent stateData={boardData.ES_query_result.pc_status} />
                                        :
                                        <NoDataExistComponent />
                                        }
                                    </ReactBootStrap.Tab.Pane>
                                    <ReactBootStrap.Tab.Pane eventKey="2" id='pcstatus_id' >
                                    {(!props.IsTenantManagement && boardData.ES_query_result.hasOwnProperty("pc_status") &&
                                            boardData.ES_query_result["pc_status"] !== 'NoHitError' ) ?
                                            <PcAgentData stateData={boardData.ES_query_result.pc_status} />
                                            :
                                            <NoDataExistComponent />
                                        }
                                    </ReactBootStrap.Tab.Pane>
                                    <ReactBootStrap.Tab.Pane eventKey="3" id="nas_info_id" >
                                    {(boardData.ES_query_result.hasOwnProperty("nas_info") &&
                                            boardData.ES_query_result["nas_info"] !== 'NoHitError') ?
                                            <NAS_FAX_Component data={{ nas_info: boardData.ES_query_result.nas_info, fax_data: boardData.ES_query_result.fax_data,nas_id:nas_id,fax_id:fax_id }}  />
                                        :
                                        <NoDataExistComponent />
                                        }
                                    </ReactBootStrap.Tab.Pane>
                                    <ReactBootStrap.Tab.Pane eventKey="4" id='sharp_mfp_id' >
                                    {(boardData.ES_query_result.hasOwnProperty("mfp_data") &&
                                            boardData.ES_query_result.mfp_data["sharp"] !== 'NoHitError') ?
                                            <MFPAgentData stateData={boardData.ES_query_result.mfp_data["sharp"]} tenantid={tenantid}/>
                                            :
                                            <NoDataExistComponent />
                                        }
                                    </ReactBootStrap.Tab.Pane>
                                    <ReactBootStrap.Tab.Pane eventKey="5" id='other_mfp_id' >
                                    {(boardData.ES_query_result.hasOwnProperty("mfp_data") &&
                                            boardData.ES_query_result.mfp_data["other"] !== 'NoHitError') ?
                                            <MFPOtherComponent stateData={boardData.ES_query_result.mfp_data["other"]} tenantid={tenantid} />
                                            :
                                            <NoDataExistComponent />
                                        }
                                    </ReactBootStrap.Tab.Pane>
                                    <ReactBootStrap.Tab.Pane eventKey="6" id='network_info_id' >
                                    {( boardData.ES_query_result.hasOwnProperty("network_status") &&
                                            boardData.ES_query_result["network_status"] !== 'NoHitError') ?
                                            <NetworkComponent stateData={boardData.ES_query_result.network_status} />
                                            :
                                            <NoDataExistComponent />
                                        }
                                    </ReactBootStrap.Tab.Pane>
                                    <ReactBootStrap.Tab.Pane eventKey="7" id='application_usage_id' >
                                    {(boardData.ES_query_result.hasOwnProperty("portal_data") &&
                                           (boardData.ES_query_result["portal_data"][0] !== 'NoHitError' ||
                                            boardData.ES_query_result["portal_data"][1] !== 'NoHitError')) ?
                                            <ApplicationUsageComponent stateData={boardData.ES_query_result.portal_data} tenantid={tenantid}/>
                                            :
                                            <NoDataExistComponent />
                                    }
                                    </ReactBootStrap.Tab.Pane>
                                    <ReactBootStrap.Tab.Pane eventKey="8" id='security_status_id' >
                                    {(!props.IsTenantManagement && boardData.ES_query_result.hasOwnProperty("pc_status") &&
                                            boardData.ES_query_result["pc_status"] !== 'NoHitError' ) ?
                                            <SecurityUsageStatusComponent stateData={boardData.ES_query_result.pc_status} />
                                            :
                                            <NoDataExistComponent />
                                    }
                                    </ReactBootStrap.Tab.Pane>
                                    <ReactBootStrap.Tab.Pane eventKey="9" id='fsecure_id' >
                                    {(boardData.ES_query_result.hasOwnProperty("fsecure_data") && 
                                        boardData.ES_query_result["fsecure_data"] !== 'NoHitError') ?
                                            <FsecureMainComponent stateData={boardData.ES_query_result.fsecure_data} tenantid={tenantid}/>
                                            :
                                            <NoDataExistComponent />
                                    }
                                    </ReactBootStrap.Tab.Pane>
                                </ReactBootStrap.Tab.Content>
                            </ReactBootStrap.Row>

                        </ReactBootStrap.Tab.Container>
                   
                   

                    </ReactBootStrap.Card.Body>
                </div>
            )}
        </div>


    )
}

export default Dashboardcomponent;
