import React, { useEffect, useState, useContext } from 'react';
import { useDispatch, useSelector } from "react-redux";
const ProgressBarComponent = () => {
    let dispatch = useDispatch();
    let percentage = useSelector(
        (state) => state.searchComponent.progressValue
      );
    var perecenatge_value = percentage.value + "%";
    const closebtn=()=>{
        dispatch({ type: "setProgress", showProgressbar:false ,isClosebtnclicked:true});
    }
    return (
        <div>
        {percentage.showProgressbar && !percentage.isClosebtnclicked ? 
        <div id="id__dialog" className="progressbar-modal" >
            <div className="modal-dialog modal-dialog-centered">
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title"> PDF出力</h5>
                    </div>
                    <div id="id_progressBar_dialog" className="modal-body">
                        <div className="progress progressBar">
                            <div id="id_progressBar" className="progress-bar bg-info" style={{ width: perecenatge_value }} role="progressbar" aria-valuemin="0" aria-valuemax="100" aria-valuenow={percentage.value} >{perecenatge_value}</div>
                        </div>
                        <div>
                            <small id="id_progressBar_text">{percentage.tenantid}</small>
                        </div>
                    </div>
                    <div className="modal-footer">
                        <button id="id_btn_allprintPDF_cancel" type="button" onClick={closebtn} name="cancel" className="btn btn-secondary" data-dismiss="modal" >キャンセル</button>
                    </div>
                </div>
            </div>
        </div> :null}
        </div>

    )
}

export default ProgressBarComponent