export const mfpparsedata=(mfpInfo,selectedYear,selectedMon)=>{
 
  // get latest data
  const lastData = mfpInfo.latest_data.hits.hits[0]._source;

  const modelName = lastData.model_name;
  
  const isSharpMFP = (() => {
    if (modelName.match(/SHARP/)) return true;
    return false;
  })();

  const logTime = lastData.log_time
    .slice(0, 8)
    .replace("-", "年")
    .replace("-", "月");


 // macaddressを抽出
 const mfpMacAddress = extractMacAddress(lastData.ethernet_information);

  // get first day of selected month
  const startDateDaily = `${selectedYear}年${selectedMon}月01日`;
  // last date
  const lastDateInMon = new Date(selectedYear, Number(selectedMon), 0);
  const totalDays=lastDateInMon.getDate();
  // get last day of selected month
  const endDateDaily = `${selectedYear}年${selectedMon}月${totalDays}日`;

  // Create daily horizontal axis [01 (Sun), ..., Last day (Sun)]
  const dailyXLim = createDailyXLim(selectedYear, selectedMon, totalDays);
  // Create daily vertical axis [count, ...]
  const dailyYLim = (() => {
    if (isSharpMFP){
    const colorPrintsList = createDailyYLim(
      "counter_type_prints_color",
      mfpInfo,
      totalDays,
      selectedYear,
      selectedMon
    );
    const colorCopiesList = createDailyYLim(
      "counter_type_copies_color",
      mfpInfo,
      totalDays,
      selectedYear,
      selectedMon
    );
    const monoPrintsList = createDailyYLim(
      "counter_type_prints_monochrome",
      mfpInfo,
      totalDays,
      selectedYear,
      selectedMon
    );
    const monoCopiesList = createDailyYLim(
      "counter_type_copies_monochrome",
      mfpInfo,
      totalDays,
      selectedYear,
      selectedMon
    );
    const monoFaxList = createDailyYLim(
      "counter_type_fax_receive_monochrome",
      mfpInfo,
      totalDays,
      selectedYear,
      selectedMon
    );
    return {
      color: sumArrays([colorPrintsList, colorCopiesList]),
      mono: sumArrays([monoPrintsList, monoCopiesList, monoFaxList]),
    };
  }
  return {
    life_count: createDailyYLim(
      "life_count",
      mfpInfo,
      totalDays,
      selectedYear,
      selectedMon
    ),
  };
  })();
  // fetch 11 months before the selected month 
  const dateBefore11Mon = new Date(Number(selectedYear), Number(selectedMon) - 12, 1);
  // convert 11 month before date to year and month format
  // this is considering as staring month
  const startDateMonthly = dateToStr(dateBefore11Mon);
  // end month 
  const endDateMonthly = `${selectedYear}年${selectedMon}月`;
  const monthlyXLimPram = [`${selectedMon}月`];
  const monthlyXLim =  xLimPaddingMon(monthlyXLimPram);


  // Create monthly vertical axis [count, ...]
  const monthlyYLim = (() => {
    if (isSharpMFP)
    {
      const colorPrintsList = createYParamsMonthly(
        "counter_type_prints_color",
        mfpInfo,
        selectedMon,
        selectedYear,
        monthlyXLim
      );
      const colorCopiesList = createYParamsMonthly(
        "counter_type_copies_color",
        mfpInfo,
        selectedMon,
        selectedYear,
        monthlyXLim
      );
      const monoPrintsList = createYParamsMonthly(
        "counter_type_prints_monochrome",
        mfpInfo,
        selectedMon,
        selectedYear,
        monthlyXLim
      );
      const monoCopiesList = createYParamsMonthly(
        "counter_type_copies_monochrome",
        mfpInfo,
        selectedMon,
        selectedYear,
        monthlyXLim
      );
      const monoFaxList = createYParamsMonthly(
        "counter_type_fax_receive_monochrome",
        mfpInfo,
        selectedMon,
        selectedYear,
        monthlyXLim
      );

      const colorValues = sumArrays([colorPrintsList, colorCopiesList]);
      const monoValues = sumArrays([
        monoPrintsList,
        monoCopiesList,
        monoFaxList,
      ]);
      return {
        color: {
          values: colorValues,
          avg: calcAvg(colorValues),
        },
        mono: {
          values: monoValues,
          avg: calcAvg(monoValues),
        },
      };
    }
    // 社外機の場合
    const lifeCountValues = createYParamsMonthly(
      "life_count",
      mfpInfo,
      selectedMon,
      selectedYear,
      monthlyXLim
    );
    return {
      life_count: {
        values: lifeCountValues,
        avg: calcAvg(lifeCountValues),
      },
    };
  })();
  // 他社しか使わない
  const lifeCountUnit = (() => {
    if (lastData.life_count_unit == "sheets") return "印刷枚数";
    if (lastData.life_count_unit == "impressions") return "印刷面数";
    return "単位情報なし";
  })();

  const mfpName = (() => {
    if (lastData.friendly_name === undefined || lastData.friendly_name == "")
      return `${lastData.model_name}_${lastData.serial_id}`;
    return lastData.friendly_name;
  })();
  const basic_info={"製品名": lastData.model_name,"IPアドレス": lastData.address,"シリアル番号": lastData.serial_id,"MACアドレス": mfpMacAddress}
  return (() => {
    if (isSharpMFP)
      return {
        mfp_name: mfpName,
        mfp_basicinfo:basic_info,
        mfp_productname: lastData.model_name,
        mfp_ipaddress: lastData.address,
        mfp_macaddress: mfpMacAddress,
        mfp_serialnumber: lastData.serial_id,
        graph_data_day: {
          value: [
            ["date", ...dailyXLim],
            ["カラー", ...dailyYLim.color],
            ["モノクロ", ...dailyYLim.mono],
          ],
          start_date: startDateDaily,
          end_date: endDateDaily,
        },
        graph_data_month: {
          value: [
            ["date", ...monthlyXLim],
            ["カラー", ...monthlyYLim.color.values],
            ["モノクロ", ...monthlyYLim.mono.values],
          ],
          start_date: startDateMonthly,
          end_date: endDateMonthly,
        },
        graph_data_ave: {
          value: [
            ["カラー", monthlyYLim.color.avg],
            ["モノクロ", monthlyYLim.mono.avg],
          ],
          value_sum: monthlyYLim.color.avg + monthlyYLim.mono.avg,
        },
        graph_data_all: {
          // value: [
          //   ["カラー", lastData.counter_type_prints_color],
          //   ["モノクロ", lastData.counter_type_prints_monochrome],
          // ],
          value: [
            [
              "カラー",
              sumValues([
                lastData.counter_type_prints_color,
                lastData.counter_type_copies_color,
              ]),
            ],
            [
              "モノクロ",
              sumValues([
                lastData.counter_type_prints_monochrome,
                lastData.counter_type_copies_monochrome,
                lastData.counter_type_fax_receive_monochrome,
              ]),
            ],
          ],
          value_sum:
            // lastData.counter_type_prints_color +
            // lastData.counter_type_prints_monochrome,
            sumValues([
              lastData.counter_type_prints_color,
              lastData.counter_type_copies_color,
              lastData.counter_type_prints_monochrome,
              lastData.counter_type_copies_monochrome,
              lastData.counter_type_fax_receive_monochrome,
            ]),
          start_date: endDateMonthly,
        },
        log_time: logTime,
        is_sharp_mfp: isSharpMFP,
      };

    return {
      mfp_name: mfpName,
      mfp_basicinfo:basic_info,
      mfp_productname: lastData.model_name,
      mfp_ipaddress: lastData.address,
      mfp_macaddress: mfpMacAddress,
      mfp_serialnumber: lastData.serial_id,
      graph_data_day: {
        value: [
          ["date", ...dailyXLim],
          ["印刷枚数", ...dailyYLim.life_count],
        ],
        start_date: startDateDaily,
        end_date: endDateDaily,
      },
      graph_data_month: {
        value: [
          ["date", ...monthlyXLim],
          ["印刷枚数", ...monthlyYLim.life_count.values],
        ],
        start_date: startDateMonthly,
        end_date: endDateMonthly,
      },
      graph_data_ave: {
        value_sum: monthlyYLim.life_count.avg,
      },
      graph_data_all: {
        value_sum: lastData.life_count,
        start_date: endDateMonthly,
      },
      log_time: logTime,
      is_sharp_mfp: isSharpMFP,
      unit: lifeCountUnit,
    };
  })();
}
const extractMacAddress = (macAddressList) => {
  // const notBlankMacAddressList = macAddressList.filter(prop => prop.address.match(/([0-9a-Z]{2}[:]){5}([0-9a-Z]{2})/))
  const notBlankMacAddressList = (() => {
    if  (Array.isArray(macAddressList))　return macAddressList;
    return [macAddressList];
  })();
  return (() => {
    if (notBlankMacAddressList) return notBlankMacAddressList[0].address;
    return "-";
  })();
};
const dateToStr = (date) => {
  //convert date to year年month月 format
  const year = String(date.getFullYear());
  const mon = date.getMonth() + 1;
  const mon0Complement = (() => {
    if (mon >= 10) return String(mon);
    return "0" + String(mon);
  })();
  return `${year}年${mon0Complement}月`;
};
const xLimPaddingMon = (xLim) => {
  const calender = [
    "01月",
    "02月",
    "03月",
    "04月",
    "05月",
    "06月",
    "07月",
    "08月",
    "09月",
    "10月",
    "11月",
    "12月",
  ];
  let offset = calender.indexOf(xLim[0]);
  const xLimPadding = [...xLim];
  while (xLimPadding.length < 12) {
    offset = (() => {
      if (offset === 0) return 11;
      return (offset -= 1);
    })();
    // リスト先頭に一月前を追加
    xLimPadding.unshift(calender[offset]);
  }
  return xLimPadding;
};

const createDailyXLim = (selectedYear, selectedMon, numOfDay) => {
    const dayOfWeekStr = ["(日)", "(月)", "(火)", "(水)", "(木)", "(金)", "(土)"];
    const xLim = [];
    for (let i = 1; i <= numOfDay; i++) {
      let today = new Date(Number(selectedYear), Number(selectedMon-1), i);
      let dayOfWeek = dayOfWeekStr[today.getDay()];
      // let date = `${("0"+i).slice(-2)}日${dayOfWeek}`;
      let date = (() => {
        if (i < 10) return `0${i}日${dayOfWeek}`;
        return `${i}日${dayOfWeek}`;
      })();
      xLim.push(date);
    }
    return xLim;
  };
  
const monthlyYPramExceptionHandling = (_first, _last) => {
    if (_first.value == null || _last.value == null) return null;
    // 日ずけ(2020-11-24')まで等しい場合
    if (_first.logTime.slice(0, 10) == _last.logTime.slice(0, 10)) return null;
    if (_last.value < _first.value) return null;
    return _last.value - _first.value;
  };
  const calcDifference = (yParamList, A0, mode, lastList, firstList) => {
    const yLim = yParamList.reduce(
      ({ prev, result }, next, idx) => {
        //falsy eval (0, "", undefined and null are falsy)
        if (!next) {
          return { prev: prev, result: [...result, null] };
        }
        // keeping value is falsy eval
        if (!prev) {
          if (mode === "monthly")
            return {
              prev: next,
              result: [
                ...result,
                monthlyYPramExceptionHandling(firstList[idx], lastList[idx]),
              ],
            };
          return { prev: next, result: [...result, null] };
        }
        const diff = next - prev;
        //assuming it is accumlated value,
        if (diff < 0) return { prev: next, result: [...result, next] };
        //avoid mutation. => [newEle0, ...arr, newEle1]
        return { prev: next, result: [...result, diff] };
      },
      { prev: A0, result: [] }
    );
  
    return yLim.result;
  };
  
  const createYParamsDaily = (
    key,
    A0,
    dailyBuckets,
    _numOfDay,
    _selectedYear,
    _selectedMon
  ) => {
    const yParamList = [];
    for (let i = 1; i <= _numOfDay; i++) {
      let setDate = (() => {
        if (i < 10) return `${_selectedYear}-${_selectedMon}-0${i}`;
        return `${_selectedYear}-${_selectedMon}-${i}`;
      })();
      let machData = dailyBuckets.filter(
        (prop) => prop.key_as_string === setDate
      );
      let yData = (() => {
        if (machData.length) return machData[0][key]["value"];
        return 0;
      })();
      yParamList.push(yData);
    }
    const mode = "daily";
    return calcDifference(yParamList, A0, mode, null, null);
  };
  
  const createOneMonthAgo = (_selectedMon, _selectedYear) => {
    return (() => {
      if (Number(_selectedMon) === 1) return `${Number(_selectedYear) - 1}-12`;
      if (Number(_selectedMon) === 10) return `${_selectedYear}-09`;
      if (Number(_selectedMon) > 10)
        return `${_selectedYear}-${Number(_selectedMon) - 1}`;
      return `${_selectedYear}-0${Number(_selectedMon) - 1}`;
    })();
  };
  const extractOneMonthAgoLog = (_key, _mfpInfo, _selectedMon, _selectedYear) => {
    // A0を作成する
    const setDateBefore1Mon = createOneMonthAgo(_selectedMon, _selectedYear);
    return _mfpInfo.monthly_buckets.buckets
      .filter((prop) => {
        return prop.key_as_string === setDateBefore1Mon;
      })
      .map((prop) => {
        return (() => {
          if (prop[_key]["value"]) return prop[_key]["value"];
          return 0;
        })();
      });
  };
  const createDailyYLim = (
    key,
    mfpInfo,
    numOfDay,
    selectedYear,
    selectedMon
  ) => {
    const dailyBuckets = mfpInfo.sort_month.buckets[0].daily_buckets.buckets;
    // 先月のカウントを取得(A0)
    const extractLastValues = extractOneMonthAgoLog(
      key,
      mfpInfo,
      selectedMon,
      selectedYear
    );
  
    const lastMonValues = (() => {
      if (extractLastValues.length === 0) return 0;
      return extractLastValues[0];
    })();
  
    return createYParamsDaily(
      key,
      lastMonValues,
      dailyBuckets,
      numOfDay,
      selectedYear,
      selectedMon
    );
  };
  
  const calcAvg = (values) => {
    const nums = values.filter((prop) => prop !== null);
    const sumValue = nums.reduce((sum, num) => (sum += num), 0);
    return (() => {
      if (nums.length === 0) return 0;
      return Math.round(sumValue / nums.length);
    })();
  };
  
  const createYParamsMonthly = (
    key,
    mfpInfo,
    selectedMon,
    selectedYear,
    xLim
  ) => {
    const bucketsMonthly = mfpInfo.monthly_buckets.buckets;
  
    const lastList = xLim.map((month) => {
      const setDate = month.replace("月", "");
      const machData = bucketsMonthly.filter(
        (prop) => prop.key_as_string.slice(5, 7) === setDate
      );
      return (() => {
        if (
          machData.length === 0 ||
          machData[0].latest_data.hits.hits.length === 0
        )
          return {
            value: null,
            logTime: null,
            dateMon: setDate,
          };
        return {
          value: machData[0].latest_data.hits.hits[0]._source[key],
          logTime: machData[0].latest_data.hits.hits[0]._source.log_time,
          dateMon: setDate,
        };
      })();
    });
  
    const firstList = xLim.map((month) => {
      const setDate = month.replace("月", "");
      const machData = bucketsMonthly.filter(
        (prop) => prop.key_as_string.slice(5, 7) === setDate
      );
      return (() => {
        if (
          machData.length === 0 ||
          machData[0].first_data.hits.hits.length === 0
        )
          return {
            value: null,
            logTime: null,
            dateMon: setDate,
          };
        return {
          value: machData[0].first_data.hits.hits[0]._source[key],
          logTime: machData[0].first_data.hits.hits[0]._source.log_time,
          dateMon: setDate,
        };
      })();
    });
  
    const mode = "monthly";
    // 1ヵ月前の差分リスト[a1-a0,...]
    const yLimMonthlyPramDiff = (() => {
      if (bucketsMonthly.length < 12) {
        const A0 = null;
        const yLimMonthlyPramPadding = [];
        for (let i = 0; i < xLim.length; i++) {
          let setDate = xLim[i].replace("月", "");
          let machData = bucketsMonthly.filter(
            (prop) => prop.key_as_string.slice(5, 7) === setDate
          );
          let yData = (() => {
            if (machData.length) return machData[0][key]["value"];
            return null;
          })();
          yLimMonthlyPramPadding.push(yData);
        }
  
        return calcDifference(
          yLimMonthlyPramPadding,
          A0,
          mode,
          lastList,
          firstList
        );
      }
      // 先頭データを初期値とする(A0)
      const A0 = mfpInfo.monthly_buckets.buckets[0][key].value;
      const yLimMonthlyPram = bucketsMonthly.map((prop) => {
        if (prop[key]["value"]) return prop[key]["value"];
        return 0;
      });
      return calcDifference(
        yLimMonthlyPram.slice(1, 13),
        A0,
        mode,
        lastList.slice(0, 13),
        firstList.slice(1, 13)
      );
    })();
  
    return yLimMonthlyPramDiff;
  };
  const sumArrays = (_arrays) => {
    return _arrays[0].reduce((sumsList, next, idx) => {
      let sum = 0;
      let existNum = false;
      for (let i = 0; i < _arrays.length; i++) {
        if (_arrays[i][idx] === null) continue;
        existNum = true;
        sum += _arrays[i][idx];
      }
      if (existNum) {
        return [...sumsList, sum];
      } else {
        return [...sumsList, null];
      }
    }, []);
  };
  const sumValues = (_array) => {
    return _array.reduce((sum, next) => {
      if (next === null || isNaN(next)) return sum;
      return (sum += next);
    }, 0);
  };