//namespaces 
import React, { useEffect, useState } from 'react';
import c3 from 'c3';
import * as d3 from 'd3';
import './../../StylesCSS/style.css'
import { useDispatch } from 'react-redux';
///########################################
///MFP Other info component
///#######################################
let displayText = false;
const MFPOtherGraphComponent = (props) => {
    let dispatch = useDispatch();
    //Invoke draw chart only if the data is modified
    useEffect(() => {
        if (props.data.mfp_data) {
            drawChart();
            dispatch({type:"setmfpotherstatus","tenantid":props.data.tenantid,value:true}); 
            
        }
    }, [props.data.mfp_data]);

    //api which reads the nas_data and creates a chart
    const drawChart = () => {
        const newArray = props.data.mfp_data.value[1].filter(n => n !== props.data.mfp_data.value[1][0]);
        if(newArray.every(v => v === 0 || v === null)){
            displayText = true;
        }
        else{
        let chartc = c3.generate({
            bindto: "#"+props.data.id,
            size: {
                width: 1050,
                height: 350
            },
            data: {
                x: 'date',
                columns:props.data.mfp_data.value,
                type: 'bar',
               
                colors: {
                    '印刷枚数': '#B1E7F4'
                },
                order: 'null'
            },
            grid: {
                y: {
                    show: true
                }
            }, 
            axis: {
                x: {
                    type: "category",
                    tick: {
                        rotate: 0,
                        multiline: true,
                        fit: true,
                        outer:false,
                    }
                },
                y: {
                    tick:{
                        outer:false
                    }
                    //Removed y axis label as it is not in current implementation
                    // ,
                    // label: {
                    //     text: "単位情報なし",
                    //     position: 'outer-top',
                        
                    // }
                    
                }
            },
           
            legend: {
                position:'bottom',
                show: false
               
            },
            onrendered: function () { 
                try {
                    d3.selectAll(".c3-axis-x .tick text")
                    .style("font-weight", "bold")
                    .style("font-size", "10px");
                    
                d3.selectAll(".c3-axis-y .tick text")
                    .style("font-weight", "bold")
                    .style("font-size", "10px");

                d3.selectAll(".c3-legend-item .tick text")
                    .style("font-weight", "normal")
                    .style("font-size", "10px");

                d3.selectAll(".c3 .c3-grid line")
                    .style("stroke", "#C0C0C0")
                
                d3.selectAll("#"+props.data.id  +" .c3-legend-item.c3-legend-item-カラー").nodes().forEach(function (el, i) {
                        d3.select(el).select("text").style("font-size", "10px");
                    });
                
                d3.selectAll("#"+props.data.id  +" .c3-legend-item.c3-legend-item-モノクロ").nodes().forEach(function (el, i) {
                        d3.select(el).select("text").style("font-size", "10px");
                    });
                    
                } catch (error) {
                    
                }      
              
            }
            
        });
        }
    }
        

    return (
        <div style={{marginLeft:"10%"}}>
            <div id={props.data.id}>
                <div style={{ height: '13rem', fontSize: '30px', display: displayText ? "" : "None" }}>
                    <div style={{ margin: 'auto', paddingTop: '80px', textAlign: 'center' }}>
                        {'この期間には印刷が行われておりません。'}
                    </div>
                </div>
            </div>
        </div>
    );

}

export default MFPOtherGraphComponent;