import React, { useEffect, useState } from 'react';
import * as ReactBootStrap from 'react-bootstrap';
import './../../StylesCSS/table.css';
import TableComponent from '../reusableComponents/TableComponent';

const TableIconComponent = (props) => {

    const [columns, setcolumns] = useState([]);

    useEffect(() => {
        setcolumns(Object.keys(props.pc_data.tableData));
    }, [props.pc_data]);

    return (
        <div className="container">
            {columns != "undefined" && columns.length != 0 &&
            <ReactBootStrap.Table bordered id="pc-table-id" className="nas_graph_table " >
                <thead>
                        <tr >
                            <th colSpan='4' className="Table-header" style={{fontSize:"1.25rem",fontWeight:"normal", borderBottom:'1px solid rgba(0, 0, 0)'}}>
                                {props.pc_data.header_data}
                            </th>
                        </tr>
                </thead>
                <tbody>
                    <tr >
                        <td style={{textAlign:"center",verticalAlign:"middle", width:"20%"}} rowSpan='4' className="pt-1 pb-1"> 
                            {props.pc_data.icon}
                        </td> 
                       <td style={{width:"40%",textAlign:'left', color: props.pc_data.tableData[columns[0]].color,fontWeight: props.pc_data.tableData[columns[0]].fontWeight}}> {props.pc_data.tableData[columns[0]].title}</td>
                       <td style={{color:props.pc_data.tableData[columns[0]].color,fontWeight: props.pc_data.tableData[columns[0]].fontWeight}}> {props.pc_data.tableData[columns[0]].value}</td>
                    </tr>
                    <tr>
                       <td style={{textAlign:'left' ,color: props.pc_data.tableData[columns[1]].color,fontWeight: props.pc_data.tableData[columns[1]].fontWeight}}>{props.pc_data.tableData[columns[1]].title}</td>
                       <td style={{color:props.pc_data.tableData[columns[1]].color,fontWeight: props.pc_data.tableData[columns[1]].fontWeight}}> {props.pc_data.tableData[columns[1]].value}</td>
                    </tr> 
                    <tr>
                       <td style={{textAlign:'left', color: props.pc_data.tableData[columns[2]].color,fontWeight: props.pc_data.tableData[columns[2]].fontWeight}}>{props.pc_data.tableData[columns[2]].title} </td>
                       <td style={{color:props.pc_data.tableData[columns[2]].color,fontWeight: props.pc_data.tableData[columns[2]].fontWeight}}>{props.pc_data.tableData[columns[2]].value}</td>
                    </tr> 
                    <tr>
                       <td style={{textAlign:'left',  color: props.pc_data.tableData[columns[3]].color,fontWeight: props.pc_data.tableData[columns[3]].fontWeight}}>{props.pc_data.tableData[columns[3]].title} </td>
                       <td style={{color:props.pc_data.tableData[columns[3]].color,fontWeight: props.pc_data.tableData[columns[3]].fontWeight}}>{props.pc_data.tableData[columns[3]].value}</td>
                    </tr> 
                                    
                </tbody>
            </ReactBootStrap.Table>}
        </div>
    );
}
export default TableIconComponent;